.fullscreenContainer {
  @mixin fullscreen;

  z-index: $fullscreenZIndex;
}

.header {
  position: relative;

  .exitBtn {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
  }

  .title {
    position: relative;
    flex: 1 0 auto;
    text-align: center;
    font-weight: 200;
    font-size: 1.3rem;
  }
}

.logo {
  flex: 1 0 auto;
}

.opacityContainer {
  width: 100%;
  height: 100%;
  transition: opacity $modalSpeed ease-in-out;
}

.content {
  position: relative;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: transform $modalSpeed ease-in-out;
  transform: translateY(0);
}

.component {
  @mixin fullscreen;
  @mixin overflowAuto;

  top: $headerHeight;
  display: flex;
  box-sizing: border-box;
  margin: 10px 0;
  padding: 0 10px;
  user-select: text;

  &.left {
    justify-content: flex-start;
  }

  &.center {
    justify-content: center;
  }

  &.right {
    justify-content: flex-end;
  }
}

/*
 * Transition
 */

.enter {
  opacity: 0;

  .content {
    transform: translateY(30px);
  }
}

.enterActive {
  opacity: 1;

  .content {
    transform: translateY(0);
    pointer-events: none;
  }
}

.leave {
  opacity: 1;

  .content {
    transform: translateY(0);
  }
}

.leaveActive {
  opacity: 0;
  pointer-events: none;

  .content {
    transform: translateY(30px);
  }
}

/*
 * Theme
 */

:global(.light) {
  .opacityContainer {
    background: $light_fullscreenLayer_background;
    color: $light_globalApp_color;
  }

  .exitBtnTheme {
    @mixin iconBtnTheme $light_fullscreenLayer_exitButton_color, lightness;
  }
}

:global(.dark) {
  .opacityContainer {
    background: $dark_fullscreenLayer_background;
    color: $dark_globalApp_color;
  }

  .exitBtnTheme {
    @mixin iconBtnTheme $dark_fullscreenLayer_exitButton_color, lightness;
  }
}
