.opacityContainer{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  overflow:hidden;
  padding:10px;
  -webkit-transition:opacity 0.2s ease-in-out;
  -o-transition:opacity 0.2s ease-in-out;
  transition:opacity 0.2s ease-in-out;
}

.quickrefDetails{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  overflow:hidden;
  height:100%;
  -webkit-transition:-webkit-transform 0.2s ease-in-out;
  transition:-webkit-transform 0.2s ease-in-out;
  -o-transition:transform 0.2s ease-in-out;
  transition:transform 0.2s ease-in-out;
  transition:transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  -webkit-transform:translateY(0);
          transform:translateY(0);
}

.label{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}

.details{
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  -ms-flex:1 1 auto;

      flex:1 1 auto;
}

.tokenExplanation,
.regexExample{
  line-height:1.3;
}

.tokenExplanation{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
}

.explanationText{
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  margin-left:5px;
}

.token{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}

.regex,
.regexMatch,
.token{
  white-space:pre-wrap;
  word-wrap:break-word;
  word-break:break-all;
  overflow-wrap:break-word;

  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
}

.regexExample{
  margin-top:10px;
}

.resetFont{
  font-size:inherit;
}

.regex{
  margin-bottom:10px;
}

.closeDetails{
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  padding:5px;
  opacity:0.4;

  display:inline-block;
  margin:0 -5px;
  padding:0 5px;
}

.closeDetails:hover{
  opacity:0.6;
}

.closeDetails:active{
  opacity:0.8;
}

.enter{
  opacity:0;
}

.enter .quickrefDetails{
    -webkit-transform:translateY(30px);
            transform:translateY(30px);
  }

.enterActive{
  opacity:1;
}

.enterActive .quickrefDetails{
    -webkit-transform:translateY(0);
            transform:translateY(0);
    pointer-events:none;
  }

.leave{
  opacity:1;
}

.leave .quickrefDetails{
    -webkit-transform:translateY(0);
            transform:translateY(0);
  }

.leaveActive{
  opacity:0;
  pointer-events:none;
}

.leaveActive .quickrefDetails{
    -webkit-transform:translateY(30px);
            transform:translateY(30px);
  }

:global(.light) .opacityContainer{
    background:#fdfdfd;
  }

:global(.light) .regex{
    color:#417751;
  }

:global(.light) .closeDetails{
    color:#000000;
  }

:global(.dark) .opacityContainer{
    background:#1f1f1f;
    -webkit-box-shadow:0 -20px 10px #1f1f1f;
            box-shadow:0 -20px 10px #1f1f1f;
  }

:global(.dark) .regex{
    color:#417751;
  }

:global(.dark) .closeDetails{
    color:#ffffff;
  }
