.item {
  display: inline-block;
  white-space: nowrap;
  font-size: 0.85rem;
  line-height: 1;
  cursor: pointer;
}

.icon {
  padding: 4px 5px;
}

.menuIcon {
  margin-left: -2px;
  padding-right: 5px;
  vertical-align: top;
  font-size: 0.8rem;
}

/* Below this is tiny screens */
@media (max-width: $markdownEditorBreakpoint) {
  .item {
    font-size: 0.65rem;
  }
}

/*
 * Theme
 */

:global(.light) {
  .item {
    color: $light_toolbarItem_color;

    &:hover {
      color: $light_toolbarItem_hoverColor;
    }
    &:active {
      color: $light_toolbarItem_activeColor;
    }
  }
}

:global(.dark) {
  .item {
    color: $dark_toolbarItem_color;

    &:hover {
      color: $dark_toolbarItem_hoverColor;
    }
    &:active {
      color: $dark_toolbarItem_activeColor;
    }
  }
}
