.iconButton{
  position:relative;
  border-radius:50%;
  cursor:pointer;
  -webkit-transition:all 0.15s ease;
  -o-transition:all 0.15s ease;
  transition:all 0.15s ease;
  overflow:hidden;
}

.childElement{
  position:absolute;
  top:50%;
  left:50%;
  -webkit-transform:translate(-50%, -50%);
          transform:translate(-50%, -50%);
}
