.root{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  z-index:16;
}

.finishedLoading{
  pointer-events:none;
}

.container{
  width:100%;
  height:100%;
  opacity:1;
  -webkit-transition:opacity 1s ease-out;
  -o-transition:opacity 1s ease-out;
  transition:opacity 1s ease-out;
}

noscript,
.content{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  top:50px;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack:center;
      justify-content:center;
  overflow:auto;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  margin:10px 0;
  padding:0 10px;
  font-weight:100;
  font-size:1rem;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

noscript{
  z-index:1;
  background:#f7f7f7;
}

.text{
  margin-bottom:20px;
}

.leave{
  opacity:1;

}

.leaveActive{
  opacity:0;
  pointer-events:none;
}

:global(.light) .container{
    background:#f7f7f7;
    color:#333333;
  }

:global(.dark) .container{
    background:#1a1a1a;
    color:#ababab;
  }
