.root {
  display: flex;
  flex-direction: column;
  float: right;
  box-sizing: border-box;
  padding: 10px;
  max-width: 50vw;
  width: 50%;
  height: 100%;
  border-left: 1px solid;
  box-shadow: -3px 0 10px -5px rgba(0, 0, 0, 0.15);
  position: relative;
}

.resizer {
  width: 8px;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  cursor: ew-resize;
  pointer-events: auto;
  z-index: 10;
}

@media (max-width: $extraLargeRightSidebarBreakpoint) {
  .root {
    width: 40%;
  }
}

/*
 * Theme
 */

:global(.light) {
  .root {
    border-color: $light_sidebar_borderColor;
    background: $light_sidebar_background;
  }
}

:global(.dark) {
  .root {
    border-color: $dark_sidebar_borderColor;
    background: $dark_sidebar_background;
  }
}
