.root {
  display: flex;
  flex-direction: row;
  margin-bottom: 2px;
}

.icons {
  flex: 1 0 auto;
  margin: -5px 0 0 -5px;
}

.date {
  flex: 1 1 auto;
  text-align: right;
}

.tooltip {
  display: inline;
}

.icon {
  padding: 5px;
  cursor: pointer;
}

/*
 * Theme
 */

:global(.light) {
  .date {
    color: $light_accountPageToolbar_date_color;
  }
  .icon {
    color: $light_accountPageToolbar_icon_color;
  }
  .delete,
  .isFavorite {
    &:hover,
    &.fill {
      color: $light_accountPageToolbar_delete_hoverColor;
    }
    &:active {
      color: $light_accountPageToolbar_delete_activeColor;
    }
  }
  .isPrivate {
    &:hover,
    &.fill {
      color: $light_accountPageToolbar_isPrivate_hoverColor;
    }
    &:active {
      color: $light_accountPageToolbar_isPrivate_activeColor;
    }
  }
  .isLocked {
    &:hover,
    &.fill {
      color: $light_accountPageToolbar_isLocked_hoverColor;
    }
    &:active {
      color: $light_accountPageToolbar_isLocked_activeColor;
    }
  }
  .title {
    &:hover {
      color: $light_accountPageToolbar_title_hoverColor;
    }
    &:active {
      color: $light_accountPageToolbar_title_activeColor;
    }
  }
}

:global(.dark) {
  .date {
    color: $dark_accountPageToolbar_date_color;
  }
  .icon {
    color: $dark_accountPageToolbar_icon_color;
  }
  .delete,
  .isFavorite {
    &:hover,
    &.fill {
      color: $dark_accountPageToolbar_delete_hoverColor;
    }
    &:active {
      color: $dark_accountPageToolbar_delete_activeColor;
    }
  }
  .isPrivate {
    &:hover,
    &.fill {
      color: $dark_accountPageToolbar_isPrivate_hoverColor;
    }
    &:active {
      color: $dark_accountPageToolbar_isPrivate_activeColor;
    }
  }
  .isLocked {
    &:hover,
    &.fill {
      color: $dark_accountPageToolbar_isLocked_hoverColor;
    }
    &:active {
      color: $dark_accountPageToolbar_isLocked_activeColor;
    }
  }
  .title {
    &:hover {
      color: $dark_accountPageToolbar_title_hoverColor;
    }
    &:active {
      color: $dark_accountPageToolbar_title_activeColor;
    }
  }
}
