:global(.light) .group0:nth-child(odd){
    background:#c6e3ff;
  }
  :global(.light) .group0:nth-child(even){
    background:#74c4ff;
  }
  :global(.light) .group1{
    background:#c6e99d;
  }
  :global(.light) .group2{
    background:#f5aba5;
  }
  :global(.light) .group3{
    background:#ffbf6d;
  }

:global(.dark) .group0:nth-child(odd){
    background:#084b8c;
  }

:global(.dark) .group0:nth-child(even){
    background:#05335D;
  }

:global(.dark) .group1{
    background:#2c5200;
  }

:global(.dark) .group2{
    background:#8e3e37;
  }

:global(.dark) .group3{
    background:#945401;
  }
