.root,
.container{
  width:100%;
  height:100%;
}

.container{
  -webkit-transition:-webkit-filter 0.2s ease-in-out;
  transition:-webkit-filter 0.2s ease-in-out;
  -o-transition:filter 0.2s ease-in-out;
  transition:filter 0.2s ease-in-out;
  transition:filter 0.2s ease-in-out, -webkit-filter 0.2s ease-in-out;

  display:-ms-flexbox;

  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
}

.container.isFetchingData{
  -webkit-filter:blur(2px);
          filter:blur(2px);
  pointer-events:none;
}

.fetchingDataLoader{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  display:-ms-flexbox;

  display:flex;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack:center;
      justify-content:center;
}

.historyContainer{
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  -ms-flex:1 1 auto;

      flex:1 1 auto;
}

.historyLabel,
.paginator{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}
