.root {
  position: absolute;
  right: 0;
  top: 0;
  padding: 3px 5px;
  border-radius: 3px;
  text-transform: lowercase;
  font-weight: normal;
  font-size: 0.7rem;
}

/*
 * Theme
 */

:global(.light) {
  .root {
    box-shadow: -5px 0 5px $light_globalApp_background;

    &.noMatch {
      background: $light_matchIndicator_noMatch_background;
      color: $light_matchIndicator_noMatch_color;
    }

    &.match {
      background: $light_matchIndicator_match_background;
      color: $light_matchIndicator_match_color;
    }

    &.error {
      background: $light_matchIndicator_error_background;
      color: $light_matchIndicator_error_color;
    }
  }
}

:global(.dark) {
  .root {
    box-shadow: -10px 3px 5px $dark_globalApp_background;

    &.noMatch {
      background: $dark_matchIndicator_noMatch_background;
      color: $dark_matchIndicator_noMatch_color;
    }

    &.match {
      background: $dark_matchIndicator_match_background;
      color: $dark_matchIndicator_match_color;
    }

    &.error {
      background: $dark_matchIndicator_error_background;
      color: $dark_matchIndicator_error_color;
    }
  }
}
