.root {
  @mixin wordwrap;

  display: flex;
  max-width: calc(100% - 20px); /* 20px is the width of the little arrow in the treeview */
  letter-spacing: 1px;
  font-family: $codeFont;
  line-height: 1.35;
  overflow-wrap: break-word;
}

.regex {
  flex: 0 1 auto;
  min-width: 0;
}

.delimiter {
  flex: 0 0 auto;

  &.first {
    padding-right: 5px;
  }
  &.last {
    padding: 0 5px;
  }
}

.flags {
  flex: 0 0 auto;
  /* Add some padding between it and the scrollbar. Not ideal, but has to be done. */
  padding-right: 5px;
}

/*
 * Theme
 */

:global(.light) {
  .flags {
    color: $light_rootLabel_flags_color;
  }
  .delimiter {
    color: $light_rootLabel_delimiter_color;
  }
}

:global(.dark) {
  .flags {
    color: $dark_rootLabel_flags_color;
  }
  .delimiter {
    color: $dark_rootLabel_delimiter_color;
  }
}
