.input{
  padding:8px;
}

  .input :global .CodeMirror pre{
      padding:0 2px;
    }

  .input :global .CodeMirror-lines{
      padding:2px 0;
    }

.input{
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  padding:5px;
  width:100%;
  outline:0;
  border:1px solid;
  border-radius:3px;
  -webkit-box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.1);
          box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.1);
  font-family:inherit;
  -webkit-transition:all 0.2s ease;
  -o-transition:all 0.2s ease;
  transition:all 0.2s ease;
  -webkit-transition-property:border-color, -webkit-box-shadow;
  transition-property:border-color, -webkit-box-shadow;
  -o-transition-property:border-color, box-shadow;
  transition-property:border-color, box-shadow;
  transition-property:border-color, box-shadow, -webkit-box-shadow;

  outline:0;
  font-size:0.875rem;
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
  resize:none;
}

:global(.light) .input{
    border-color:#bdbdbd;
    background:#ffffff;
  }

:global(.light) .input.editorFocused{
      border-color:#457abb;
      -webkit-box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.075), 0 0 5px rgba(69, 122, 187, 0.5);
              box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.075), 0 0 5px rgba(69, 122, 187, 0.5);
    }

:global(.dark) .input{
    border-color:#292828;
    background:#272727;
  }

:global(.dark) .input.editorFocused{
      border-color:#333333;
      -webkit-box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.075), 0 0 5px rgba(0, 0, 0, 0.6);
              box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.075), 0 0 5px rgba(0, 0, 0, 0.6);
    }
