.root{
  width:30px;
  text-align:center;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

.votebutton{
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  padding:5px;
  opacity:0.4;
}

.votebutton:hover{
  opacity:0.6;
}

.votebutton:active{
  opacity:0.8;
}

.disabled{
  pointer-events:none;
}

.score{
  font-weight:bold;
}

.hasVoted{
  opacity:1;
}

:global(.light) .upvote:active,
  :global(.light) .upvote:hover,
  :global(.light) .upvote.hasVoted{
    color:#007300;
  }

:global(.light) .downvote:active,
  :global(.light) .downvote:hover,
  :global(.light) .downvote.hasVoted{
    color:#6d0000;
  }

:global(.dark) .upvote:active,
  :global(.dark) .upvote:hover,
  :global(.dark) .upvote.hasVoted{
    color:#3b9458;
  }

:global(.dark) .downvote:active,
  :global(.dark) .downvote:hover,
  :global(.dark) .downvote.hasVoted{
    color:#b74141;
  }
