.matchData{
  white-space:pre-wrap;
  word-wrap:break-word;
  word-break:break-all;
  overflow-wrap:break-word;

  padding:6px 10px;
  border-radius:3px;
  width:100%;
}

.textTruncated{
  cursor:pointer;
}

.didNotParticipate{
  font-style:italic;
  opacity:0.5;
}

.groupNum{
  white-space:nowrap;
  overflow:hidden;
  -o-text-overflow:ellipsis;
     text-overflow:ellipsis;

  padding:5px 0;
}

.groupNum.clickable{
    cursor:pointer;
  }

.groupNum span{
    padding:2px 3px;
  }

.matchPos{
  white-space:nowrap;
  overflow:hidden;
  -o-text-overflow:ellipsis;
     text-overflow:ellipsis;

  padding:5px 10px;
}

.groupNum{
  white-space:nowrap;
}

:global(.light) .matchData{
    background:rgba(0, 0, 0, 0.03);
  }

:global(.light) .matchPos{
    color:#8e8e8e;
  }

:global(.light) .groupNum.clickable:hover span{
    background:#e4e440 !important;
  }

:global(.dark) .matchData{
    background:rgba(0, 0, 0, 0.05);
  }

:global(.dark) .matchPos{
    color:#565656;
  }

:global(.dark) .groupNum.clickable:hover span{
    background:#ffffbb !important;
  }
