.root{
  white-space:pre-wrap;
  word-wrap:break-word;
  word-break:break-all;
  overflow-wrap:break-word;

  display:-ms-flexbox;

  display:flex;
  max-width:calc(100% - 20px);
  letter-spacing:1px;
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
  line-height:1.35;
  overflow-wrap:break-word;
}

.regex{
  -ms-flex:0 1 auto;
      flex:0 1 auto;
  min-width:0;
}

.delimiter{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}

.delimiter.first{
    padding-right:5px;
  }

.delimiter.last{
    padding:0 5px;
  }

.flags{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
  padding-right:5px;
}

:global(.light) .flags{
    color:#5d7f68;
  }

:global(.light) .delimiter{
    color:rgb(128, 128, 128);
  }

:global(.dark) .flags{
    color:#3b9458;
  }

:global(.dark) .delimiter{
    color:rgb(128, 128, 128);
  }
