.root{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:50px;

  display:-ms-flexbox;

  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  padding:10px;
  height:100%;
  font-size:0.875rem;
}

.innerContents{
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  overflow:hidden;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  height:100%;
  border:1px solid;
  border-radius:3px;
  -webkit-user-select:initial;
     -moz-user-select:initial;
      -ms-user-select:initial;
          user-select:initial;
  position:relative;
}

.innerContents > div{
    position:absolute;
    top:10px;
    right:10px;
    bottom:10px;
    left:10px;
  }

.label{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}

:global(.light) .innerContents{
    border-color:#ffffff #f3f3f3 #ededed #f3f3f3;
    background:#fdfdfd;
  }

:global(.dark) .innerContents{
    border-color:#1f1f1f #212121 #212121 #212121;
    background:#1f1f1f;
  }
