.root {
  display: flex;
  flex-direction: row;
  align-items: center;
  box-sizing: border-box;
  padding: 0 10px;
  height: $headerHeight;
  border-bottom: 1px solid;
  box-shadow: 0 -3px 10px 3px rgba(0, 0, 0, 0.15);
  user-select: none;

  &.leftSidebarHamburger {
    /* 10px padding on each side */
    padding-left: calc($sidebarHamburgerWidth + 20px);
  }

  &.rightSidebarHamburger {
    /* 10px padding on each side and 10px for extra spacing */
    padding-right: calc($sidebarHamburgerWidth + 20px);
  }
}

.links {
  display: flex;
  flex: 2 0 0;
  flex-direction: row;
  justify-content: flex-end;
}

/* on small devices, hide the links entirely instead */
@media (max-width: $headerIconsMinimumSize) {
  .links * {
    display: none;
  }
}

/* Hide the logo between the two sizes */
@media (max-width: $headerLogoBreakpoint) and (min-width: $headerIconsMinimumSize) {
  .logo {
    display: none;
  }
}

/*
 * Theme
 */

:global(.light) {
  .root {
    border-color: $light_header_border;
    background: $light_header_background;
    color: $light_header_color;
  }

  .iconButton {
    @mixin iconBtnTheme $light_header_background, lightness;
  }

  .twitterLink {
    color: $light_headerLink_twitter_color;
  }

  .donateLink {
    color: $light_headerLink_donate_color;
  }

  .contactLink {
    color: $light_headerLink_contact_color;
  }

  .feedbackLink {
    color: $light_headerLink_feedback_color;
  }

  .wiki {
    color: $light_headerLink_wiki_color;
  }
}

:global(.dark) {
  .root {
    border-color: $dark_header_border;
    background: $dark_header_background;
    color: $dark_header_color;
  }

  .iconButton {
    @mixin iconBtnTheme $dark_header_background, lightness;
  }

  .twitterLink {
    color: $dark_headerLink_twitter_color;
  }

  .donateLink {
    color: $dark_headerLink_donate_color;
  }

  .contactLink {
    color: $dark_headerLink_contact_color;
  }

  .feedbackLink {
    color: $dark_headerLink_feedback_color;
  }

  .wiki {
    color: $dark_headerLink_wiki_color;
  }
}
