.root{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  display:-ms-flexbox;

  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  font-size:0.8rem;
}

.testingArea{
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  position:relative;
}

.switchAreaLabel{
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  padding:5px;
  opacity:0.4;
}

.switchAreaLabel:hover{
  opacity:0.6;
}

.switchAreaLabel:active{
  opacity:0.8;
}

.caret{
  margin-right:5px;
}

.label{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}

:global(.light) .switchAreaLabel{
    color:#000000;
  }

:global(.dark) .switchAreaLabel{
    color:#989898;
  }
