.menuItem {
  display: flex;
  flex-direction: row;
  padding: 8px 20px;
  cursor: pointer;
  user-select: none;
}

.disabled {
  pointer-events: none;
}

.icon {
  flex: 1 1 auto;
  text-align: right;

  &.hidden {
    visibility: hidden;
  }
}

/*
 * Theme
 */

:global(.light) {
  .menuItem {
    color: $light_menuItem_color;

    &:hover {
      background: $light_menuItem_hoverBackground;
      color: $light_menuItem_hoverColor;
    }
    &:active {
      background: $light_menuItem_activeBackground;
    }
  }
}

:global(.dark) {
  .menuItem {
    color: $dark_menuItem_color;

    &:hover {
      background: $dark_menuItem_hoverBackground;
      color: $dark_menuItem_hoverColor;
    }
    &:active {
      background: $dark_menuItem_activeBackground;
    }
  }
}
