.okButton,
.cancelButton,
.dangerButton,
.executeButton{
  -webkit-box-sizing:content-box;
          box-sizing:content-box;
  padding:6px 20px;
  outline:0;
  border:1px solid;
  border-radius:3px;
  -webkit-box-shadow:inset 0 1px 0 0 rgba(255, 255, 255, 0.19);
          box-shadow:inset 0 1px 0 0 rgba(255, 255, 255, 0.19);
  text-align:center;
  text-transform:uppercase;
  text-shadow:0 -1px 1px rgba(0, 0, 0, 0.4), 0 0 15px rgba(0, 0, 0, 0.2);
  white-space:nowrap;
  font-weight:bold;
  font-size:0.9rem;
  cursor:pointer;
  -webkit-transition:background 0.2s ease;
  -o-transition:background 0.2s ease;
  transition:background 0.2s ease;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

  .okButton:active,.cancelButton:active,.dangerButton:active,.executeButton:active{
    -webkit-box-shadow:inset 0 2px 3px 0 rgba(0, 0, 0, 0.12), inset 0 -1px 2px 0 rgba(0, 0, 0, 0.06), 0 1px 2px 0 rgba(0, 0, 0, 0.11);
            box-shadow:inset 0 2px 3px 0 rgba(0, 0, 0, 0.12), inset 0 -1px 2px 0 rgba(0, 0, 0, 0.06), 0 1px 2px 0 rgba(0, 0, 0, 0.11);
  }

.disabled{
  opacity:0.3;
  pointer-events:none;
}

:global(.light) .okButton,
  :global(.light) .cancelButton,
  :global(.light) .dangerButton,
  :global(.light) .executeButton{
    color:#f7f7f7;
  }

:global(.light) .okButton{
    border-color:rgb(69, 110, 161) rgb(69, 110, 161) rgb(69, 99, 135) rgb(69, 110, 161);
    background:#457abb;
  }

:global(.light) .okButton:focus,:global(.light) .okButton:hover{
  border-color:#457abb #457abb rgb(69, 116, 173) #457abb;
  background:rgb(87, 135, 193);
}

:global(.light) .okButton:active{
  background:rgb(69, 116, 173);
}

:global(.light) .cancelButton{
    border-color:rgb(93, 93, 93) rgb(93, 93, 93) rgb(85, 85, 85) rgb(93, 93, 93);
    background:#666666;
  }

:global(.light) .cancelButton:focus,:global(.light) .cancelButton:hover{
  border-color:#666666 #666666 rgb(97, 97, 97) #666666;
  background:rgb(115, 115, 115);
}

:global(.light) .cancelButton:active{
  background:rgb(97, 97, 97);
}

:global(.light) .dangerButton{
    border-color:rgb(158, 61, 61) rgb(158, 61, 61) rgb(133, 61, 61) rgb(158, 61, 61);
    background:#B83E3E;
  }

:global(.light) .dangerButton:focus,:global(.light) .dangerButton:hover{
  border-color:#B83E3E #B83E3E rgb(171, 61, 61) #B83E3E;
  background:rgb(195, 75, 75);
}

:global(.light) .dangerButton:active{
  background:rgb(171, 61, 61);
}

:global(.light) .executeButton{
    border-color:rgb(56, 125, 72) rgb(56, 125, 72) rgb(56, 99, 66) rgb(56, 125, 72);
    background:#38964E;
  }

:global(.light) .executeButton:focus,:global(.light) .executeButton:hover{
  border-color:#38964E #38964E rgb(56, 138, 75) #38964E;
  background:rgb(62, 168, 87);
}

:global(.light) .executeButton:active{
  background:rgb(56, 138, 75);
}

:global(.dark) .okButton,
  :global(.dark) .cancelButton,
  :global(.dark) .dangerButton,
  :global(.dark) .executeButton{
    color:#ababab;
  }

:global(.dark) .okButton{
    border-color:rgb(46, 69, 94) rgb(46, 69, 94) rgb(46, 57, 69) rgb(46, 69, 94);
    background:#2d5079;
  }

:global(.dark) .okButton:focus,:global(.dark) .okButton:hover{
  border-color:#2d5079 #2d5079 rgb(46, 74, 107) #2d5079;
  background:rgb(52, 94, 141);
}

:global(.dark) .okButton:active{
  background:rgb(46, 74, 107);
}

:global(.dark) .cancelButton{
    border-color:rgb(56, 56, 56) rgb(56, 56, 56) rgb(51, 51, 51) rgb(56, 56, 56);
    background:#3e3e3e;
  }

:global(.dark) .cancelButton:focus,:global(.dark) .cancelButton:hover{
  border-color:#3e3e3e #3e3e3e rgb(58, 58, 58) #3e3e3e;
  background:rgb(74, 74, 74);
}

:global(.dark) .cancelButton:active{
  background:rgb(58, 58, 58);
}

:global(.dark) .dangerButton{
    border-color:rgb(135, 28, 28) rgb(135, 28, 28) rgb(110, 28, 28) rgb(135, 28, 28);
    background:#a01b1b;
  }

:global(.dark) .dangerButton:focus,:global(.dark) .dangerButton:hover{
  border-color:#a01b1b #a01b1b rgb(148, 28, 28) #a01b1b;
  background:rgb(183, 31, 31);
}

:global(.dark) .dangerButton:active{
  background:rgb(148, 28, 28);
}

:global(.dark) .executeButton{
    border-color:rgb(56, 125, 72) rgb(56, 125, 72) rgb(56, 99, 66) rgb(56, 125, 72);
    background:#38964E;
  }

:global(.dark) .executeButton:focus,:global(.dark) .executeButton:hover{
  border-color:#38964E #38964E rgb(56, 138, 75) #38964E;
  background:rgb(62, 168, 87);
}

:global(.dark) .executeButton:active{
  background:rgb(56, 138, 75);
}
