.modalHeader{
  -ms-flex:0 0 65px;
      flex:0 0 65px;
  margin:0;
  padding:0 15px;
  height:65px;
  font-weight:bold;
  font-size:1rem;
  line-height:65px;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

  .modalHeader .titleIcon{
    margin-right:10px;
    font-size:1.1em;
  }

:global(.light) .modalHeader{
    background:#457abb;
    color:#ffffff;
  }

:global(.light) .titleIcon{
    color:#abc3e0;
  }

:global(.dark) .modalHeader{
    background:#2d5079;
    color:#ababab;
  }

:global(.dark) .titleIcon{
    color:#6f92bb;
  }
