.rightElement{
  -ms-flex:1 0 auto;
      flex:1 0 auto;
  padding-left:5px;
  text-align:right;
  text-transform:none;
  font-weight:bold;
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
}

.wrapText{
  white-space:nowrap;
  overflow:hidden;
  -o-text-overflow:ellipsis;
     text-overflow:ellipsis;
}

:global(.light) .multiLabel:not(:active):not(:hover) .rightElement{
      color:#417751;
    }

:global(.dark) .multiLabel:not(:active):not(:hover) .rightElement{
      color:#3b9458;
    }
