.exportMatch{
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  padding:5px;
  opacity:0.4;
}
  .exportMatch:hover{
  opacity:0.6;
}
  .exportMatch:active{
  opacity:0.8;
}

:global(.light) .root{
    background:#fdfdfd;
  }

:global(.light) .exportMatch{
    color:#000000;
  }

:global(.dark) .root{
    background:#1f1f1f;
  }

:global(.dark) .exportMatch{
    color:#ffffff;
  }
