.root{
  position:absolute;
  right:0;
  top:0;
  padding:3px 5px;
  border-radius:3px;
  text-transform:lowercase;
  font-weight:normal;
  font-size:0.7rem;
}

:global(.light) .root{
    -webkit-box-shadow:-5px 0 5px #ebebeb;
            box-shadow:-5px 0 5px #ebebeb;
  }

:global(.light) .root.noMatch{
      background:#6d6d6d;
      color:#fdfdfd;
    }

:global(.light) .root.match{
      background:#5a9282;
      color:#ffffff;
    }

:global(.light) .root.error{
      background:#d03f3f;
      color:#ffffff;
    }

:global(.dark) .root{
    -webkit-box-shadow:-10px 3px 5px #1f1f1f;
            box-shadow:-10px 3px 5px #1f1f1f;
  }

:global(.dark) .root.noMatch{
      background:#3e3e3e;
      color:#d4d4d4;
    }

:global(.dark) .root.match{
      background:#2f584c;
      color:#d4d4d4;
    }

:global(.dark) .root.error{
      background:#8a2d2d;
      color:#d4d4d4;
    }
