$size: 14px;

$transition-time: 0.3s;
$background: #f9f9f9;
$foreground: #2c3e50;

$foreground-light: #34495e;

.stepContainer,
.debugContainer {
  font-family: $codeFont;
  line-height: 1.3em;
  white-space: pre-wrap;
}

.stepContainer {
  overflow: hidden;
}

.shortcuts {
  cursor: pointer;
  margin: 10px 0 0;
  float: right;

  &:hover {
    text-decoration: underline;
  }
}

.sliderArea {
  display: flex;
  align-items: center;

  .leftInput,
  .rightInput {
    flex: 0 0 60px;
    text-align: center;
    position: relative;
    z-index: 5;
  }

  .leftInput {
    margin-right: 10px;
  }

  .rightInput {
    margin-left: 10px;
  }

  .slider {
    flex: 1 1 auto;
  }
}

.controls {
  display: flex;
  justify-content: center;

  .goBeginning,
  .goEnd,
  .increase,
  .decrease,
  .playPause {
    font-size: 15px;
    flex: 0 0 20px;
    cursor: pointer;
    user-select: none;
    margin: 0 5px;
    width: 20px;
    text-align: center;

    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.7;
    }
  }

  .playPause {
    flex: 0 0 auto;
  }

  .disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}

.input {
  text-align: center;
}

.loadMoreButton {
  margin-top: 10px;
}

.container {
  display: flex;
  flex-direction: column;
}

.stepSlider {
  flex: 0 0 auto;
  position: relative;
  margin-bottom: 10px;
}

.matchStep {
  flex: 1 1 auto;
}

/*
 * Theme
 */

:global(.light) {
  .playPause {
    color: $light_theme_color;
  }

  .goBeginning,
  .goEnd,
  .increase,
  .decrease {
    color: #777;
  }
}

:global(.dark) {
  .playPause {
    color: $dark_theme_color;
  }

  .goBeginning,
  .goEnd,
  .increase,
  .decrease {
    color: #777;
  }

  .leftInput,
  .rightInput {
    background: #1f1f1f;
  }
}
