.modal{
  max-width:450px;
  height:auto;
  max-height:100%;
}

.shortcut{
  border-radius:3px;
  padding:3px 6px;
  text-align:center;
  display:inline-block;
  font-size:1rem;
}

.table{
  width:100%;
  margin:0 auto;
  font-size:0.9rem;
  max-width:300px;
}

.table th{
    font-weight:bold;
    line-height:30px;
    border-bottom:1px solid transparent;
  }

.table .descriptionTitle{
    text-align:left;
  }

.table .shortcutTitle{
    text-align:center;
    width:100px;
  }

.table tbody::before{
    content:'-';
    display:block;
    line-height:1em;
    color:transparent;
  }

.shortcutRow{
  height:35px;
}

.shortcutContainer{
  text-align:center;
}

:global(.light) .table th{
    border-bottom-color:#e4e4e4;
  }

:global(.light) .shortcut{
    background:rgba(6, 6, 6, 0.1);
    color:#5d5d5d;
  }

:global(.dark) .table th{
    border-bottom-color:#313131;
  }

:global(.dark) .shortcut{
    background:rgba(255, 255, 255, 0.06);
    color:#b5b5b5;
  }
