.root{
  -ms-flex:1 0 auto;
      flex:1 0 auto;
  outline:0;
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  padding:6px 8px 6px 0;
}

  .root .options{
    margin:0 3px;
  }

  .root .flagBtn,
  .root .options{
    display:inline-block;
    outline:0;
    resize:none;
  }

.centered{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align:baseline;
      align-items:baseline;
  padding:4px;
}

.delimiter{
  font-weight:bold;
}

:global(.light) .delimiter{
    color:#c5c5c5;
  }

:global(.light) .root{
    color:#5d7f68;
  }

:global(.light) .root .flagBtn{
      color:#c5c5c5;
    }

:global(.light) .root:hover .flagBtn{
      color:#719c7f;
    }

:global(.light) .root:active .flagBtn{
      color:#5d7f68;
    }

:global(.dark) .delimiter{
    color:#4a4a4a;
  }

:global(.dark) .root{
    color:#3b9458;
  }

:global(.dark) .root .flagBtn{
      color:#4a4a4a;
    }

:global(.dark) .root:hover .flagBtn{
      color:#719c7f;
    }

:global(.dark) .root:active .flagBtn{
      color:#3b9458;
    }
