.root {
  @mixin fullscreen 0, 0, 0, $innerLeftSidebarWidth;

  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 10px;
  height: 100%;
  font-size: 0.875rem;
}

.innerContents {
  flex: 1 1 auto;
  overflow: hidden;
  box-sizing: border-box;
  height: 100%;
  border: 1px solid;
  border-radius: 3px;
  user-select: initial;
  position: relative;

  > div {
    @mixin fullscreen 10px, 10px, 10px, 10px;
  }
}

.label {
  flex: 0 0 auto;
}


/*
 * Theme
 */

:global(.light) {
  .innerContents {
    border-color: $light_rightSidebar_contentArea_borderColor;
    background: $light_rightSidebar_contentArea_background;
  }
}

:global(.dark) {
  .innerContents {
    border-color: $dark_rightSidebar_contentArea_borderColor;
    background: $dark_rightSidebar_contentArea_background;
  }
}
