.flagItem{
  width:155px;
}

  .flagItem div{
    padding-top:3px;
    white-space:pre-wrap;
    font-size:0.7rem;
  }

.flagIcon{
  margin-left:150px;
}

:global(.light) .flagItem strong{
      color:rgb(69, 116, 173);
    }

:global(.light) .flagItem div{
      color:#888888;
    }

:global(.dark) .flagItem strong{
      color:#ffffff;
    }

:global(.dark) .flagItem div{
      color:#888888;
    }
