.menuPortal{
  position:absolute;
  z-index:1003;
}

.menu{
  opacity:1;
  -webkit-transition:opacity 0.25s ease-out, -webkit-transform 0.25s cubic-bezier(0.6, 0.4, 0.2, 1.4);
  transition:opacity 0.25s ease-out, -webkit-transform 0.25s cubic-bezier(0.6, 0.4, 0.2, 1.4);
  -o-transition:opacity 0.25s ease-out, transform 0.25s cubic-bezier(0.6, 0.4, 0.2, 1.4);
  transition:opacity 0.25s ease-out, transform 0.25s cubic-bezier(0.6, 0.4, 0.2, 1.4);
  transition:opacity 0.25s ease-out, transform 0.25s cubic-bezier(0.6, 0.4, 0.2, 1.4), -webkit-transform 0.25s cubic-bezier(0.6, 0.4, 0.2, 1.4);
  -webkit-transform:translateY(0) scale(1);
          transform:translateY(0) scale(1);
  -webkit-transform-origin:center top;
          transform-origin:center top;
  will-change:transform, opacity;
}

.enter{
  opacity:0;
  -webkit-transform:translateY(-10px) scale(0.95);
          transform:translateY(-10px) scale(0.95);
}

.enterActive{
  opacity:1;
  -webkit-transform:translateY(0) scale(1);
          transform:translateY(0) scale(1);
  pointer-events:none;
}

.leave{
  opacity:1;
  -webkit-transform:translateY(0) scale(1);
          transform:translateY(0) scale(1);
}

.leaveActive{
  opacity:0;
  -webkit-transform:translateY(10px) scale(0.95);
          transform:translateY(10px) scale(0.95);
  pointer-events:none;
}
