.rightElement {
  flex: 1 0 auto;
  padding-left: 5px;
  text-align: right;
  text-transform: none;
  font-weight: bold;
  font-family: $codeFont;
}

.wrapText {
  @mixin textOverflow;
}

/*
 * Theme
 */

:global(.light) {
  .multiLabel:not(:active):not(:hover) {
    .rightElement {
      color: $light_multiListItem_rightElement_color;
    }
  }
}

:global(.dark) {
  .multiLabel:not(:active):not(:hover) {
    .rightElement {
      color: $dark_multiListItem_rightElement_color;
    }
  }
}
