.root{
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  padding:5px;
  width:100%;
  outline:0;
  border:1px solid;
  border-radius:3px;
  -webkit-box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.1);
          box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.1);
  font-family:inherit;

  padding:10px;
  font-size:0.8rem;
}

:global(.light) .root{
    border-color:#bdbdbd;
    background:#ffffff;
  }

:global(.dark) .root{
    border-color:#292828;
    background:#272727;
  }
