.root,
.container {
  width: 100%;
  height: 100%;
}

.container {
  @mixin fetchDataContainer;

  display: flex;
  flex-direction: column;
}

.fetchingDataLoader {
  @mixin fullscreen;

  display: flex;
  align-items: center;
  justify-content: center;
}

.historyContainer {
  @mixin overflowAuto;

  flex: 1 1 auto;
}

.historyLabel,
.paginator {
  flex: 0 0 auto;
}
