.inputArea {
  @mixin box;
  @mixin inputArea;

  display: flex;
  flex-direction: row;
  padding: 0;
  font-size: 0.875rem;
  font-family: $codeFont;

  :global(.CodeMirror-scroll) {
    max-height: 30vh;
  }
}

/*
 * Theme
 */

:global(.light) {
  .inputArea {
    border-color: $light_textInput_borderColor;
    background: $light_textInput_background;

    &.editorFocused {
      border-color: $light_textInput_activeBorderColor;
      box-shadow: $light_textInput_activeBoxShadow;
    }
  }
}

:global(.dark) {
  .inputArea {
    border-color: $dark_textInput_borderColor;
    background: $dark_textInput_background;

    &.editorFocused {
      border-color: $dark_textInput_activeBorderColor;
      box-shadow: $dark_textInput_activeBoxShadow;
    }
  }
}
