.contentArea{
  -ms-flex-preferred-size:calc(100% - 10px);
      flex-basis:calc(100% - 10px);
  -ms-flex:1 0 calc(33.333% - 10px);
      flex:1 0 calc(33.333% - 10px);
  border:1px solid;
  border-radius:3px;
  font-size:0.85rem;
  -webkit-transition:flex 0.25s ease;
  -o-transition:flex 0.25s ease;
  transition:flex 0.25s ease;
  transition:flex 0.25s ease, -ms-flex 0.25s ease;
  will-change:flex;
}

.titleBtn{
  margin:0 10px;
  padding:10px 0 4px;
  -webkit-transition:border-color 0.25s ease;
  -o-transition:border-color 0.25s ease;
  transition:border-color 0.25s ease;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

.content > *{
  position:absolute;
  top:7px;
  right:10px;
  bottom:10px;
  left:10px;
}

.shrinkArea{
  min-height:37px;
  -ms-flex:0 1 37px;
      flex:0 1 37px;
}

.shrinkArea .titleBtn{
    border-color:transparent;
  }

.resizable{
  margin-bottom:10px;
}

:global(.light) .contentArea{
    border-color:#ffffff #f3f3f3 #ededed #f3f3f3;
    background:#fdfdfd;
  }

:global(.dark) .contentArea{
    border-color:#1f1f1f #212121 #212121 #212121;
    background:#1f1f1f;
  }
