.lengthCounter{
  position:absolute;
  right:5px;
  bottom:5px;
  padding:3px;
  border-radius:3px;
  letter-spacing:1px;
  font-size:.6rem;
  pointer-events:none;
}

  .lengthCounter.singleLine{
    bottom:50%;
    -webkit-transform:translateY(50%);
            transform:translateY(50%);
  }

:global(.light) .lengthCounter{
    background:#f7f7f7;
    color:#adadad;
  }

:global(.dark) .lengthCounter{
    background:#2f2f2f;
    color:#616161;
  }
