.item{
  display:inline-block;
  white-space:nowrap;
  font-size:0.85rem;
  line-height:1;
  cursor:pointer;
}

.icon{
  padding:4px 5px;
}

.menuIcon{
  margin-left:-2px;
  padding-right:5px;
  vertical-align:top;
  font-size:0.8rem;
}

@media (max-width: 500px){
  .item{
    font-size:0.65rem;
  }
}

:global(.light) .item{
    color:#767676;
  }

:global(.light) .item:hover{
      color:#457abb;
    }

:global(.light) .item:active{
      color:rgb(69, 110, 161);
    }

:global(.dark) .item{
    color:#767676;
  }

:global(.dark) .item:hover{
      color:#2d5079;
    }

:global(.dark) .item:active{
      color:rgb(46, 69, 94);
    }
