.root{
  position:absolute;
  z-index:1002;
  display:inline-block;
  -webkit-transition:opacity 0.1s ease-in;
  -o-transition:opacity 0.1s ease-in;
  transition:opacity 0.1s ease-in;
  pointer-events:none;
}

.tooltipped{
  padding:6px 8px;
  max-width:300px;
  border-radius:3px;
  text-decoration:none;
  font-size:0.8rem;
}

.arrow{
  position:relative;
  left:50%;
  margin-left:-5px;
  width:0;
  height:0;
  border:5px solid transparent;
  border-top-color:rgba(0, 0, 0, 0.8);
  pointer-events:none;
}

.enter{
  opacity:0;
}

.enterActive{
  opacity:1;
}

.leave{
  opacity:1;
}

.leaveActive{
  opacity:0;
}

:global(.light) .tooltipped{
    background:rgba(0, 0, 0, 0.8);
    color:#ffffff;
  }

:global(.light) .arrow{
    border-top-color:rgba(0, 0, 0, 0.8);
  }

:global(.dark) .tooltipped{
    background:rgba(0, 0, 0, 0.8);
    color:#cacaca;
  }

:global(.dark) .arrow{
    border-top-color:rgba(0, 0, 0, 0.8);
  }
