.root{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  margin-bottom:2px;
}

.icons{
  -ms-flex:1 0 auto;
      flex:1 0 auto;
  margin:-5px 0 0 -5px;
}

.date{
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  text-align:right;
}

.tooltip{
  display:inline;
}

.icon{
  padding:5px;
  cursor:pointer;
}

:global(.light) .date{
    color:#cccccc;
  }

:global(.light) .icon{
    color:#cccccc;
  }

:global(.light) .delete:hover,
    :global(.light) .delete.fill,
    :global(.light) .isFavorite:hover,
    :global(.light) .isFavorite.fill{
      color:#CE4040;
    }

:global(.light) .delete:active,:global(.light) .isFavorite:active{
      color:rgb(181, 64, 64);
    }

:global(.light) .isPrivate:hover,
    :global(.light) .isPrivate.fill{
      color:#4c4c4c;
    }

:global(.light) .isPrivate:active{
      color:rgb(115, 115, 115);
    }

:global(.light) .isLocked:hover,
    :global(.light) .isLocked.fill{
      color:#DAA520;
    }

:global(.light) .isLocked:active{
      color:rgb(191, 146, 33);
    }

:global(.light) .title:hover{
      color:#008000;
    }

:global(.light) .title:active{
      color:rgb(0, 102, 0);
    }

:global(.dark) .date{
    color:#545454;
  }

:global(.dark) .icon{
    color:#545454;
  }

:global(.dark) .delete:hover,
    :global(.dark) .delete.fill,
    :global(.dark) .isFavorite:hover,
    :global(.dark) .isFavorite.fill{
      color:#a53131;
    }

:global(.dark) .delete:active,:global(.dark) .isFavorite:active{
      color:rgb(199, 67, 67);
    }

:global(.dark) .isPrivate:hover,
    :global(.dark) .isPrivate.fill{
      color:#a9a9a9;
    }

:global(.dark) .isPrivate:active{
      color:rgb(207, 207, 207);
    }

:global(.dark) .isLocked:hover,
    :global(.dark) .isLocked.fill{
      color:#a58535;
    }

:global(.dark) .isLocked:active{
      color:rgb(196, 162, 74);
    }

:global(.dark) .title:hover{
      color:#3b9458;
    }

:global(.dark) .title:active{
      color:rgb(76, 184, 112);
    }
