.matchData {
  @mixin wordwrap;

  padding: 6px 10px;
  border-radius: 3px;
  width: 100%;
}

.textTruncated {
  cursor: pointer;
}

.didNotParticipate {
  font-style: italic;
  opacity: 0.5;
}

.groupNum {
  @mixin textOverflow;

  padding: 5px 0;

  &.clickable {
    cursor: pointer;
  }

  span {
    padding: 2px 3px;
  }
}

.matchPos {
  @mixin textOverflow;

  padding: 5px 10px;
}

.groupNum {
  white-space: nowrap;
}

/*
 * THeme
 */

:global(.light) {
  .matchData {
    background: rgba(0, 0, 0, 0.03);
  }

  .matchPos {
    color: $light_matchGroup_pos_color;
  }

  .groupNum.clickable:hover span {
    background: $light_codeMirror_highlight !important;
  }
}

:global(.dark) {
  .matchData {
    background: rgba(0, 0, 0, 0.05);
  }

  .matchPos {
    color: $dark_matchGroup_pos_color;
  }

  .groupNum.clickable:hover span {
    background: $dark_codeMirror_highlight !important;
  }
}
