.stepContainer,
.debugContainer{
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
  line-height:1.3em;
  white-space:pre-wrap;
}

.stepContainer{
  overflow:hidden;
}

.shortcuts{
  cursor:pointer;
  margin:10px 0 0;
  float:right;
}

.shortcuts:hover{
    text-decoration:underline;
  }

.sliderArea{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align:center;
      align-items:center;
}

.sliderArea .leftInput,
  .sliderArea .rightInput{
    -ms-flex:0 0 60px;
        flex:0 0 60px;
    text-align:center;
    position:relative;
    z-index:5;
  }

.sliderArea .leftInput{
    margin-right:10px;
  }

.sliderArea .rightInput{
    margin-left:10px;
  }

.sliderArea .slider{
    -ms-flex:1 1 auto;
        flex:1 1 auto;
  }

.controls{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack:center;
      justify-content:center;
}

.controls .goBeginning,
  .controls .goEnd,
  .controls .increase,
  .controls .decrease,
  .controls .playPause{
    font-size:15px;
    -ms-flex:0 0 20px;
        flex:0 0 20px;
    cursor:pointer;
    -webkit-user-select:none;
       -moz-user-select:none;
        -ms-user-select:none;
            user-select:none;
    margin:0 5px;
    width:20px;
    text-align:center;
  }

.controls .goBeginning:hover,.controls .goEnd:hover,.controls .increase:hover,.controls .decrease:hover,.controls .playPause:hover{
      opacity:0.8;
    }

.controls .goBeginning:active,.controls .goEnd:active,.controls .increase:active,.controls .decrease:active,.controls .playPause:active{
      opacity:0.7;
    }

.controls .playPause{
    -ms-flex:0 0 auto;
        flex:0 0 auto;
  }

.controls .disabled{
    opacity:0.5;
    pointer-events:none;
  }

.input{
  text-align:center;
}

.loadMoreButton{
  margin-top:10px;
}

.container{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
}

.stepSlider{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
  position:relative;
  margin-bottom:10px;
}

.matchStep{
  -ms-flex:1 1 auto;
      flex:1 1 auto;
}

:global(.light) .playPause{
    color:#457abb;
  }

:global(.light) .goBeginning,
  :global(.light) .goEnd,
  :global(.light) .increase,
  :global(.light) .decrease{
    color:#777;
  }

:global(.dark) .playPause{
    color:#2d5079;
  }

:global(.dark) .goBeginning,
  :global(.dark) .goEnd,
  :global(.dark) .increase,
  :global(.dark) .decrease{
    color:#777;
  }

:global(.dark) .leftInput,
  :global(.dark) .rightInput{
    background:#1f1f1f;
  }
