.sidebar{
  position:absolute;
  top:50px;
  bottom:0;
  left:0;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  width:270px;
  border-right:1px solid;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}
.sidebarChild{
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  -ms-flex:1 1 auto;

      flex:1 1 auto;
  margin:10px 10px 10px 0;
}
.sidebarContents{
  position:relative;
  margin-left:50px;
  height:100%;
  overflow:hidden;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
}
.offlineText{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
  height:35px;
  line-height:35px;
  text-align:center;
  font-size:0.8rem;
}
.sponsor{
  font-size:0.8rem;
  border-top:1px solid;
  margin:10px;
  outline:0;
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}
:global(.light) .sidebar{
    border-color:#ffffff;
    background:#f7f7f7;
  }
:global(.light) .offlineText{
    background:#a03b3b;
    color:#fff;
  }
:global(.light) .sponsor{
    color:#344f71;
    border-color:rgba(58, 58, 58, 0.1);
  }
:global(.dark) .sidebar{
    border-color:#212121;
    background:#1a1a1a;
  }
:global(.dark) .offlineText{
    background:#a03b3b;
    color:#d0d0d0;
  }
:global(.dark) .sponsor{
    color:#797979;
    border-color:rgba(156, 156, 156, 0.07);
  }
