.menuItem{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  padding:8px 20px;
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

.disabled{
  pointer-events:none;
}

.icon{
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  text-align:right;
}

.icon.hidden{
    visibility:hidden;
  }

:global(.light) .menuItem{
    color:#505050;
  }

:global(.light) .menuItem:hover{
      background:#d8d8d8;
      color:#505050;
    }

:global(.light) .menuItem:active{
      background:#cacaca;
    }

:global(.dark) .menuItem{
    color:#bbbbbb;
  }

:global(.dark) .menuItem:hover{
      background:#555555;
      color:#ffffff;
    }

:global(.dark) .menuItem:active{
      background:#5a5a5a;
    }
