.fetchError {
  margin-top: 10px;
}

.chartContainer {
  margin: 10px 0 0;
  min-height: 200px;
}

.spinner {
  margin: 10px auto 0;
}

.tooltip {
  padding: 6px 8px;
  border-radius: 3px;
  text-align: center;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-size: 0.75rem;
  font-family: $appFont;
  max-width: 200px;
  display: block;
}

/*
 * Theme
 */

:global(.light) {
  .tooltip {
    background: $light_tooltipped_background;
    color: $light_tooltipped_color;
  }
}

:global(.dark) {
  .tooltip {
    background: $dark_tooltipped_background;
    color: $dark_tooltipped_color;
  }
}
