.loading {
  z-index: 1;
  border-top: 6px solid rgba(0, 0, 0, 0.2);
  border-right: 6px solid rgba(0, 0, 0, 0.2);
  border-bottom: 6px solid rgba(0, 0, 0, 0.2);
  border-left: 6px solid;
  transform: translateZ(0);
  animation: loading $spinnerDuration infinite linear;
}

.loading,
.loading:after {
  border-radius: 50%;
}

.large {
  width: 60px;
  height: 60px;
}

.medium {
  width: 40px;
  height: 40px;
}

.small {
  width: 30px;
  height: 30px;
}

.tiny {
  width: 10px;
  height: 10px;
}

.loading.tiny {
  border-width: 3px !important;
}

.loading.small {
  border-width: 5px !important;
}

@keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

:global(.light) {
  .loading {
    border-left-color: $light_spinner_borderColor;
  }
}

:global(.dark) {
  .loading {
    border-left-color: $dark_spinner_borderColor;
  }
}
