:global .CodeMirror{
    background:transparent;
    letter-spacing:1px;
    line-height:1.6em;
    cursor:text;
  }
  :global .CodeMirror-scrollbar-filler,
  :global .CodeMirror-gutter-filler{
    background-color:transparent;
  }
  :global .CodeMirror-gutters{
    margin-left:-5px;
    border-right:1px solid;
    white-space:nowrap;
  }
  :global .CodeMirror-linenumber{
    margin-left:-5px;
    padding:0 3px 0 5px;
    min-width:20px;
    text-align:right;
    white-space:nowrap;
  }
  :global .CodeMirror-cursor{
    width:0;
    border-right:none;
    border-left:1px solid black;
    pointer-events:none;
  }
  :global .cm-tab{
    display:inline-block;
    text-decoration:inherit;
  }
  :global .CodeMirror{
    position:relative;
    overflow:hidden;
  }
  :global .CodeMirror-scroll{
    position:relative;
    overflow:scroll !important;
    margin-right:-30px;
    margin-bottom:-30px;
    padding-bottom:30px;
    height:100%;
    outline:none;
  }
  :global .CodeMirror-sizer{
    position:relative;
    border-right:30px solid transparent;
  }
  :global .CodeMirror-vscrollbar,
  :global .CodeMirror-hscrollbar,
  :global .CodeMirror-scrollbar-filler,
  :global .CodeMirror-gutter-filler{
    position:absolute;
    z-index:6;
    display:none;
  }
  :global .CodeMirror-vscrollbar{
    top:0;
    right:0;
    overflow-x:hidden;
    overflow-y:scroll;
  }
  :global .CodeMirror-hscrollbar{
    bottom:0;
    left:0;
    overflow-x:scroll;
    overflow-y:hidden;
  }
  :global .CodeMirror-scrollbar-filler{
    right:0;
    bottom:0;
  }
  :global .CodeMirror-gutter-filler{
    bottom:0;
    left:0;
  }
  :global .CodeMirror-gutters{
    position:absolute;
    top:0;
    left:0;
    z-index:3;
    min-height:100%;
  }
  :global .CodeMirror-gutter{
    display:inline-block;
    margin-bottom:-30px;
    height:100%;
    vertical-align:top;
    white-space:normal;
  }
  :global .CodeMirror-gutter-wrapper{
    position:absolute;
    z-index:4;
    border:none !important;
    background:none !important;
  }
  :global .CodeMirror-gutter-background{
    position:absolute;
    top:0;
    bottom:0;
    z-index:4;
  }
  :global .CodeMirror-gutter-elt{
    position:absolute;
    z-index:4;
    cursor:default;
  }
  :global .CodeMirror-gutter-wrapper{
    -webkit-user-select:none;
       -moz-user-select:none;
        -ms-user-select:none;
            user-select:none;
  }
  :global .CodeMirror-lines{
    min-height:1px;
    cursor:text;
  }
  :global .CodeMirror pre{
    position:relative;
    z-index:0;
    overflow:visible;
    margin:0;
    border-width:0;
    border-radius:0;
    background:transparent;
    color:inherit;
    white-space:pre;
    word-wrap:normal;
    font-size:inherit;
    font-family:inherit;
    line-height:inherit;
    -webkit-tap-highlight-color:transparent;
    -webkit-font-variant-ligatures:none;
            font-variant-ligatures:none;
  }
  :global .CodeMirror-wrap pre{
    white-space:pre-wrap;
    word-wrap:break-word;
    word-break:normal;
  }
  :global .CodeMirror-linebackground{
    position:absolute;
    top:0;
    right:0;
    bottom:0;
    left:0;
    z-index:0;
  }
  :global .CodeMirror-linewidget{
    position:relative;
    z-index:2;
    overflow:auto;
  }
  :global .CodeMirror-code{
    outline:none;
  }
  :global .CodeMirror-scroll,
  :global .CodeMirror-sizer,
  :global .CodeMirror-gutter,
  :global .CodeMirror-gutters,
  :global .CodeMirror-linenumber{
    -webkit-box-sizing:content-box;
            box-sizing:content-box;
  }
  :global .CodeMirror-measure{
    position:absolute;
    visibility:hidden;
    overflow:hidden;
    width:100%;
    height:0;
  }
  :global .CodeMirror-cursor{
    position:absolute;
    pointer-events:none;
  }
  :global .CodeMirror-measure pre{
    position:static;
  }
  :global div.CodeMirror-cursors{
    position:relative;
    z-index:3;
    visibility:hidden;
  }
  :global div.CodeMirror-dragcursors{
    visibility:visible;
  }
  :global .CodeMirror-focused div.CodeMirror-cursors{
    visibility:visible;
  }
  :global .cm-force-border{
    padding-right:0.1px;
  }
  :global .cm-tab-wrap-hack:after{
    content:'';
  }
  :global .CodeMirror-selected{
    pointer-events:none;
  }
  :global .light .CodeMirror-cursor{
      border-color:#282828;
    }
  :global .light .CodeMirror-selected{
      background:rgba(128, 127, 127, 0.3);
    }
  :global .light .CodeMirror-focused .CodeMirror-selected{
      background:rgba(77, 145, 226, 0.3);
    }
  :global .light .CodeMirror-placeholder{
      color:#cccccc;
    }
  :global .light .CodeMirror-gutters{
      border-right-color:#f7f7f7;
      background:#f7f7f7;
    }
  :global .light .CodeMirror-linenumber{
      color:#c8c8c8;
    }
  :global .dark .CodeMirror-cursor{
      border-color:#cccccc;
    }
  :global .dark .CodeMirror-selected{
      background:rgba(128, 127, 127, 0.3);
    }
  :global .dark .CodeMirror-focused .CodeMirror-selected{
      background:rgba(77, 145, 226, 0.3);
    }
  :global .dark .CodeMirror-placeholder{
      color:#676767;
    }
  :global .dark .CodeMirror-gutters{
      border-right-color:#2d2d2d;
      background:#2d2d2d;
    }
  :global .dark .CodeMirror-linenumber{
      color:#545454;
    }
