.root{
  display:-ms-flexbox;
  display:flex;
  width:100%;
}

.centered{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack:center;
      justify-content:center;
  width:100%;
  text-align:center;
}

.loadingText{
  margin-bottom:20px;
  font-weight:200;
}

.matchSelector{
  overflow:auto;
  -webkit-overflow-scrolling:touch;
}

@media (max-width: 900px){
  .root{
    -ms-flex-direction:column;
        flex-direction:column;
  }

  .matchSelector{
    -ms-flex:1 1 auto;
        flex:1 1 auto;
    margin-bottom:20px;
    min-height:60px;
  }

  .debugResult{
    -ms-flex:5 1 auto;
        flex:5 1 auto;
  }
}

@media (min-width: 900px){
  .matchSelector{
    -ms-flex:0 0 auto;
        flex:0 0 auto;
    padding-right:20px;
    min-width:300px;
  }

  .debugResult{
    -ms-flex:1 1 auto;
        flex:1 1 auto;
  }
}

:global(.light) .match{
    color:#338433;
  }

:global(.light) .noMatch{
    color:#cc3f3f;
  }

:global(.dark) .match{
    color:#338433;
  }

:global(.dark) .noMatch{
    color:#cc3f3f;
  }
