.root {
  @mixin fullscreen 10px, 10px, 10px, 10px;

  display: flex;
}

.contents {
  @mixin overflowAuto;

  white-space: pre;
  font-family: $codeFont;
  position: relative;
  line-height: 1.3;
  flex: 1 1 auto;
}

.copyButton {
  @mixin genericButton;

  font-size: 1rem;
}

.contentWrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: 100%;
}

.includeFullMatch {
  flex: 0 0 auto;
  padding-top: 5px;
  vertical-align: middle;
}

@media (min-width: $fullscreenBreakpoint) {
  .root {
    flex-direction: row;
  }

  .dataContainer {
    flex: 1 1 auto;
  }

  .listMenu {
    padding-right: 20px;
    flex: 0 0 auto;
    min-width: 175px;
  }
}

@media (max-width: $fullscreenBreakpoint) {
  .root {
    flex-direction: column;
  }

  .dataContainer {
    margin-top: 10px;
    flex: 3 1 auto;
  }

  .listMenu {
    flex: 1 1 auto;
  }
}
