.centered{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  display:-ms-flexbox;

  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack:center;
      justify-content:center;
}

.note{
  margin-top:10px;
  line-height:1.25;
}

.input{
  font-size:0.8rem;
}

.paragraph{
  margin:10px 0;
  font-size:0.85rem;
}

.accountData{
  margin-top:20px;
}

:global(.light) .centered{
    background:#f7f7f7;
  }

:global(.dark) .centered{
    background:#1f1f1f;
  }
