.root{
  position:relative;
  float:left;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  padding:10px 0;
  width:50px;
  height:100%;
}

.accountLink,
.link{
  text-decoration:none;
  cursor:pointer;
  outline:0;
}

.accountLink img{
    -webkit-filter:grayscale(100%);
            filter:grayscale(100%);
  }

.accountLink.active .iconButton::after{
    position:absolute;
    top:0;
    right:0;
    bottom:0;
    left:0;

    content:'';
    -webkit-box-shadow:inset 0 0 4px 2px rgba(0, 0, 0, 0.15);
            box-shadow:inset 0 0 4px 2px rgba(0, 0, 0, 0.15);
    border-radius:50%;
  }

.iconButton{
  margin:0 auto 10px auto;
}

.donationButton{
  position:absolute;
  bottom:0;
  left:50%;
  margin:0;
  -webkit-transform:translateX(-50%);
          transform:translateX(-50%);
}

.active img{
  -webkit-filter:none;
          filter:none;
}

:global(.light) .root{
    background:#e1e1e1;
    color:#777777;
  }

:global(.light) .link{
    color:#777777;
  }

:global(.light) .link.active{
      color:#e4e4e4;
    }

:global(.light) .link.active .iconButton{
        background:#636363 !important;
      }

:global(.light) .iconButton{
    background:rgb(214, 214, 214);
  }

:global(.light) .iconButton:hover{
  background:rgb(204, 204, 204);
}

:global(.light) .iconButton:active{
  background:rgb(195, 195, 195);
}

:global(.light) .donationButton .iconButton:hover{
    background:#56845c;
    color:#ffd700;
  }

:global(.dark) .root{
    background:#121212;
    color:#6f6f6f;
  }

:global(.dark) .link{
    color:#6f6f6f;
  }

:global(.dark) .link.active{
      color:#9e9e9e;
    }

:global(.dark) .link.active .iconButton{
        background:#4e4e4e !important;
      }

:global(.dark) .iconButton{
    background:rgb(31, 31, 31);
  }

:global(.dark) .iconButton:hover{
  background:rgb(43, 43, 43);
}

:global(.dark) .iconButton:active{
  background:rgb(56, 56, 56);
}

:global(.dark) .donationButton .iconButton:hover{
    background:#2c6d35;
    color:#d4c470;
  }
