.markdownContainer{
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  white-space:pre;
  line-height:16px;
}

  .markdownContainer > *:first-child{
    margin-top:0;
  }

  .markdownContainer > *:last-child{
    margin-bottom:0;
  }

  .markdownContainer ul,
  .markdownContainer ol{
    padding-left:30px;
  }

  .markdownContainer ul{
    list-style-type:disc;
  }

  .markdownContainer ol{
    list-style-type:decimal;
  }

  .markdownContainer p{
    margin:10px 0;
  }

  .markdownContainer h1,
  .markdownContainer h2{
    padding-bottom:5px;
    border-bottom:1px solid;
  }

  .markdownContainer h1{
    font-size:1.5rem;
  }

  .markdownContainer h2{
    font-size:1.2rem;
  }

  .markdownContainer h3{
    font-size:1rem;
  }

  .markdownContainer h4{
    font-size:0.8rem;
  }

  .markdownContainer h5{
    font-size:0.6rem;
  }

  .markdownContainer h6{
    font-size:0.5rem;
  }

  .markdownContainer h1,
  .markdownContainer h2,
  .markdownContainer h3,
  .markdownContainer h4,
  .markdownContainer h5,
  .markdownContainer h6{
    margin-bottom:20px;
  }

  .markdownContainer code{
    display:inline;
    padding:2px;
    border-radius:3px;
    white-space:pre;
    font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
  }

  .markdownContainer pre{
    overflow:auto;
    -webkit-overflow-scrolling:touch;

    display:block;
    padding:8px;
    border-radius:3px;
    white-space:normal;
    font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
    line-height:1.45;
  }

  .markdownContainer pre code{
      padding:0;
      background:none !important;
    }

  .markdownContainer blockquote{
    padding:0 15px;
    border-left:4px solid;
  }

  .markdownContainer hr{
    margin:24px 0;
    padding:0;
    height:4px;
    border:0;
  }

:global(.light) .markdownContainer h1,
    :global(.light) .markdownContainer h2{
      border-color:#cccccc;
    }

:global(.light) .markdownContainer code{
      background:rgba(0, 0, 0, 0.04);
    }

:global(.light) .markdownContainer pre{
      background:#f7f7f7;
    }

:global(.light) .markdownContainer blockquote{
      border-left-color:#dddddd;
      color:#777777;
    }

:global(.light) .markdownContainer hr{
      background:#e7e7e7;
    }

:global(.dark) .markdownContainer h1,
    :global(.dark) .markdownContainer h2{
      border-color:#1a1a1a;
    }

:global(.dark) .markdownContainer code{
      background:rgba(0, 0, 0, 0.04);
    }

:global(.dark) .markdownContainer pre{
      background:#2d2d2d;
    }

:global(.dark) .markdownContainer blockquote{
      border-left-color:#dddddd;
      color:#777777;
    }

:global(.dark) .markdownContainer hr{
      background:#e7e7e7;
    }
