.opacityContainer {
  @mixin fullscreen;

  overflow: hidden;
  padding: 10px;
  transition: opacity $defaultSpeed ease-in-out;
}

.quickrefDetails {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: 100%;
  transition: transform $defaultSpeed ease-in-out;
  transform: translateY(0);
}

.label {
  flex: 0 0 auto;
}

.details {
  @mixin overflowAuto;

  flex: 1 1 auto;
}

.tokenExplanation,
.regexExample {
  line-height: 1.3;
}

.tokenExplanation {
  display: flex;
  flex-direction: row;
}

.explanationText {
  flex: 1 1 auto;
  margin-left: 5px;
}

.token {
  flex: 0 0 auto;
}

.regex,
.regexMatch,
.token {
  @mixin wordwrap;

  font-family: $codeFont;
}

.regexExample {
  margin-top: 10px;
}

.resetFont {
  font-size: inherit;
}

.regex {
  margin-bottom: 10px;
}

.closeDetails {
  @mixin genericButton;

  display: inline-block;
  margin: 0 -5px;
  padding: 0 5px;
}

/*
 * Animations
 */

.enter {
  opacity: 0;

  .quickrefDetails {
    transform: translateY(30px);
  }
}

.enterActive {
  opacity: 1;

  .quickrefDetails {
    transform: translateY(0);
    pointer-events: none;
  }
}

.leave {
  opacity: 1;

  .quickrefDetails {
    transform: translateY(0);
  }
}

.leaveActive {
  opacity: 0;
  pointer-events: none;

  .quickrefDetails {
    transform: translateY(30px);
  }
}

/*
 * Theme
 */

:global(.light) {
  .opacityContainer {
    background: $light_rightSidebar_contentArea_background;
  }

  .regex {
    color: $light_quickrefDetails_regex_color;
  }

  .closeDetails {
    color: $light_quickrefDetails_closeDetails_color;
  }
}

:global(.dark) {
  .opacityContainer {
    background: $dark_rightSidebar_contentArea_background;
    box-shadow: 0 -20px 10px $dark_rightSidebar_contentArea_background;
  }

  .regex {
    color: $dark_quickrefDetails_regex_color;
  }

  .closeDetails {
    color: $dark_quickrefDetails_closeDetails_color;
  }
}
