.sidebar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  box-sizing: border-box;
  width: $totalLeftSidebarWidth;
  border-right: 1px solid;
  transition: transform $sidebarSpeed ease;
  user-select: none;
  will-change: transform;
}

.sidebarContents {
  margin-left: $innerLeftSidebarWidth;
  padding: 10px 10px 10px 0;
}

.menuHamburgerParent {
  top: 0;
  left: 0;
}

.collapsedSidebar {
  @mixin fullscreen;

  z-index: 1000;
}

.header {
  flex: 1 0 $headerHeight;
  margin-right: -1px; /* eat up the 1 border pixel in the header */
  padding-right: 1px;
  height: $headerHeight;
  line-height: 50px;
  width: 100%;
  text-align: center;
}

.sidebarBackdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity $sidebarSpeed ease-in-out;
  will-change: opacity;
}

.contents {
  height: calc(100% - $headerHeight);
}

/*
 * Animations
 */

.enter {
  opacity: 0;
  will-change: transform, opacity;

  .sidebar {
    transform: translateX(-$totalLeftSidebarWidth);
  }
}

.enterActive {
  opacity: 1;
  pointer-events: none;

  .sidebar {
    transform: translateX(0);
  }
}

.leave {
  opacity: 1;

  .sidebar {
    transform: translateX(0);
  }
}

.leaveActive {
  opacity: 0;
  pointer-events: none;

  .sidebar {
    transform: translateX(-$totalLeftSidebarWidth);
  }
}

/*
 * Theme
 */

:global(.light) {
  .header {
    border-color: $light_header_border;
    background: $light_header_background;
    color: $light_header_color;
  }
  .sidebar {
    border-color: $light_sidebar_borderColor;
    background: $light_sidebar_background;
    box-shadow: 0 5px 30px $light_modal_boxShadow_color;
    color: $light_globalApp_color;
  }
  .menuHamburger {
    @mixin iconBtnTheme $light_sidebar_menuHamburger_background, lightness;

    color: $light_sidebar_menuHamburger_color;
  }
  .sidebarBackdrop {
    background: $light_modal_backdrop_background;
  }
}

:global(.dark) {
  .header {
    border-color: $dark_header_border;
    background: $dark_header_background;
    color: $dark_header_color;
  }
  .sidebar {
    border-color: $dark_sidebar_borderColor;
    background: $dark_sidebar_background;
    box-shadow: 0 5px 30px $dark_modal_boxShadow_color;
    color: $dark_globalApp_color;
  }
  .menuHamburger {
    @mixin iconBtnTheme $dark_sidebar_menuHamburger_background, lightness;

    color: $dark_sidebar_menuHamburger_color;
  }
  .sidebarBackdrop {
    background: $dark_modal_backdrop_background;
  }
}
