.root{
  position:relative;
}

.title{
  white-space:nowrap;
  overflow:hidden;
  -o-text-overflow:ellipsis;
     text-overflow:ellipsis;

  margin-bottom:5px;
  padding-bottom:5px;
}

.versionSelector{
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  padding:5px;
  opacity:0.4;

  margin-left:5px;
  text-transform:none;
  cursor:pointer;
}

.versionSelector:hover{
  opacity:0.6;
}

.versionSelector:active{
  opacity:0.8;
}

.tooltip{
  display:inline-block;
}

:global(.light) .versionSelector{
    color:#000000;
  }

:global(.dark) .versionSelector{
    color:#989898;
  }
