.modalFooter{
  display:-ms-flexbox;
  display:flex;
  -ms-flex:0 0 65px;
      flex:0 0 65px;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack:end;
      justify-content:flex-end;
  padding:0 15px;
  height:65px;
  border-top:1px solid;
  text-align:right;
}

:global(.light) .modalFooter{
    border-color:#d0d0d0;
    background:#e4e4e4;
  }

:global(.dark) .modalFooter{
    border-color:rgb(43, 43, 43);
    background:#2d2d2d;
  }
