.root{
  display:-ms-flexbox;
  display:flex;
  -ms-flex:0 1 auto;
      flex:0 1 auto;
  -ms-flex-direction:column;
      flex-direction:column;
  margin-top:10px;
  margin-bottom:-7px;
  -webkit-transition:flex 0.25s cubic-bezier(0.23, 1, 0.32, 1);
  -o-transition:flex 0.25s cubic-bezier(0.23, 1, 0.32, 1);
  transition:flex 0.25s cubic-bezier(0.23, 1, 0.32, 1);
  transition:flex 0.25s cubic-bezier(0.23, 1, 0.32, 1), -ms-flex 0.25s cubic-bezier(0.23, 1, 0.32, 1);
}

  .root.visible{
    -ms-flex:1 1 auto;
        flex:1 1 auto;
    margin-bottom:0;
    max-height:50%;
  }

.inputArea{
  display:-ms-flexbox;
  display:flex;
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  -ms-flex-direction:column;
      flex-direction:column;
  overflow:hidden;
  height:100%;
}

.input{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
  margin-bottom:5px;
}

.result{
  white-space:pre-wrap;
  word-wrap:break-word;
  word-break:break-all;
  overflow-wrap:break-word;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  padding:5px;
  width:100%;
  outline:0;
  border:1px solid;
  border-radius:3px;
  -webkit-box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.1);
          box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.1);
  font-family:inherit;
  -webkit-transition:all 0.2s ease;
  -o-transition:all 0.2s ease;
  transition:all 0.2s ease;
  -webkit-transition-property:border-color, -webkit-box-shadow;
  transition-property:border-color, -webkit-box-shadow;
  -o-transition-property:border-color, box-shadow;
  transition-property:border-color, box-shadow;
  transition-property:border-color, box-shadow, -webkit-box-shadow;
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  -ms-flex:1 1 auto;

      flex:1 1 auto;
  line-height:1.4em;
  outline:0;
  font-size:0.875rem;
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
  resize:none;
  position:relative;
  width:100%;
  height:100%;
}

.result :global(.CodeMirror){
    position:absolute;
    top:5px;
    right:5px;
    bottom:5px;
    left:5px;
  }

@media (max-height: 500px){
  .root.visible{
    margin-top:0;
  }
}

:global(.light) .result{
    border-color:#bdbdbd;
    background:#fafafa;
  }

:global(.dark) .result{
    border-color:#292828;
    background:#1a1a1a;
  }
