.link {
  text-decoration: none;
}

.theme:hover {
  text-decoration: underline;
}

/*
* Theme
*/

:global(.light) {
  .theme {
    color: $light_link_color;

    &:visited {
      color: $light_link_visitedColor;
    }
    &:hover {
      color: $light_link_hoverColor;
    }
    &:active {
      color: $light_link_activeColor
    }
  }
}

:global(.dark) {
  .theme {
    color: $dark_link_color;

    &:visited {
      color: $dark_link_visitedColor;
    }
    &:hover {
      color: $dark_link_hoverColor;
    }
    &:active {
      color: $dark_link_activeColor
    }
  }
}
