.saveButton {
  margin-right: 10px;
}

.root {
  @mixin fullscreen;
  @mixin overflowAuto;

  padding: 10px;
  box-sizing: border-box;

  select {
    margin: 0 0 5px 5px;
    min-width: 100px;
  }
}

.spaceUnder {
  margin-bottom: 5px;
}

.saveIndicator {
  position: absolute;
  top: 10px;
  right: 10px;
  color: green;
  background: inherit;
  font-size: 0.7rem;
  text-transform: uppercase;
  pointer-events: none;
  transition: all 300ms ease-out;
}

/*
 * Animations
 */

.saveLeave {
  opacity: 1;
  top: 10px;
}

.saveLeaveActive {
  opacity: 0;
  top: 0;
}

/*
 * Theme
 */

:global(.light) {
  .content {
    background: $light_fullscreenLayer_background;
  }

  .saveIndicator {
    color: $light_listMenu_toggleContent_checkMark_color;
  }
}

:global(.dark) {
  .content {
    background: $dark_fullscreenLayer_background;
  }

  .saveIndicator {
    color: $dark_listMenu_toggleContent_checkMark_color;
  }
}
