.root{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  cursor:pointer;
  -webkit-transition:color 0.1s ease;
  -o-transition:color 0.1s ease;
  transition:color 0.1s ease;
}

  .root .toggle{
    -ms-flex:1 1 auto;
        flex:1 1 auto;
    -ms-flex-item-align:center;
        align-self:center;
    -ms-flex-align:center;
        align-items:center;
    text-align:right;
  }

:global(.light) .root .toggle{
      color:#a9a9a9;
    }

:global(.light) .root:hover .toggle{
      color:#344f71;
    }

:global(.light) .root:active .toggle,
    :global(.light) .root:active{
      color:#5a84b9;
    }

:global(.dark) .root .toggle{
      color:#5f5f5f;
    }

:global(.dark) .root:hover .toggle{
      color:#828282;
    }

:global(.dark) .root:active .toggle,
    :global(.dark) .root:active{
      color:#888888;
    }
