.root {
  display: flex;
  flex-direction: row;
  cursor: pointer;
  transition: color $labelButtonSpeed ease;

  .toggle {
    flex: 1 1 auto;
    align-self: center;
    align-items: center;
    text-align: right;
  }
}

/*
* Theme
*/

:global(.light) {
  .root {
    .toggle {
      color: $light_expandableLabel_color;
    }

    &:hover .toggle {
      color: $light_expandableLabel_hoverColor;
    }

    &:active .toggle,
    &:active {
      color: $light_expandableLabel_activeColor;
    }
  }
}

:global(.dark) {
  .root {
    .toggle {
      color: $dark_expandableLabel_color;
    }

    &:hover .toggle {
      color: $dark_expandableLabel_hoverColor;
    }

    &:active .toggle,
    &:active {
      color: $dark_expandableLabel_activeColor;
    }
  }
}
