.root {
  display: flex;
  flex-direction: row;
}

.url {
  flex: 1 0 auto;
}

.flavor {
  flex: 1 1 auto;
  text-align: right;
}

.title {
  font-weight: bold;
}

/*
 * Theme
 */

:global(.light) {
  .flavor {
    color: $light_titleRow_flavor_color;
  }
  .root a:not(:active) .title {
    color: $light_titleRow_title_color;
  }
}

:global(.dark) {
  .flavor {
    color: $dark_titleRow_flavor_color;
  }
  .root a:not(:active) .title {
    color: $dark_titleRow_title_color;
  }
}
