.sponsor {
  position: relative;
}

.carbonLink,
.revoLink {
  position: relative;
  box-sizing: border-box;
  display: block;

  strong {
    margin: 5px 0;
    display: block;
  }

  strong,
  .description {
    line-height: 1.4;
  }
}

.sponsorText {
  position: absolute;
  top: -15px;
  padding: 2px 6px;
  border-radius: 3px;
  content: 'Sponsor';
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.7rem;
}

.carbonLink {
  padding: 10px 0 0;
}

.revoLink {
  background: url('data:image/png;base64,');
  background-repeat: no-repeat;
  padding: 5px 0 30px;
  background-size: 30%;
  background-position: bottom center;
}

:global(.light) {
  .sponsorText {
    background: $light_theme_color;
    color: $light_lightGrey;
  }
}

:global(.dark) {
  .sponsorText {
    background: $dark_theme_color;
    color: $dark_globalApp_color;
  }
}
