.groupLabel,
.tokenExplanation{
  display:inline-block;
}

.groupLabel,
.nodeLabel,
.boldNodeLabel{
  white-space:pre-wrap;
  word-wrap:break-word;
  word-break:break-all;
  overflow-wrap:break-word;

  vertical-align:top;
}

.boldNodeLabel{
  font-weight:bold;
}

.groupLabel,
.token{
  letter-spacing:1px;
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
}

.token{
  white-space:pre-wrap;
  word-wrap:break-word;
  word-break:break-all;
  overflow-wrap:break-word;
}
