.root{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  display:-ms-flexbox;

  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  overflow:hidden;
  padding:10px;
}

@media (max-width: 900px){
  .root{
    -ms-flex-direction:column;
        flex-direction:column;
    overflow:initial;
  }
}
