.textInput{
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace !important;
  resize:none;
}

.select{
  margin-bottom:5px;
  width:auto;
}

.button{
  margin-top:10px;
  text-align:right;
}

.notice{
  margin-right:5px;
}

.notice,
.textInput,
.button,
.select{
  font-size:0.8rem;
}

.saveBtn{
  margin-right:10px;
}

.container{
  margin-bottom:15px;
}

.resizable{
  resize:vertical;
}

.testResult{
  text-align:center;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack:center;
      justify-content:center;
  padding:15px 0;
  font-size:0.8rem;
}

:global(.light) .testResult.pass{
    background:#46a754;
    color:#ffffff;
  }

:global(.light) .testResult.fail{
    background:#d03b3b;
    color:#ffffff;
  }

:global(.dark) .testResult.pass{
    background:#0e6f1c;
    color:#ffffff;
  }

:global(.dark) .testResult.fail{
    background:#902929;
    color:#f3eded;
  }
