.testRowParent {
  position: relative;
  overflow: hidden;
  margin-bottom: 5px;
  border-radius: 3px;
}

.header {
  position: relative;
}

.testRow {
  display: flex;
  align-items: center;
  padding: 8px 110px 8px 10px;
  cursor: pointer;
  user-select: none;
}

.activeTest {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.tools {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  font-size: 1rem;
  padding: 0 4px;
  display: flex;
  align-items: center;

  .icon {
    padding: 2px 5px;
    width: 16px;
    text-align: center;
    cursor: pointer;

    &:last-of-type {
      margin-right: 0;
    }
  }
}

.body {
  flex: 1 1 auto;
  overflow: hidden;
}

.caret {
  width: 20px;
  opacity: 0.6;
  text-align: center;
  margin-left: -6px;
  flex: 0 0 auto;
}

.description {
  font-weight: bold;
  font-size: 0.75rem;
  padding-bottom: 5px;
}

.metaData {
  font-size: 0.7rem;
  font-family: $codeFont;
}

.description,
.metaData {
  @mixin textOverflow;
}

.metadataDetails {
  font-weight: bold;
}

.disabled {
  opacity: 0.5;
  pointer-events: none;
}

.delete,
.move,
.edit {
  &:hover {
    opacity: 0.75;
  }

  &:active {
    opacity: 0.85;
  }
}

/*
 * Theme
 */

:global(.light) {
  .testRowParent {
    background: $light_testList_testRowParent_background;
    color: $light_testList_testRowParent_color;

    .tools {
      color: $light_testList_tools_color;
    }

    .testRow {
      background: $light_testList_testRow_background;

      &:hover {
        background: $light_testList_testRow_hoverBackground;
      }

      &:active {
        background: $light_testList_testRow_activeBackground;
      }
    }

    &.fail {
      background: $light_testList_testRowParent_fail_background;

      .tools {
        color: $light_testList_tools_fail_color;
      }

      .testRow {
        background: $light_testList_testRow_fail_background;
        color: $light_testList_testRow_fail_color;

        &:hover {
          background: $light_testList_testRow_fail_hoverBackground;
        }

        &:active {
          background: $light_testList_testRow_fail_activeBackground;
        }
      }
    }

    &.pass {
      background: $light_testList_testRowParent_pass_background;

      .tools {
        color: $light_testList_tools_pass_color;
      }

      .testRow {
        background: $light_testList_testRow_pass_background;
        color: $light_testList_testRow_pass_color;

        &:hover {
          background: $light_testList_testRow_pass_hoverBackground;
        }

        &:active {
          background: $light_testList_testRow_pass_activeBackground;
        }
      }
    }
  }
}

:global(.dark) {
  .testRowParent {
    background: $dark_testList_testRowParent_background;
    color: $dark_testList_testRowParent_color;

    .tools {
      color: $dark_testList_tools_color;
    }

    .testRow {
      background: $dark_testList_testRow_background;

      &:hover {
        background: $dark_testList_testRow_hoverBackground;
      }

      &:active {
        background: $dark_testList_testRow_activeBackground;
      }
    }

    &.fail {
      background: $dark_testList_testRowParent_fail_background;

      .tools {
        color: $dark_testList_tools_fail_color;
      }

      .testRow {
        background: $dark_testList_testRow_fail_background;
        color: $dark_testList_testRow_fail_color;

        &:hover {
          background: $dark_testList_testRow_fail_hoverBackground;
        }

        &:active {
          background: $dark_testList_testRow_fail_activeBackground;
        }
      }
    }

    &.pass {
      background: $dark_testList_testRowParent_pass_background;

      .tools {
        color: $dark_testList_tools_pass_color;
      }

      .testRow {
        background: $dark_testList_testRow_pass_background;
        color: $dark_testList_testRow_pass_color;

        &:hover {
          background: $dark_testList_testRow_pass_hoverBackground;
        }

        &:active {
          background: $dark_testList_testRow_pass_activeBackground;
        }
      }
    }
  }
}
