/*
 * Theme
 */

:global(.light) {
  .group0:nth-child(odd) {
    background: $light_matchGroup_0;
  }

  .group0:nth-child(even) {
    background: $light_matchGroup_0_alt;
  }

  .group1 {
    background: $light_matchGroup_1;
  }

  .group2 {
    background: $light_matchGroup_2;
  }

  .group3 {
    background: $light_matchGroup_3;
  }
}

:global(.dark) {
  .group0:nth-child(odd) {
    background: $dark_matchGroup_0;
  }

  .group0:nth-child(even) {
    background: $dark_matchGroup_0_alt;
  }

  .group1 {
    background: $dark_matchGroup_1;
  }

  .group2 {
    background: $dark_matchGroup_2;
  }

  .group3 {
    background: $dark_matchGroup_3;
  }
}
