.listItem{
  white-space:nowrap;
  overflow:hidden;
  -o-text-overflow:ellipsis;
     text-overflow:ellipsis;

  display:-ms-flexbox;

  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-align:center;
      align-items:center;
  overflow:hidden;
  line-height:1.25;
  cursor:pointer;
}

.disabled{
  opacity:0.3;
  pointer-events:none;
}

.disabled,
  .disabled *{
    color:inherit !important;
    font-weight:inherit !important;
  }
