.root{
  display:inline-block;
  text-transform:lowercase;
  letter-spacing:-1px;
  font-weight:100;
  font-size:120%;
}

  .root:hover{
    text-decoration:none;
  }

.root:hover{
  cursor:pointer;
}

.suffix{
  margin-top:3px;
  font-size:50%;
}

.narrow{
  display:none;
}

.wide{
  display:inline;
}

@media (max-width: 560px){
  .narrow{
    display:inline;
  }
  .wide{
    display:none;
  }
}

.regular .wide,.expressions .wide{
    margin-right:3px;
  }

.noClick{
  pointer-events:none;
}

:global(.light) .regular{
    color:#ffffff;
  }

:global(.light) .suffix{
    color:#ffffff;
  }

:global(.light) .expressions{
    color:#bae0ff;
  }

:global(.light) .root:hover .regular{
      color:rgba(255, 255, 255, 0.8);
    }

:global(.light) .root:hover .suffix{
      color:rgba(255, 255, 255, 0.8);
    }

:global(.light) .root:hover .expressions{
      color:rgba(186, 224, 255, 0.8);
    }

:global(.dark) .regular{
    color:#c1c1c1;
  }

:global(.dark) .suffix{
    color:#c1c1c1;
  }

:global(.dark) .expressions{
    color:#72a9d4;
  }

:global(.dark) .root:hover .regular{
      color:rgb(207, 207, 207);
    }

:global(.dark) .root:hover .suffix{
      color:rgb(207, 207, 207);
    }

:global(.dark) .root:hover .expressions{
      color:rgb(134, 182, 218);
    }
