.root {
  position: relative;
  float: left;
  box-sizing: border-box;
  padding: 10px 0;
  width: $innerLeftSidebarWidth;
  height: 100%;
}

.accountLink,
.link {
  text-decoration: none;
  cursor: pointer;
  outline: 0;
}

.accountLink {
  img {
    filter: grayscale(100%);
  }

  &.active .iconButton::after {
    @mixin fullscreen;

    content: '';
    box-shadow: inset 0 0 4px 2px rgba(0, 0, 0, 0.15);
    border-radius: 50%;
  }
}

.iconButton {
  margin: 0 auto 10px auto;
}

.donationButton {
  position: absolute;
  bottom: 0;
  left: 50%;
  margin: 0;
  transform: translateX(-50%);
}

.active img {
  filter: none;
}

/*
* Theme
*/

:global(.light) {
  .root {
    background: $light_innerSidebar_background;
    color: $light_innerSidebar_color;
  }

  .link {
    color: $light_innerSidebar_color;

    &.active {
      color: $light_innerSidebar_activeColor;

      .iconButton {
        background: $light_innerSidebar_activeBackground !important;
      }
    }
  }

  .iconButton {
    @mixin iconBtnTheme $light_innerSidebar_background, blackness;
  }

  .donationButton .iconButton:hover {
    background: $light_innerSidebar_dontationButton_hoverBackround;
    color: $light_innerSidebar_dontationButton_hoverColor;
  }
}

:global(.dark) {
  .root {
    background: $dark_innerSidebar_background;
    color: $dark_innerSidebar_color;
  }

  .link {
    color: $dark_innerSidebar_color;

    &.active {
      color: $dark_innerSidebar_activeColor;

      .iconButton {
        background: $dark_innerSidebar_activeBackground !important;
      }
    }
  }

  .iconButton {
    @mixin iconBtnTheme $dark_innerSidebar_background, lightness;
  }

  .donationButton .iconButton:hover {
    background: $dark_innerSidebar_dontationButton_hoverBackround;
    color: $dark_innerSidebar_dontationButton_hoverColor;
  }
}
