.regex{
  white-space:pre-wrap;
  word-wrap:break-word;
  word-break:break-all;
  overflow-wrap:break-word;

  font-size:0.9rem;
  line-height:1.9rem;
  margin-bottom:6px;
  position:relative;
}

  .regex .backtrack{
    position:relative;
    display:inline-block;
    vertical-align:top;
  }

  .regex .backtrack::before{
      content:'';
      height:0;
      width:0;
      border-top:3px solid transparent;
      border-bottom:3px solid transparent;
      border-right:8px solid transparent;
      display:inline-block;
      position:absolute;
      top:-2px;
      left:-2px;
    }

  .regex .backtrack::after{
      content:'';
      display:block;
      position:absolute;
      left:0;
      right:0;
      top:0;
      height:2px;
    }

.regexHighlight{
  outline:1px solid;
}

:global(.light) .regexHighlight{
    background:#129163;
    color:#edfff9;
    outline-color:#129163;
  }

:global(.light) .regex{
    color:#848484;
  }

:global(.light) .backtrack::before{
      border-right-color:#d20000;
    }

:global(.light) .backtrack::after{
      background:#d20000;
    }

:global(.dark) .regexHighlight{
    background:#129163;
    color:#edfff9;
    outline-color:#129163;
  }

:global(.dark) .regex{
    color:#848484;
  }

:global(.dark) .backtrack::before{
      border-right-color:#a22e2e;
    }

:global(.dark) .backtrack::after{
      background:#a22e2e;
    }
