.textInput {
  flex: 1 1 auto;
  font-family: $codeFont !important;
  resize: none;
}

.select {
  margin-bottom: 5px;
  width: auto;
}

.button {
  margin-top: 10px;
  text-align: right;
}

.notice {
  margin-right: 5px;
}

.notice,
.textInput,
.button,
.select {
  font-size: 0.8rem;
}

.saveBtn {
  margin-right: 10px;
}

.container {
  margin-bottom: 15px;
}

.resizable {
  resize: vertical;
}

.testResult {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 0;
  font-size: 0.8rem;
}

/*
 * Theme
 */

:global(.light) {
  .testResult.pass {
    background: $light_testList_testRow_pass_background;
    color: $light_testList_testRow_pass_color;
  }

  .testResult.fail {
    background: $light_testList_testRow_fail_background;
    color: $light_testList_testRow_fail_color;
  }
}

:global(.dark) {
  .testResult.pass {
    background: $dark_testList_testRow_pass_background;
    color: $light_testList_testRow_pass_color;
  }

  .testResult.fail {
    background: $dark_testList_testRow_fail_background;
    color: $dark_testList_testRow_fail_color;
  }
}
