.center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
}

.successContainer {
  font-size: 0.9rem;

  p {
    padding: 5px 0;
  }
}

.loading {
  margin-bottom: 20px;
  font-size: 0.9rem;
}

.success {
  margin-bottom: 15px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1rem;
}

.cancelButton {
  margin-left: 10px;
}

/*
 * Theme
 */

:global(.light) {
  .success {
    color: $light_submitToLibraryModal_success_color;
  }
}

:global(.dark) {
  .success {
    color: $dark_submitToLibraryModal_success_color;
  }
}
