.root{
  margin:0 0 7px 0;
  padding:0 0 4px 0;
  border-bottom:1px solid;
  text-transform:uppercase;
  font-weight:bold;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

.small{
  font-size:0.8rem;
}

.medium{
  font-size:0.9rem;
}

.large{
  font-size:1rem;
}

.overflow{
  white-space:nowrap;
  overflow:hidden;
  -o-text-overflow:ellipsis;
     text-overflow:ellipsis;
}

:global(.light) .root{
    border-color:rgba(58, 58, 58, 0.1);
    color:#344f71;
  }

:global(.dark) .root{
    border-color:rgba(156, 156, 156, 0.07);
    color:#797979;
  }
