.tokenSample {
  @mixin wordwrap;
}

.plainText,
.keyword,
.flag,
.info,
.tokenSample,
.zeroWidth,
.token {
  letter-spacing: 1px;
  font-family: $codeFont;
  white-space: pre-wrap;
}

.zeroWidth {
  border-right: 2px dotted #dc00ff;
  overflow: hidden;
  padding-left: 2px;
  margin-right: 2px;
}

.flag,
.boldNodeLabel,
.keyword {
  font-weight: bold;
}

.tokenExplanation {
  display: inline-block;
}

.nodeLabel,
.boldNodeLabel {
  @mixin wordwrap;
}

.flagDisabled {
  text-decoration: line-through;
}

.tooltip {
  display: inline-block;
  margin-left: 5px;
  opacity: 0.5;
  cursor: help;

  &:hover {
    opacity: 1;
  }
}

/*
 * Theme
 */

:global(.light) {
  .keyword {
    color: $light_tokenStyles_keyword_color;
  }

  .info {
    color: $light_tokenStyles_info_color;
  }

  .tokenSample {
    background: $light_tokenStyles_tokenSample_background;
    color: $light_tokenStyles_tokenSample_color;
  }

  .plainText {
    background: $light_tokenStyles_plainText_background;
    color: $light_tokenStyles_plainText_color;
  }

  .flag {
    color: $light_tokenStyles_flag_color;
  }

  .flags {
    color: $light_tokenStyles_flags_color;
  }

  .delimiter {
    color: $light_tokenStyles_delimiter_color;
  }

  .parentTokens {
    background: $light_tokenStyles_parentTokens_color;
  }

  .groupSuffix {
    color: $light_tokenStyles_groupSuffix_none_color;

    &.positive {
      color: $light_tokenStyles_groupSuffix_positive_color;
    }

    &.negative {
      color: $light_tokenStyles_groupSuffix_negative_color;
    }
  }
}

:global(.dark) {
  .keyword {
    color: $dark_tokenStyles_keyword_color;
  }

  .info {
    color: $dark_tokenStyles_info_color;
  }

  .tokenSample {
    background: $dark_tokenStyles_tokenSample_background;
    color: $dark_tokenStyles_tokenSample_color;
  }

  .plainText {
    background: $dark_tokenStyles_plainText_background;
    color: $dark_tokenStyles_plainText_color;
  }

  .flag {
    color: $dark_tokenStyles_flag_color;
  }

  .flags {
    color: $dark_tokenStyles_flags_color;
  }

  .delimiter {
    color: $dark_tokenStyles_delimiter_color;
  }

  .parentTokens {
    background: $dark_tokenStyles_parentTokens_color;
  }

  .groupSuffix {
    color: $dark_tokenStyles_groupSuffix_none_color;

    &.positive {
      color: $dark_tokenStyles_groupSuffix_positive_color;
    }

    &.negative {
      color: $dark_tokenStyles_groupSuffix_negative_color;
    }
  }
}
