.sponsor{
  position:relative;
}

.carbonLink,
.revoLink{
  position:relative;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  display:block;
}

.carbonLink strong,.revoLink strong{
    margin:5px 0;
    display:block;
  }

.carbonLink strong,
  .carbonLink .description,
  .revoLink strong,
  .revoLink .description{
    line-height:1.4;
  }

.sponsorText{
  position:absolute;
  top:-15px;
  padding:2px 6px;
  border-radius:3px;
  content:'Sponsor';
  text-transform:uppercase;
  font-weight:bold;
  font-size:0.7rem;
}

.carbonLink{
  padding:10px 0 0;
}

.revoLink{
  background:url('data:image/png;base64,');
  background-repeat:no-repeat;
  padding:5px 0 30px;
  background-size:30%;
  background-position:bottom center;
}

:global(.light) .sponsorText{
    background:#457abb;
    color:#f7f7f7;
  }

:global(.dark) .sponsorText{
    background:#2d5079;
    color:#ababab;
  }
