.root {
  display: flex;
  flex: 0 1 auto;
  flex-direction: column;
  margin-top: 10px;
  margin-bottom: -7px; /* Label adds 7px bottom margin, remove this when not visible */
  transition: flex $testareaSpeed $motionEasing;

  &.visible {
    flex: 1 1 auto;
    margin-bottom: 0;

    /* Fixes flex bug where it expands over the whole thing, in older chrome. */
    max-height: 50%;
  }
}

.inputArea {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  overflow: hidden;
  height: 100%;
}

.input {
  flex: 0 0 auto;
  margin-bottom: 5px;
}

.result {
  @mixin wordwrap;
  @mixin box;
  @mixin inputArea;
  @mixin overflowAuto;

  flex: 1 1 auto;
  line-height: 1.4em;
  outline: 0;
  font-size: 0.875rem;
  font-family: $codeFont;
  resize: none;
  position: relative;
  width: 100%;
  height: 100%;

  :global(.CodeMirror) {
    @mixin fullscreen 5px, 5px, 5px, 5px;
  }
}

@media (max-height: $substitutionAreaBreakpoint) {
  .root.visible {
    margin-top: 0;
  }
}

/*
 * Theme
 */

:global(.light) {
  .result {
    border-color: $light_textInput_borderColor;
    background: $light_textInput_disabled_background;
  }
}

:global(.dark) {
  .result {
    border-color: $dark_textInput_borderColor;
    background: $dark_textInput_disabled_background;
  }
}
