.regexPlayground {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-sizing: border-box;
  padding: 10px;
  height: 100%;
}

.regexArea {
  flex: 0 0 auto;
  margin-bottom: 10px;
}

.switchableArea {
  position: relative;
  flex: 1 1 auto;
  overflow: hidden;
}

.testStringArea {
  @mixin fullscreen;

  display: flex;
  flex-direction: column;
}
