.root {
  @mixin fullscreen;

  z-index: $fullscreenLayerZindex;
}

.finishedLoading {
  pointer-events: none;
}

.container {
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity $loadingScreenSpeed ease-out;
}

noscript,
.content {
  @mixin fullscreen;

  top: $headerHeight;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: auto;
  box-sizing: border-box;
  margin: 10px 0;
  padding: 0 10px;
  font-weight: 100;
  font-size: 1rem;
  user-select: none;
}

noscript {
  z-index: 1;
  background: $light_loadingScreen_background;
}

.text {
  margin-bottom: 20px;
}

/*
 * Transition
 */

.leave {
  opacity: 1;

}

.leaveActive {
  opacity: 0;
  pointer-events: none;
}

/*
* Theme
*/

:global(.light) {
  .container {
    background: $light_loadingScreen_background;
    color: $light_loadingScreen_color;
  }
}

:global(.dark) {
  .container {
    background: $dark_loadingScreen_background;
    color: $dark_loadingScreen_color;
  }
}
