.root{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -ms-flex-align:center;
      align-items:center;
  padding:10px 0;
  width:50px;
  height:100%;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
}

.iconBtn{
  margin-bottom:10px;
}

.iconBtn:last-of-type{
    margin-bottom:0;
  }

:global(.light) .root{
    background:#e1e1e1;
    color:#777777;
  }

:global(.light) .iconBtnTheme{
    background:rgb(214, 214, 214);
  }

:global(.light) .iconBtnTheme:hover{
  background:rgb(204, 204, 204);
}

:global(.light) .iconBtnTheme:active{
  background:rgb(195, 195, 195);
}

:global(.light) .iconBtn.active{
    background:#636363 !important;
    color:#e4e4e4 !important;
  }

:global(.dark) .root{
    background:#121212;
    color:#6f6f6f;
  }

:global(.dark) .iconBtnTheme{
    background:rgb(31, 31, 31);
  }

:global(.dark) .iconBtnTheme:hover{
  background:rgb(43, 43, 43);
}

:global(.dark) .iconBtnTheme:active{
  background:rgb(56, 56, 56);
}

:global(.dark) .iconBtn.active{
    background:#4e4e4e !important;
    color:#9e9e9e !important;
  }
