.exportMatch {
  @mixin genericButton;
}

/*
 * Theme
 */

:global(.light) {
  .root {
    background: $light_rightSidebar_contentArea_background;
  }

  .exportMatch {
    color: $light_exportMatchButton_color;
  }
}

:global(.dark) {
  .root {
    background: $dark_rightSidebar_contentArea_background;
  }

  .exportMatch {
    color: $dark_exportMatchButton_color;
  }
}
