.root {
  @mixin fullscreen;

  display: flex;
  flex-direction: column;
  font-size: 0.8rem;
}

.testingArea {
  flex: 1 1 auto;
  position: relative;
}

.switchAreaLabel {
  @mixin genericButton;
}

.caret {
  margin-right: 5px;
}

.label {
  flex: 0 0 auto;
}

/*
 * Theme
 */

:global(.light) {
  .switchAreaLabel {
    color: $light_unitTestArea_switchAreaLabel_color;
  }
}

:global(.dark) {
  .switchAreaLabel {
    color: $dark_unitTestArea_switchAreaLabel_color;
  }
}
