.matchResult {
  @mixin overflowAuto;

  overflow-x: hidden;
  font-family: $codeFont;
  transform: translate3d(0, 0, 0);
  position: relative;
}

.noGroupsExplain {
  margin-top: 10px;
}

.loadMoreButton {
  margin-top: 10px;
}

/*
 * Theme
 */

:global(.light) {
  .noMatch {
    color: $light_matchInfo_noMatch_color;
  }
}

:global(.dark) {
  .noMatch {
    color: $dark_matchInfo_noMatch_color;
  }
}
