.wrapper {
  @mixin box;

  font-size: 0.8rem;
  resize: none;
  transition: all $defaultSpeed ease;
  transition-property: border-color, box-shadow;
  position: relative;
  padding: 0;

  &.invalidInput {
    animation: shake 0.4s linear;
  }
}

.input {
  width: 100%;
  background: transparent;
  padding: 5px;
  margin: 0;
  outline: 0;
  border: none;
  font-size: 0.8rem;
  font-family: inherit;
  color: inherit;
  box-sizing: border-box;
}

.codeInput {
  font-family: $codeFont;
}

@keyframes shake {
  8%,
  41% {
    transform: translateX(-5px);
  }

  25%,
  58% {
    transform: translateX(5px);
  }

  75% {
    transform: translateX(-2px);
  }

  92% {
    transform: translateX(2px);
  }

  0%,
  100% {
    transform: translateX(0);
  }
}

/* Copy button */

.copyInput {
  padding-right: 35px;
  box-sizing: border-box;
}

.copyButton {
  border-left: 1px solid;
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  width: 30px;
  height: 100%;
  overflow: hidden;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}

/*
 * Theme
 */

:global(.light) {
  .wrapper {
    border-color: $light_textInput_borderColor;
    background: $light_textInput_background;
    color: $light_textInput_color;

    &.readOnly {
      background: $light_textInput_disabled_background;
      color: $light_textInput_disabled_color;
    }

    &.focus {
      border-color: $light_textInput_activeBorderColor;
      box-shadow: $light_textInput_activeBoxShadow !important;
    }

    .copyButton {
      border-color: $light_textInput_borderColor;
      background-image: linear-gradient(
        color($light_textInput_background lightness(+5%)),
        color($light_textInput_background blackness(+10%))
      );

      &:hover {
        background-image: linear-gradient(
          color($light_textInput_background blackness(+8%)),
          color($light_textInput_background blackness(+15%))
        );
      }

      &:active {
        background-color: color($light_textInput_background blackness(+10%));
        background-image: none;
        box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15);
      }
    }
  }

  .wrapper.error {
    border-color: $light_textInput_error_borderColor;
    background: $light_textInput_error_background;
    color: $light_textInput_error_color;

    &.focus {
      border-color: $light_textInput_error_activeBorderColor;
      box-shadow: $light_textInput_error_activeBoxShadow !important;
    }

    .copyButton {
      border-color: $light_textInput_error_borderColor;
      background-image: linear-gradient(
        color($light_textInput_error_background lightness(+5%)),
        color($light_textInput_error_background blackness(+10%))
      );

      &:hover {
        background-image: linear-gradient(
          color($light_textInput_error_background blackness(+8%)),
          color($light_textInput_error_background blackness(+15%))
        );
      }

      &:active {
        background-color: color(
          $light_textInput_error_background blackness(+10%)
        );
        background-image: none;
        box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15);
      }
    }
  }
}

:global(.dark) {
  .wrapper {
    border-color: $dark_textInput_borderColor;
    background: $dark_textInput_background;
    color: $dark_textInput_color;

    &.readOnly {
