      box-shadow: $dark_textInput_activeBoxShadow !important;
    }

    .copyButton {
      border-color: $dark_textInput_borderColor;
      background-image: linear-gradient(
        color($dark_textInput_background lightness(+5%)),
        $dark_textInput_background
      );

      &:hover {
        background-image: linear-gradient(
          color($dark_textInput_background lightness(+7%)),
          color($dark_textInput_background lightness(+2%))
        );
      }

      &:active {
        background-color: color($dark_textInput_background lightness(+2%));
        background-image: none;
        box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15);
      }
    }
  }

  .wrapper.error {
    border-color: $dark_textInput_error_borderColor;
    background: $dark_textInput_error_background;
    color: $dark_textInput_error_color;

    &.focus {
      border-color: $dark_textInput_error_activeBorderColor;
      box-shadow: $dark_textInput_error_activeBoxShadow !important;
