.root {
  @mixin box;

  padding: 10px;
  font-size: 0.8rem;
}

/*
 * Theme
 */

:global(.light) {
  .root {
    border-color: $light_basicContainer_borderColor;
    background: $light_basicContainer_background;
  }
}

:global(.dark) {
  .root {
    border-color: $dark_basicContainer_borderColor;
    background: $dark_basicContainer_background;
  }
}
