.root {
  margin: 0 0 7px 0;
  padding: 0 0 4px 0;
  border-bottom: 1px solid;
  text-transform: uppercase;
  font-weight: bold;
  user-select: none;
}

.small {
  font-size: 0.8rem;
}

.medium {
  font-size: 0.9rem;
}

.large {
  font-size: 1rem;
}

.overflow {
  @mixin textOverflow;
}

/*
* Theme
*/

:global(.light) {
  .root {
    border-color: $light_label_borderColor;
    color: $light_label_color;
  }
}

:global(.dark) {
  .root {
    border-color: $dark_label_borderColor;
    color: $dark_label_color;
  }
}
