.stringMatch{
  padding:0 2px;
}

.stringArea{
  white-space:pre-wrap;
  word-wrap:break-word;
  word-break:break-all;
  overflow-wrap:break-word;
}

:global(.light) .backtrack{
    background:#d20000;
    color:#edfff9;
  }

:global(.light) .stringMatch{
    background:#c6e3ff;
  }

:global(.dark) .backtrack{
    background:#a22e2e;
    color:#edfff9;
  }

:global(.dark) .stringMatch{
    background:#084b8c;
  }
