.center{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack:center;
      justify-content:center;
  height:100%;
  text-align:center;
}

.successContainer{
  font-size:0.9rem;
}

.successContainer p{
    padding:5px 0;
  }

.loading{
  margin-bottom:20px;
  font-size:0.9rem;
}

.success{
  margin-bottom:15px;
  text-transform:uppercase;
  font-weight:bold;
  font-size:1rem;
}

.cancelButton{
  margin-left:10px;
}

:global(.light) .success{
    color:#357b35;
  }

:global(.dark) .success{
    color:#357b35;
  }
