.root {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: $testControllerHeight;
  padding: 0 10px;
  border: 1px solid;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  text-align: right;
  box-shadow: 0 -3px 10px -5px rgba(0, 0, 0, 0.15);
  z-index: 10;
}

.progress {
  position: absolute;
  top: -1px;
  left: 0;
  height: 3px;
}

.buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;

  .left {
    flex: 0 0 auto;
  }
  .right {
    flex: 1 1 auto;
    text-align: right;
  }
}

.addTest {
  margin-left: 10px;
}

/*
 * Theme
 */

:global(.light) {
  .root {
    border-color: $light_basicContainer_borderColor $light_testController_borderColor $light_testController_borderColor;
    background: $light_sidebar_background;
  }
  .progress {
    background: $light_theme_color;
  }
}

:global(.dark) {
  .root {
    border-color: $dark_basicContainer_borderColor $dark_testController_borderColor $dark_testController_borderColor;
    background: $dark_sidebar_background;
  }
  .progress {
    background: $dark_theme_color;
  }
}
