.regex{
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
  margin-top:10px;
  border-radius:3px;
  padding:4px 8px;
}

:global(.light) .regex{
    background:rgba(0, 0, 0, 0.04);
  }

:global(.light) .tooltip{
    background:rgba(0, 0, 0, 0.8);
    color:#ffffff;
  }

:global(.dark) .regex{
    background:rgba(0, 0, 0, 0.04);
  }

:global(.dark) .tooltip{
    background:rgba(0, 0, 0, 0.8);
    color:#cacaca;
  }
