.modalContainer{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  z-index:1001;
}

.modalWrapper{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  display:-ms-flexbox;

  display:flex;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack:center;
      justify-content:center;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  padding:20px;
  width:100%;
  height:100%;
  -webkit-transition:opacity 0.2s ease-out;
  -o-transition:opacity 0.2s ease-out;
  transition:opacity 0.2s ease-out;
  will-change:opacity;
  opacity:1;
}

.modalBackdrop{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
}

.modal{
  position:relative;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  overflow:hidden;
  max-width:900px;
  max-height:550px;
  width:100%;
  height:100%;
  border-radius:3px;
  opacity:1;
  -webkit-transition:-webkit-transform 0.2s cubic-bezier(0.6, 0.4, 0.2, 1.4);
  transition:-webkit-transform 0.2s cubic-bezier(0.6, 0.4, 0.2, 1.4);
  -o-transition:transform 0.2s cubic-bezier(0.6, 0.4, 0.2, 1.4);
  transition:transform 0.2s cubic-bezier(0.6, 0.4, 0.2, 1.4);
  transition:transform 0.2s cubic-bezier(0.6, 0.4, 0.2, 1.4), -webkit-transform 0.2s cubic-bezier(0.6, 0.4, 0.2, 1.4);
  -webkit-transform:scale(1);
          transform:scale(1);
  -webkit-transform-origin:center center;
          transform-origin:center center;
  -webkit-user-select:text;
     -moz-user-select:text;
      -ms-user-select:text;
          user-select:text;
  will-change:transform;
}

.closeButton{
  position:absolute;
  top:0;
  right:0;
  z-index:1;
  width:65px;
  height:65px;
  text-align:center;
  font-size:1.1em;
  line-height:65px;
  cursor:pointer;
}

.enter{
  opacity:0;
  will-change:transform, opacity;
}

.enter .modal{
    -webkit-transform:scale(0.95);
            transform:scale(0.95);
  }

.enterActive{
  opacity:1;
}

.enterActive .modal{
    -webkit-transform:scale(1);
            transform:scale(1);
    pointer-events:none;
  }

.leave{
  opacity:1;
}

.leave .modal{
    -webkit-transform:scale(1);
            transform:scale(1);
  }

.leaveActive{
  opacity:0;
  pointer-events:none;
}

.leaveActive .modal{
    -webkit-transform:scale(0.95);
            transform:scale(0.95);
  }

:global(.light) .closeButton{
    color:#abc3e0;
  }

:global(.light) .closeButton:hover{
      color:rgb(255, 255, 255);
    }

:global(.light) .closeButton:active{
      color:rgb(225, 234, 244);
    }

:global(.light) .modalBackdrop{
    background:hsla(0, 0%, 40%, 0.6);
  }

:global(.light) .modal{
    -webkit-box-shadow:0 5px 30px rgba(93, 93, 93, 0.6);
            box-shadow:0 5px 30px rgba(93, 93, 93, 0.6);
    color:#333333;
  }

:global(.dark) .closeButton{
    color:#6f92bb;
  }

:global(.dark) .closeButton:hover{
      color:rgb(213, 224, 235);
    }

:global(.dark) .closeButton:active{
      color:rgb(161, 184, 211);
    }

:global(.dark) .modalBackdrop{
    background:hsla(0, 0%, 40%, 0.6);
  }

:global(.dark) .modal{
    -webkit-box-shadow:0 5px 30px hsla(0, 0%, 20%, 0.6);
            box-shadow:0 5px 30px hsla(0, 0%, 20%, 0.6);
    color:#ababab;
  }
