.wrapper {
  @mixin fullscreen;
}

.entries {
  @mixin fullscreen;
  @mixin fetchDataContainer;

  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 10px;
}

.error {
  align-items: center;
  justify-content: center;
}

.label {
  flex: 0 0 auto;
}

.dataContainer {
  @mixin overflowAuto;

  flex: 1 1 auto;
  padding: 10px;
}

.searchInput {
  flex: 0 0 auto;
  margin-bottom: 10px;
}

.paginator {
  flex: 0 0 auto;
}

/*
 * Theme
 */

:global(.light) {
  .libraryEntry:nth-child(odd) {
    background: $light_regexLibrary_libraryEntry_background;
  }
}

:global(.dark) {
  .libraryEntry:nth-child(odd) {
    background: $dark_regexLibrary_libraryEntry_background;
  }
}
