.modalContainer {
  @mixin fullscreen;

  z-index: $modalZIndex;
}

.modalWrapper {
  @mixin fullscreen;

  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding: 20px;
  width: 100%;
  height: 100%;
  transition: opacity $modalSpeed ease-out;
  will-change: opacity;
  opacity: 1;
}

.modalBackdrop {
  @mixin fullscreen;
}

.modal {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  max-width: $maxModalWidth;
  max-height: $maxModalHeight;
  width: 100%;
  height: 100%;
  border-radius: 3px;
  opacity: 1;
  transition: transform $modalSpeed $jumpyEasing;
  transform: scale(1);
  transform-origin: center center;
  user-select: text;
  will-change: transform;
}

.closeButton {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  width: $modalHeaderHeight;
  height: $modalHeaderHeight;
  text-align: center;
  font-size: 1.1em;
  line-height: $modalHeaderHeight;
  cursor: pointer;
}

/*
 * Transition
 */

.enter {
  opacity: 0;
  will-change: transform, opacity;

  .modal {
    transform: scale(0.95);
  }
}

.enterActive {
  opacity: 1;

  .modal {
    transform: scale(1);
    pointer-events: none;
  }
}

.leave {
  opacity: 1;

  .modal {
    transform: scale(1);
  }
}

.leaveActive {
  opacity: 0;
  pointer-events: none;

  .modal {
    transform: scale(0.95);
  }
}

/*
 * Theme
 */

:global(.light) {
  .closeButton {
    color: $light_modal_closeButton_color;

    &:hover {
      color: $light_modal_closeButton_hoverColor;
    }
    &:active {
      color: $light_modal_closeButton_activeColor;
    }
  }
  .modalBackdrop {
    background: $light_modal_backdrop_background;
  }
  .modal {
    box-shadow: 0 5px 30px $light_modal_boxShadow_color;
    color: $light_globalApp_color;
  }
}

:global(.dark) {
  .closeButton {
    color: $dark_modal_closeButton_color;

    &:hover {
      color: $dark_modal_closeButton_hoverColor;
    }
    &:active {
      color: $dark_modal_closeButton_activeColor;
    }
  }
  .modalBackdrop {
    background: $dark_modal_backdrop_background;
  }
  .modal {
    box-shadow: 0 5px 30px $dark_modal_boxShadow_color;
    color: $dark_globalApp_color;
  }
}
