.root {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  /*
   * Prevent canvas from expanding too far
   * This seems to happen if you paste something into the regex area.
   * Since that takes up more space but no match was made, the canvas doesn't update. This fixes it until that happens.
   */
  overflow: hidden;
  height: 0;
  transition: flex $testareaSpeed $motionEasing;

  :global(.CodeMirror) {
    height: 100%;
  }
}

.input {
  @mixin fullscreen;

  z-index: $testAreaZindex;
  padding: $testAreaPadding;
}

.inputArea {
  @mixin box;
  @mixin inputArea;

  position: relative;
  flex: 1 0 auto;
  font-size: 0.875rem;
  font-family: $codeFont;
}

@media (max-height: $substitutionAreaBreakpoint) {
  .substitutionVisible {
    flex: 0 1 auto;
    overflow: hidden;
  }
}

.switchAreaLabel {
  @mixin genericButton;
}

.caret {
  margin-left: 5px;
}

/*
 * Theme
 */

:global(.light) {
  .inputArea {
    border-color: $light_textInput_borderColor;
    background: $light_textInput_background;

    &.editorFocused {
      border-color: $light_textInput_activeBorderColor;
      box-shadow: $light_textInput_activeBoxShadow;
    }
  }
  .switchAreaLabel {
    color: $light_testArea_switchArealabel_color;
  }
}

:global(.dark) {
  .inputArea {
    border-color: $dark_textInput_borderColor;
    background: $dark_textInput_background;

    &.editorFocused {
      border-color: $dark_textInput_activeBorderColor;
      box-shadow: $dark_textInput_activeBoxShadow;
    }
  }
  .switchAreaLabel {
    color: $dark_testArea_switchArealabel_color;
  }
}
