.link{
  text-decoration:none;
}

.theme:hover{
  text-decoration:underline;
}

:global(.light) .theme{
    color:#457abb;
  }

:global(.light) .theme:visited{
      color:#457abb;
    }

:global(.light) .theme:hover{
      color:#457abb;
    }

:global(.light) .theme:active{
      color:rgb(69, 87, 110)
    }

:global(.dark) .theme{
    color:#36577d;
  }

:global(.dark) .theme:visited{
      color:#36577d;
    }

:global(.dark) .theme:hover{
      color:#36577d;
    }

:global(.dark) .theme:active{
      color:rgb(69, 112, 161)
    }
