.matchHeader{
  padding-bottom:5px;
  text-align:left;
  white-space:nowrap;
}

.contentWrapper{
  margin-bottom:5px;
  padding-bottom:5px;
  border-bottom:1px solid;
}

.matchTable{
  table-layout:auto;
  width:100%;
  border-collapse:separate;
  border-spacing:0 5px;
}

@media (max-width: 500px){
  .matchTable{
    table-layout:fixed;
  }
}

:global(.light) .matchHeader{
    color:#4e7bb3;
  }

:global(.light) .contentWrapper{
    border-color:#f1f1f1;
  }

:global(.dark) .matchHeader{
    color:#4e7bb3;
  }

:global(.dark) .contentWrapper{
    border-color:#272727;
  }
