.root{
  position:absolute;
  top:10px;
  right:10px;
  bottom:10px;
  left:10px;

  display:-ms-flexbox;

  display:flex;
}

.contents{
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  white-space:pre;
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
  position:relative;
  line-height:1.3;
  -ms-flex:1 1 auto;
      flex:1 1 auto;
}

.copyButton{
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  padding:5px;
  opacity:0.4;

  font-size:1rem;
}

.copyButton:hover{
  opacity:0.6;
}

.copyButton:active{
  opacity:0.8;
}

.contentWrapper{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  overflow:hidden;
  height:100%;
}

.includeFullMatch{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
  padding-top:5px;
  vertical-align:middle;
}

@media (min-width: 900px){
  .root{
    -ms-flex-direction:row;
        flex-direction:row;
  }

  .dataContainer{
    -ms-flex:1 1 auto;
        flex:1 1 auto;
  }

  .listMenu{
    padding-right:20px;
    -ms-flex:0 0 auto;
        flex:0 0 auto;
    min-width:175px;
  }
}

@media (max-width: 900px){
  .root{
    -ms-flex-direction:column;
        flex-direction:column;
  }

  .dataContainer{
    margin-top:10px;
    -ms-flex:3 1 auto;
        flex:3 1 auto;
  }

  .listMenu{
    -ms-flex:1 1 auto;
        flex:1 1 auto;
  }
}
