.sidebar{
  position:absolute;
  top:0;
  bottom:0;
  left:0;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  width:270px;
  border-right:1px solid;
  -webkit-transition:-webkit-transform 0.25s ease;
  transition:-webkit-transform 0.25s ease;
  -o-transition:transform 0.25s ease;
  transition:transform 0.25s ease;
  transition:transform 0.25s ease, -webkit-transform 0.25s ease;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  will-change:transform;
}

.sidebarContents{
  margin-left:50px;
  padding:10px 10px 10px 0;
}

.menuHamburgerParent{
  top:0;
  left:0;
}

.collapsedSidebar{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  z-index:1000;
}

.header{
  -ms-flex:1 0 50px;
      flex:1 0 50px;
  margin-right:-1px;
  padding-right:1px;
  height:50px;
  line-height:50px;
  width:100%;
  text-align:center;
}

.sidebarBackdrop{
  width:100%;
  height:100%;
  opacity:1;
  -webkit-transition:opacity 0.25s ease-in-out;
  -o-transition:opacity 0.25s ease-in-out;
  transition:opacity 0.25s ease-in-out;
  will-change:opacity;
}

.contents{
  height:calc(100% - 50px);
}

.enter{
  opacity:0;
  will-change:transform, opacity;
}

.enter .sidebar{
    -webkit-transform:translateX(-270px);
            transform:translateX(-270px);
  }

.enterActive{
  opacity:1;
  pointer-events:none;
}

.enterActive .sidebar{
    -webkit-transform:translateX(0);
            transform:translateX(0);
  }

.leave{
  opacity:1;
}

.leave .sidebar{
    -webkit-transform:translateX(0);
            transform:translateX(0);
  }

.leaveActive{
  opacity:0;
  pointer-events:none;
}

.leaveActive .sidebar{
    -webkit-transform:translateX(-270px);
            transform:translateX(-270px);
  }

:global(.light) .header{
    border-color:#d5dfeb;
    background:#457abb;
    color:#ffffff;
  }

:global(.light) .sidebar{
    border-color:#ffffff;
    background:#f7f7f7;
    -webkit-box-shadow:0 5px 30px rgba(93, 93, 93, 0.6);
            box-shadow:0 5px 30px rgba(93, 93, 93, 0.6);
    color:#333333;
  }

:global(.light) .menuHamburger{
    background:rgb(87, 135, 193);

    color:#ffffff;
  }

:global(.light) .menuHamburger:hover{
  background:rgb(106, 148, 200);
}

:global(.light) .menuHamburger:active{
  background:rgb(125, 162, 207);
}

:global(.light) .sidebarBackdrop{
    background:hsla(0, 0%, 40%, 0.6);
  }

:global(.dark) .header{
    border-color:#345b88;
    background:#2d5079;
    color:#c1c1c1;
  }

:global(.dark) .sidebar{
    border-color:#212121;
    background:#1a1a1a;
    -webkit-box-shadow:0 5px 30px hsla(0, 0%, 20%, 0.6);
            box-shadow:0 5px 30px hsla(0, 0%, 20%, 0.6);
    color:#ababab;
  }

:global(.dark) .menuHamburger{
    background:rgb(52, 94, 141);

    color:#c1c1c1;
  }

:global(.dark) .menuHamburger:hover{
  background:rgb(59, 106, 160);
}

:global(.dark) .menuHamburger:active{
  background:rgb(66, 119, 179);
}

:global(.dark) .sidebarBackdrop{
    background:hsla(0, 0%, 40%, 0.6);
  }
