.error,
.warn,
.info{
  font-size:0.725rem;
  line-height:1.5;
}

:global(.light) .info{
    color:#676767;
  }

:global(.light) .warn{
    color:#daa520;
  }

:global(.light) .error{
    color:#b54d4d;
  }

:global(.dark) .info{
    color:#565656;
  }

:global(.dark) .warn{
    color:#a07f2b;
  }

:global(.dark) .error{
    color:#b54d4d;
  }
