@import 'highlight.js.css';

.root {
  max-width: 1000px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.container {
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
  flex: 1 1 auto;
  overflow: hidden;
}

.language {
  flex: 0 0 auto;
  padding-right: 20px;
  min-width: 200px;
}

.codeContainer {
  flex: 2 1 auto;
}

.code {
  @mixin wordwrap;
  @mixin overflowAuto;

  position: relative;
  font-family: $codeFont;
  max-height: 100%;

  code {
    overflow: visible;
    margin: 0;
    padding: 0;
    line-height: 1.3;
  }
}

.copyButton {
  @mixin genericButton;

  font-size: 1rem;
}

.referenceUrl {
  @mixin wordwrap;
}

.infoNotice {
  flex: 0 0 auto;
  text-align: center;
  line-height: 1.3em;
}

@media (max-width: $fullscreenBreakpoint) {
  .container {
    padding: 0;
  }

  .container {
    flex-direction: column;
  }

  .language {
    flex: 1 1 auto;
    margin-bottom: 10px;
    padding: 0;
  }
}
