.root {
  display: flex;
  width: 100%;
}

.centered {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-align: center;
}

.loadingText {
  margin-bottom: 20px;
  font-weight: 200;
}

.matchSelector {
  @mixin overflowAuto;
}

@media (max-width: $fullscreenBreakpoint) {
  .root {
    flex-direction: column;
  }

  .matchSelector {
    flex: 1 1 auto;
    margin-bottom: 20px;
    min-height: 60px;
  }

  .debugResult {
    flex: 5 1 auto;
  }
}

@media (min-width: $fullscreenBreakpoint) {
  .matchSelector {
    flex: 0 0 auto;
    padding-right: 20px;
    min-width: 300px;
  }

  .debugResult {
    flex: 1 1 auto;
  }
}

/*
 * Theme
 */

:global(.light) {
  .match {
    color: $light_regexDebugger_match_color;
  }

  .noMatch {
    color: $light_regexDebugger_noMatch_color;
  }
}

:global(.dark) {
  .match {
    color: $dark_regexDebugger_match_color;
  }

  .noMatch {
    color: $dark_regexDebugger_noMatch_color;
  }
}
