.centered {
  @mixin fullscreen;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.note {
  margin-top: 10px;
  line-height: 1.25;
}

.input {
  font-size: 0.8rem;
}

.paragraph {
  margin: 10px 0;
  font-size: 0.85rem;
}

.accountData {
  margin-top: 20px;
}

/*
 * Theme
 */

:global(.light) {
  .centered {
    background: $light_modalBody_background;
  }
}

:global(.dark) {
  .centered {
    background: $dark_modalBody_background;
  }
}
