.root{
  position:relative;
  border-radius:3px;
}

.entryButton{
  display:block;
  padding:10px;
  padding-left:35px;
  border-radius:3px;
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

.voteButton{
  position:absolute;
  top:50%;
  -webkit-transform:translateY(-50%);
          transform:translateY(-50%);

  left:0;
  float:left;
}

.title{
  white-space:nowrap;
  overflow:hidden;
  -o-text-overflow:ellipsis;
     text-overflow:ellipsis;

  padding-bottom:10px;
  font-weight:bold;
}

.flavor{
  float:right;
  margin-left:5px;
  text-transform:lowercase;
  font-weight:200;
}

.clear{
  overflow:auto;
}

.description{
  white-space:pre-wrap;
}

.dateAndAuthor{
  white-space:nowrap;
  overflow:hidden;
  -o-text-overflow:ellipsis;
     text-overflow:ellipsis;

  padding-bottom:5px;
  text-transform:lowercase;
}

.noDescription{
  font-style:italic;
}

:global(.light) .root:nth-child(odd){
    background:#f7f7f7;
  }

:global(.light) .entryButton:active{
      background:#636363;
      color:#e4e4e4;
    }

:global(.light) .entryButton:not(:active) .author{
        color:#8a5e5e;
      }

:global(.light) .entryButton:not(:active) .noDescription{
        color:#a2a2a2;
      }

:global(.light) .entryButton:not(:active) .dateAndAuthor{
        color:#a2a2a2;
      }

:global(.light) .entryButton:not(:active) .title{
        color:#0b3c79;
      }

:global(.light) .entryButton:not(:active):hover .title{
        color:rgb(8, 106, 226);
      }

:global(.dark) .root:nth-child(odd){
    background:#232323;
  }

:global(.dark) .entryButton:active{
      background:#4e4e4e;
      color:#9e9e9e;
    }

:global(.dark) .entryButton:not(:active) .author{
        color:#885d5d;
      }

:global(.dark) .entryButton:not(:active) .noDescription{
        color:#545454;
      }

:global(.dark) .entryButton:not(:active) .dateAndAuthor{
        color:#545454;
      }

:global(.dark) .entryButton:not(:active) .title{
        color:#36577d;
      }

:global(.dark) .entryButton:not(:active):hover .title{
        color:rgb(83, 136, 198);
      }
