.treeviewArrow {
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 6px solid;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  transition: all 0.1s;

  &.collapsed {
    transform: rotate(-90deg);
  }
}

.treeview {
  overflow-y: hidden;
}

.treeviewLabel {
  display: flex;
  flex-direction: row;
}

.treeviewChildren {
  margin-left: 20px;

  &.collapsed {
    height: 0;
  }
}

.treeviewArrowContainer {
  flex: 0 0 20px;
  float: left;
  width: 20px;
  text-align: center;
  cursor: pointer;
  user-select: none;
}

/*
 * Theme
 */

:global(.light) {
  .treeviewArrow {
    border-top-color: $light_treeview_arrow_color;
  }
  .treeviewArrowContainer:hover .treeviewArrow {
    border-top-color: $light_treeview_arrow_hoverColor;
  }
}

:global(.dark) {
  .treeviewArrow {
    border-top-color: $dark_treeview_arrow_color;
  }
  .treeviewArrowContainer:hover .treeviewArrow {
    border-top-color: $dark_treeview_arrow_hoverColor;
  }
}
