.root{
  width:100%;
  height:100%;
}

.contentArea{
  position:absolute;
  top:50px;
  right:0;
  bottom:0;
  left:270px;
}

.contentArea.alwaysCollapseLeftSidebar{
    left:0;
  }

@media (max-width: 1200px){
  .contentArea{
    left:0;
  }
}

:global(.light) .root{
    background:#ebebeb;
    color:#333333;
  }

:global(.dark) .root{
    background:#1f1f1f;
    color:#ababab;
  }
