.modalHeader {
  flex: 0 0 $modalHeaderHeight;
  margin: 0;
  padding: 0 15px;
  height: $modalHeaderHeight;
  font-weight: bold;
  font-size: 1rem;
  line-height: $modalHeaderHeight;
  user-select: none;

  .titleIcon {
    margin-right: 10px;
    font-size: 1.1em;
  }
}

/*
 * Theme
 */

:global(.light) {
  .modalHeader {
    background: $light_modalHeader_background;
    color: $light_modalHeader_color;
  }
  .titleIcon {
    color: $light_modalHeader_titleIcon_color;
  }
}

:global(.dark) {
  .modalHeader {
    background: $dark_modalHeader_background;
    color: $dark_modalHeader_color;
  }
  .titleIcon {
    color: $dark_modalHeader_titleIcon_color;
  }
}
