.root{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  float:right;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  padding:10px;
  max-width:50vw;
  width:50%;
  height:100%;
  border-left:1px solid;
  -webkit-box-shadow:-3px 0 10px -5px rgba(0, 0, 0, 0.15);
          box-shadow:-3px 0 10px -5px rgba(0, 0, 0, 0.15);
  position:relative;
}

.resizer{
  width:8px;
  position:absolute;
  top:0;
  left:0;
  bottom:0;
  cursor:ew-resize;
  pointer-events:auto;
  z-index:10;
}

@media (max-width: 2200px){
  .root{
    width:40%;
  }
}

:global(.light) .root{
    border-color:#ffffff;
    background:#f7f7f7;
  }

:global(.dark) .root{
    border-color:#212121;
    background:#1a1a1a;
  }
