.menu{
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  -webkit-box-sizing:border-box;

          box-sizing:border-box;
  padding:5px 0;
  min-width:120px;
  max-height:100vh;
  border:1px solid;
  border-radius:3px;
  -webkit-box-shadow:0 0 10px 3px rgba(0, 0, 0, 0.2);
          box-shadow:0 0 10px 3px rgba(0, 0, 0, 0.2);
  white-space:nowrap;
  font-size:0.8rem;
}

:global(.light) .menu{
    border-color:rgba(0, 0, 0, 0.15);
    background:#f7f7f7;
    color:#333333;
  }

:global(.dark) .menu{
    border-color:rgba(0, 0, 0, 0.15);
    background:#444444;
    color:#bbbbbb;
  }
