.regex {
  @mixin wordwrap;

  font-size: 0.9rem;
  line-height: 1.9rem;
  margin-bottom: 6px;
  position: relative;

  .backtrack {
    position: relative;
    display: inline-block;
    vertical-align: top;

    &::before {
      content: '';
      height: 0;
      width: 0;
      border-top: 3px solid transparent;
      border-bottom: 3px solid transparent;
      border-right: 8px solid transparent;
      display: inline-block;
      position: absolute;
      top: -2px;
      left: -2px;
    }

    &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      height: 2px;
    }
  }
}

.regexHighlight {
  outline: 1px solid;
}

/*
 * Theme
 */

:global(.light) {
  .regexHighlight {
    background: $light_debugStep_regexHighlight_background;
    color: $light_debugStep_regexHighlight_color;
    outline-color: $light_debugStep_regexHighlight_background;
  }

  .regex {
    color: $light_debugStep_regex_color;
  }

  .backtrack {
    &::before {
      border-right-color: $light_debugStep_backtrackIcon_color;
    }

    &::after {
      background: $light_debugStep_backtrackIcon_color;
    }
  }
}

:global(.dark) {
  .regexHighlight {
    background: $dark_debugStep_regexHighlight_background;
    color: $dark_debugStep_regexHighlight_color;
    outline-color: $dark_debugStep_regexHighlight_background;
  }

  .regex {
    color: $dark_debugStep_regex_color;
  }

  .backtrack {
    &::before {
      border-right-color: $dark_debugStep_backtrackIcon_color;
    }

    &::after {
      background: $dark_debugStep_backtrackIcon_color;
    }
  }
}
