.root {
  position: relative;
  border-radius: 3px;
}

.entryButton {
  display: block;
  padding: 10px;
  padding-left: 35px;
  border-radius: 3px;
  cursor: pointer;
  user-select: none;
}

.voteButton {
  @mixin centerChildY;

  left: 0;
  float: left;
}

.title {
  @mixin textOverflow;

  padding-bottom: 10px;
  font-weight: bold;
}

.flavor {
  float: right;
  margin-left: 5px;
  text-transform: lowercase;
  font-weight: 200;
}

.clear {
  overflow: auto;
}

.description {
  white-space: pre-wrap;
}

.dateAndAuthor {
  @mixin textOverflow;

  padding-bottom: 5px;
  text-transform: lowercase;
}

.noDescription {
  font-style: italic;
}

/*
 * Theme
 */

:global(.light) {
  .root:nth-child(odd) {
    background: $light_libraryEntry_background;
  }
  .entryButton {
    &:active {
      background: $light_libraryEntry_entryButton_activeBackground;
      color: $light_libraryEntry_entryButton_activeColor;
    }
    &:not(:active) {
      .author {
        color: $light_libraryEntry_author_color;
      }
      .noDescription {
        color: $light_libraryEntry_noDescription_color;
      }
      .dateAndAuthor {
        color: $light_libraryEntry_dateAndAuthor_color;
      }
      .title {
        color: $light_libraryEntry_title_color;
      }
      &:hover .title {
        color: $light_libraryEntry_title_hoverColor;
      }
    }
  }

}

:global(.dark) {
  .root:nth-child(odd) {
    background: $dark_libraryEntry_background;
  }
  .entryButton {
    &:active {
      background: $dark_libraryEntry_entryButton_activeBackground;
      color: $dark_libraryEntry_entryButton_activeColor;
    }
    &:not(:active) {
      .author {
        color: $dark_libraryEntry_author_color;
      }
      .noDescription {
        color: $dark_libraryEntry_noDescription_color;
      }
      .dateAndAuthor {
        color: $dark_libraryEntry_dateAndAuthor_color;
      }
      .title {
        color: $dark_libraryEntry_title_color;
      }
      &:hover .title {
        color: $dark_libraryEntry_title_hoverColor;
      }
    }
  }

}
