.content {
  position: relative;
  display: flex;
  flex: 1 0 auto;
  flex-direction: row;
  box-sizing: border-box;
  width: 100%;

  &.normal {
    padding: 6px 10px;
    border-radius: 3px;
  }
  &.extendFromLeft {
    padding: 6px 10px 6px 20px;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
  }
  &.extendFromRight {
    padding: 6px 20px 6px 10px;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
}


/*
 * Theme
 */

:global(.light) {
  .content {
    color: $light_listMenu_defaultContent_button_color;

    &:hover {
      background: $light_listMenu_defaultContent_button_hoverBackground;
      color: $light_listMenu_defaultContent_button_hoverColor;
    }
    &:active {
      background: $light_listMenu_defaultContent_button_activeBackground;
      color: $light_listMenu_defaultContent_button_activeColor;
    }
  }
}

:global(.dark) {
  .content {
    color: $dark_listMenu_defaultContent_button_color;

    &:hover {
      background: $dark_listMenu_defaultContent_button_hoverBackground;
      color: $dark_listMenu_defaultContent_button_hoverColor;
    }
    &:active {
      background: $dark_listMenu_defaultContent_button_activeBackground;
      color: $dark_listMenu_defaultContent_button_activeColor;
    }
  }
}
