.tag{
  position:relative;
  display:inline-block;
  margin:0 5px 2px 0;
  padding:0 10px;
  border-radius:50px;
  -webkit-transition:background 0.2s ease;
  -o-transition:background 0.2s ease;
  transition:background 0.2s ease;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
}

  .tag.deletable{
    padding-right:30px;
  }

  .tag.clickable{
    cursor:pointer;
    white-space:nowrap;
    overflow:hidden;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    max-width:199px;
  }

.deleteButton{
  position:absolute;
  top:0;
  right:5px;
  padding:0 5px;
  opacity:0.5;
  cursor:pointer;
}

.deleteButton:hover{
    opacity:0.9;
  }

.mediumSize{
  height:25px;
  font-size:0.9rem;
  line-height:25px;
}

.smallSize{
  height:20px;
  font-size:0.65rem;
  line-height:20px;
}

:global(.light) .tag{
    background:#b3b3b3;
    color:#ffffff;
  }

:global(.light) .tag.isSelected,
    :global(.light) .tag.clickable:hover{
      background:rgb(77, 157, 135);
    }

:global(.light) .tag.clickable:active{
      background:rgb(88, 198, 167);
    }

:global(.dark) .tag{
    background:#444444;
    color:#adadad;
  }

:global(.dark) .tag.isSelected,
    :global(.dark) .tag.clickable:hover{
      background:#185a49;
    }

:global(.dark) .tag.clickable:active{
      background:#0d674d;
    }
