.content{
  position:relative;
  display:-ms-flexbox;
  display:flex;
  -ms-flex:1 0 auto;
      flex:1 0 auto;
  -ms-flex-direction:row;
      flex-direction:row;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  width:100%;
}

  .content.normal{
    padding:6px 10px;
    border-radius:3px;
  }

  .content.extendFromLeft{
    padding:6px 10px 6px 20px;
    border-top-right-radius:3px;
    border-bottom-right-radius:3px;
  }

  .content.extendFromRight{
    padding:6px 20px 6px 10px;
    border-top-left-radius:3px;
    border-bottom-left-radius:3px;
  }

:global(.light) .content{
    color:#4e4e4e;
  }

:global(.light) .content:hover{
      background:#e1e1e1;
      color:#525252;
    }

:global(.light) .content:active{
      background:#636363;
      color:#e4e4e4;
    }

:global(.dark) .content{
    color:#797979;
  }

:global(.dark) .content:hover{
      background:#121212;
      color:#a0a0a0;
    }

:global(.dark) .content:active{
      background:rgb(31, 31, 31);
      color:#afafaf;
    }
