.root{
  font-weight:bold;
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  padding:6px 0 6px 8px;
}

  .root .delimiterSelector{
    padding-right:5px;
  }

  .root.unClickableDelimiter{
    pointer-events:none;
  }

  .root.unClickableDelimiter .delimiterSelector{
      display:none;
    }

.centered{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align:baseline;
      align-items:baseline;
  padding:4px;
}

:global(.light) .root{
    color:#c5c5c5;
  }

:global(.light) .root:hover .delimiterSelector{
      color:#457abb;
    }

:global(.light) .root:active .delimiterSelector{
      color:#5b9cec;
    }

:global(.dark) .root{
    color:#4a4a4a;
  }

:global(.dark) .root:hover .delimiterSelector{
      color:#2d5079;
    }

:global(.dark) .root:active .delimiterSelector{
      color:#5b9cec;
    }
