.root {
  position: relative;
}

.title {
  @mixin textOverflow;

  margin-bottom: 5px;
  padding-bottom: 5px;
}

.versionSelector {
  @mixin genericButton;

  margin-left: 5px;
  text-transform: none;
  cursor: pointer;
}

.tooltip {
  display: inline-block;
}

/*
 * Theme
 */

:global(.light) {
  .versionSelector {
    color: $light_regexAreaLabel_versionSelector_color;
  }
}

:global(.dark) {
  .versionSelector {
    color: $dark_regexAreaLabel_versionSelector_color;
  }
}
