.treeviewArrow{
  display:inline-block;
  width:0;
  height:0;
  border-top:6px solid;
  border-right:4px solid transparent;
  border-left:4px solid transparent;
  -webkit-transition:all 0.1s;
  -o-transition:all 0.1s;
  transition:all 0.1s;
}

  .treeviewArrow.collapsed{
    -webkit-transform:rotate(-90deg);
            transform:rotate(-90deg);
  }

.treeview{
  overflow-y:hidden;
}

.treeviewLabel{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
}

.treeviewChildren{
  margin-left:20px;
}

.treeviewChildren.collapsed{
    height:0;
  }

.treeviewArrowContainer{
  -ms-flex:0 0 20px;
      flex:0 0 20px;
  float:left;
  width:20px;
  text-align:center;
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

:global(.light) .treeviewArrow{
    border-top-color:#000000;
  }

:global(.light) .treeviewArrowContainer:hover .treeviewArrow{
    border-top-color:#40d0c7;
  }

:global(.dark) .treeviewArrow{
    border-top-color:#555555;
  }

:global(.dark) .treeviewArrowContainer:hover .treeviewArrow{
    border-top-color:#358c86;
  }
