.pageArrow{
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  padding:5px;
  opacity:0.4;

  font-size:0.7rem;
}
  .pageArrow:hover{
  opacity:0.6;
}
  .pageArrow:active{
  opacity:0.8;
}
  .pageArrow.hidden{
    visibility:hidden;
  }

:global(.light) .pageArrow{
    color:#177359;
  }

:global(.dark) .pageArrow{
    color:#3b9458;
  }
