.fullscreenContainer{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  z-index:1000;
}

.header{
  position:relative;
}

.header .exitBtn{
    position:absolute;
    top:50%;
    right:10px;
    -webkit-transform:translateY(-50%);
            transform:translateY(-50%);
  }

.header .title{
    position:relative;
    -ms-flex:1 0 auto;
        flex:1 0 auto;
    text-align:center;
    font-weight:200;
    font-size:1.3rem;
  }

.logo{
  -ms-flex:1 0 auto;
      flex:1 0 auto;
}

.opacityContainer{
  width:100%;
  height:100%;
  -webkit-transition:opacity 0.2s ease-in-out;
  -o-transition:opacity 0.2s ease-in-out;
  transition:opacity 0.2s ease-in-out;
}

.content{
  position:relative;
  width:100%;
  height:100%;
  opacity:1;
  -webkit-transition:-webkit-transform 0.2s ease-in-out;
  transition:-webkit-transform 0.2s ease-in-out;
  -o-transition:transform 0.2s ease-in-out;
  transition:transform 0.2s ease-in-out;
  transition:transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  -webkit-transform:translateY(0);
          transform:translateY(0);
}

.component{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  top:50px;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  margin:10px 0;
  padding:0 10px;
  -webkit-user-select:text;
     -moz-user-select:text;
      -ms-user-select:text;
          user-select:text;
}

.component.left{
    -ms-flex-pack:start;
        justify-content:flex-start;
  }

.component.center{
    -ms-flex-pack:center;
        justify-content:center;
  }

.component.right{
    -ms-flex-pack:end;
        justify-content:flex-end;
  }

.enter{
  opacity:0;
}

.enter .content{
    -webkit-transform:translateY(30px);
            transform:translateY(30px);
  }

.enterActive{
  opacity:1;
}

.enterActive .content{
    -webkit-transform:translateY(0);
            transform:translateY(0);
    pointer-events:none;
  }

.leave{
  opacity:1;
}

.leave .content{
    -webkit-transform:translateY(0);
            transform:translateY(0);
  }

.leaveActive{
  opacity:0;
  pointer-events:none;
}

.leaveActive .content{
    -webkit-transform:translateY(30px);
            transform:translateY(30px);
  }

:global(.light) .opacityContainer{
    background:#f7f7f7;
    color:#333333;
  }

:global(.light) .exitBtnTheme{
    background:rgb(87, 135, 193);
  }

:global(.light) .exitBtnTheme:hover{
  background:rgb(106, 148, 200);
}

:global(.light) .exitBtnTheme:active{
  background:rgb(125, 162, 207);
}

:global(.dark) .opacityContainer{
    background:#1a1a1a;
    color:#ababab;
  }

:global(.dark) .exitBtnTheme{
    background:rgb(52, 94, 141);
  }

:global(.dark) .exitBtnTheme:hover{
  background:rgb(59, 106, 160);
}

:global(.dark) .exitBtnTheme:active{
  background:rgb(66, 119, 179);
}
