.root{
  min-width:100px;
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
  line-height:1.3;
  pointer-events:none;
}

.match{
  margin-bottom:5px;
  padding-bottom:5px;
  border-bottom:1px solid;
}

.group{
  white-space:nowrap;
  overflow:hidden;
  -o-text-overflow:ellipsis;
     text-overflow:ellipsis;
}

.group span,
.match span{
  font-weight:bold;
}

:global(.light) .match{
    border-bottom-color:rgba(255, 255, 255, 0.08);
  }

:global(.light) .match span{
      color:#12cc12;
    }

:global(.light) .group span{
    color:#4caaff;
  }

:global(.dark) .match{
    border-bottom-color:rgba(255, 255, 255, 0.08);
  }

:global(.dark) .match span{
      color:#12cc12;
    }

:global(.dark) .group span{
    color:#4caaff;
  }
