.details {
  @mixin overflowAuto;

  position: relative;
  z-index: 2;
  width: 100%;
}

.detailsTable {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  align-items: center;
}

.tableText {
  flex: 0 0 auto;
}

.testString,
.compareString {
  white-space: pre-wrap;
  flex: 0 1 auto;
}

.testString,
.target,
.compareString {
  margin: 0 5px;
}

.target,
.criteria,
.testString,
.compareString {
  @mixin overflowAuto;

  padding: 3px 5px;
  max-height: 100px;
  border-radius: 3px;
}

.target,
.criteria {
  white-space: nowrap;
  flex: 0 0 auto;
}

.error {
  @mixin wordwrap;

  padding: 0 10px 10px;
}

/*
 * Theme
 */

:global(.light) {
  .details {
    color: $light_testDetails_details_color;
  }

  .target {
    background: $light_testDetails_target_background;
  }

  .criteria {
    background: $light_testDetails_criteria_background;
  }

  .testString {
    background: $light_testDetails_testString_background;
  }

  .compareString {
    background: $light_testDetails_compareString_background;
  }
}

:global(.dark) {
  .details {
    color: $dark_testDetails_details_color;
  }

  .target {
    background: $dark_testDetails_target_background;
  }

  .criteria {
    background: $dark_testDetails_criteria_background;
  }

  .testString {
    background: $dark_testDetails_testString_background;
  }

  .compareString {
    background: $dark_testDetails_compareString_background;
  }
}
