.tokenSample{
  white-space:pre-wrap;
  word-wrap:break-word;
  word-break:break-all;
  overflow-wrap:break-word;
}

.plainText,
.keyword,
.flag,
.info,
.tokenSample,
.zeroWidth,
.token{
  letter-spacing:1px;
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
  white-space:pre-wrap;
}

.zeroWidth{
  border-right:2px dotted #dc00ff;
  overflow:hidden;
  padding-left:2px;
  margin-right:2px;
}

.flag,
.boldNodeLabel,
.keyword{
  font-weight:bold;
}

.tokenExplanation{
  display:inline-block;
}

.nodeLabel,
.boldNodeLabel{
  white-space:pre-wrap;
  word-wrap:break-word;
  word-break:break-all;
  overflow-wrap:break-word;
}

.flagDisabled{
  text-decoration:line-through;
}

.tooltip{
  display:inline-block;
  margin-left:5px;
  opacity:0.5;
  cursor:help;
}

.tooltip:hover{
    opacity:1;
  }

:global(.light) .keyword{
    color:#1e7d58;
  }

:global(.light) .info{
    color:#b5b5b5;
  }

:global(.light) .tokenSample{
    background:#f3db90;
    color:#7b610e;
  }

:global(.light) .plainText{
    background:#eeeeee;
    color:#555555;
  }

:global(.light) .flag{
    color:#530c8e;
  }

:global(.light) .flags{
    color:#5d7f68;
  }

:global(.light) .delimiter{
    color:rgb(217, 217, 217);
  }

:global(.light) .parentTokens{
    background:#eeeeee;
  }

:global(.light) .groupSuffix{
    color:#aaaaaa;
  }

:global(.light) .groupSuffix.positive{
      color:#658c65;
    }

:global(.light) .groupSuffix.negative{
      color:#9a2d2b;
    }

:global(.dark) .keyword{
    color:#1da06d;
  }

:global(.dark) .info{
    color:#b5b5b5;
  }

:global(.dark) .tokenSample{
    background:#ad8a1d;
    color:#231b02;
  }

:global(.dark) .plainText{
    background:#eeeeee;
    color:#555555;
  }

:global(.dark) .flag{
    color:#9061b7;
  }

:global(.dark) .flags{
    color:#3b9458;
  }

:global(.dark) .delimiter{
    color:rgb(217, 217, 217);
  }

:global(.dark) .parentTokens{
    background:rgb(38, 38, 38);
  }

:global(.dark) .groupSuffix{
    color:#5f5f5f;
  }

:global(.dark) .groupSuffix.positive{
      color:#658c65;
    }

:global(.dark) .groupSuffix.negative{
      color:#9a5857;
    }
