.root {
  position: absolute;
  z-index: $tooltipZIndex;
  display: inline-block;
  transition: opacity $fastSpeed ease-in;
  pointer-events: none;
}

.tooltipped {
  padding: 6px 8px;
  max-width: $maxWidthTooltip;
  border-radius: 3px;
  text-decoration: none;
  font-size: 0.8rem;
}

.arrow {
  position: relative;
  left: 50%;
  margin-left: -5px;
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-top-color: rgba(0, 0, 0, 0.8);
  pointer-events: none;
}

/*
 * Transition
 */

.enter {
  opacity: 0;
}

.enterActive {
  opacity: 1;
}

.leave {
  opacity: 1;
}

.leaveActive {
  opacity: 0;
}

/*
 * Theme
 */

:global(.light) {
  .tooltipped {
    background: $light_tooltipped_background;
    color: $light_tooltipped_color;
  }
  .arrow {
    border-top-color: $light_tooltipped_background;
  }
}

:global(.dark) {
  .tooltipped {
    background: $dark_tooltipped_background;
    color: $dark_tooltipped_color;
  }
  .arrow {
    border-top-color: $dark_tooltipped_background;
  }
}
