.fetchError{
  margin-top:10px;
}

.chartContainer{
  margin:10px 0 0;
  min-height:200px;
}

.spinner{
  margin:10px auto 0;
}

.tooltip{
  padding:6px 8px;
  border-radius:3px;
  text-align:center;
  white-space:pre-wrap;
  word-wrap:break-word;
  font-size:0.75rem;
  font-family:'Open Sans', 'Lucida Sans Unicode', 'Lucida Grande', sansSerif;
  max-width:200px;
  display:block;
}

:global(.light) .tooltip{
    background:rgba(0, 0, 0, 0.8);
    color:#ffffff;
  }

:global(.dark) .tooltip{
    background:rgba(0, 0, 0, 0.8);
    color:#cacaca;
  }
