.root{
  width:100%;
  padding:0 10px 10px 10px;
  border:1px solid;
  border-radius:3px;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
}

.toolbar{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-align:center;
      align-items:center;
  margin:0 -10px 10px -10px;
  padding:5px 10px 0 10px;
  border-bottom:1px solid;
  border-top-left-radius:3px;
  border-top-right-radius:3px;
}

.tab{
  display:inline-block;
  margin-bottom:-1px;
  padding:0 12px;
  border:1px solid transparent;
  border-bottom:0;
  border-top-left-radius:3px;
  border-top-right-radius:3px;
  font-size:0.8rem;
  line-height:35px;
  cursor:pointer;
}

.tab:first-child{
    margin-right:5px;
  }

.active{
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

:global(.light) .root{
    border-color:#ffffff #f3f3f3 #ededed #f3f3f3;
    background:#fdfdfd;
  }

:global(.light) .tab{
    color:#333333;
  }

:global(.light) .tab.active{
    border-color:#e0e0e0;
    background:#fdfdfd;
  }

:global(.light) .toolbar{
    border-bottom-color:#e0e0e0;
    background:#efefef;
  }

:global(.dark) .root{
    border-color:#1f1f1f #212121 #212121 #212121;
    background:#1a1a1a;
  }

:global(.dark) .tab{
    color:#6d6d6d;
  }

:global(.dark) .tab.active{
    border-color:#212121;
    background:#1a1a1a;
  }

:global(.dark) .toolbar{
    border-bottom-color:#212121;
    background:#171717;
  }
