.testRowParent{
  position:relative;
  overflow:hidden;
  margin-bottom:5px;
  border-radius:3px;
}

.header{
  position:relative;
}

.testRow{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align:center;
      align-items:center;
  padding:8px 110px 8px 10px;
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

.activeTest{
  border-bottom-right-radius:0;
  border-bottom-left-radius:0;
}

.tools{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  font-size:1rem;
  padding:0 4px;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align:center;
      align-items:center;
}

.tools .icon{
    padding:2px 5px;
    width:16px;
    text-align:center;
    cursor:pointer;
  }

.tools .icon:last-of-type{
      margin-right:0;
    }

.body{
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  overflow:hidden;
}

.caret{
  width:20px;
  opacity:0.6;
  text-align:center;
  margin-left:-6px;
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}

.description{
  font-weight:bold;
  font-size:0.75rem;
  padding-bottom:5px;
}

.metaData{
  font-size:0.7rem;
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
}

.description,
.metaData{
  white-space:nowrap;
  overflow:hidden;
  -o-text-overflow:ellipsis;
     text-overflow:ellipsis;
}

.metadataDetails{
  font-weight:bold;
}

.disabled{
  opacity:0.5;
  pointer-events:none;
}

.delete:hover,.move:hover,.edit:hover{
    opacity:0.75;
  }

.delete:active,.move:active,.edit:active{
    opacity:0.85;
  }

:global(.light) .testRowParent{
    background:#e6e6e6;
    color:#585858;
  }

:global(.light) .testRowParent .tools{
      color:#6f6f6f;
    }

:global(.light) .testRowParent .testRow{
      background:rgb(219, 219, 219);
    }

:global(.light) .testRowParent .testRow:hover{
        background:rgb(209, 209, 209);
      }

:global(.light) .testRowParent .testRow:active{
        background:rgb(213, 212, 212);
      }

:global(.light) .testRowParent.fail{
      background:#ffcaca;
    }

:global(.light) .testRowParent.fail .tools{
        color:#ffffff;
      }

:global(.light) .testRowParent.fail .testRow{
        background:#d03b3b;
        color:#ffffff;
      }

:global(.light) .testRowParent.fail .testRow:hover{
          background:rgb(217, 99, 99);
        }

:global(.light) .testRowParent.fail .testRow:active{
          background:rgb(212, 78, 78);
        }

:global(.light) .testRowParent.pass{
      background:#cce8d0;
    }

:global(.light) .testRowParent.pass .tools{
        color:#ffffff;
      }

:global(.light) .testRowParent.pass .testRow{
        background:#46a754;
        color:#ffffff;
      }

:global(.light) .testRowParent.pass .testRow:hover{
          background:rgb(97, 189, 111);
        }

:global(.light) .testRowParent.pass .testRow:active{
          background:rgb(79, 181, 94);
        }

:global(.dark) .testRowParent{
    background:#232323;
    color:#c3c3c3;
  }

:global(.dark) .testRowParent .tools{
      color:#6f6f6f;
    }

:global(.dark) .testRowParent .testRow{
      background:rgb(48, 48, 48);
    }

:global(.dark) .testRowParent .testRow:hover{
        background:rgb(66, 66, 66);
      }

:global(.dark) .testRowParent .testRow:active{
        background:rgb(61, 61, 61);
      }

:global(.dark) .testRowParent.fail{
      background:#401515;
    }

:global(.dark) .testRowParent.fail .tools{
        color:#f3eded;
      }

:global(.dark) .testRowParent.fail .testRow{
        background:#902929;
        color:#f3eded;
      }

:global(.dark) .testRowParent.fail .testRow:hover{
          background:rgb(171, 48, 48);
        }

:global(.dark) .testRowParent.fail .testRow:active{
          background:rgb(163, 46, 46);
        }

:global(.dark) .testRowParent.pass{
      background:#19381d;
    }

:global(.dark) .testRowParent.pass .tools{
        color:#d6e4d8;
      }

:global(.dark) .testRowParent.pass .testRow{
        background:#0e6f1c;
        color:#d6e4d8;
      }

:global(.dark) .testRowParent.pass .testRow:hover{
          background:rgb(18, 145, 37);
        }

:global(.dark) .testRowParent.pass .testRow:active{
          background:rgb(17, 136, 35);
        }
