.portal{
  pointer-events:none;
}

.tooltipped{
  position:relative;
  z-index:99999;
  -webkit-transition:opacity 0.1s ease-in;
  -o-transition:opacity 0.1s ease-in;
  transition:opacity 0.1s ease-in;
}

.tooltipped span{
  position:absolute;
  padding:6px 8px;
  border-radius:3px;
  text-align:center;
  white-space:pre;
  word-wrap:break-word;
  font-size:0.75rem;
  font-family:'Open Sans', 'Lucida Sans Unicode', 'Lucida Grande', sansSerif;
}

.tooltipped:before{
  position:absolute;
  z-index:99999;
  width:0;
  height:0;
  border:5px solid transparent;
  content:'';
  pointer-events:none;
}

.tooltipped:before,
.tooltipped span{
  display:inline-block;
  text-decoration:none;
}

.tooltipped-s span,
.tooltipped-se span,
.tooltipped-sw span{
  top:100%;
  right:50%;
  margin-top:5px;
}

.tooltipped-s:before,
.tooltipped-se:before,
.tooltipped-sw:before{
  top:auto;
  right:50%;
  bottom:-5px;
  margin-right:-5px;
}

.tooltipped-se span{
  right:auto;
  left:50%;
  margin-left:-15px;
}

.tooltipped-sw span{
  margin-right:-15px;
}

.tooltipped-n span,
.tooltipped-ne span,
.tooltipped-nw span{
  right:50%;
  bottom:100%;
  margin-bottom:5px;
}

.tooltipped-n:before,
.tooltipped-ne:before,
.tooltipped-nw:before{
  top:-5px;
  right:50%;
  bottom:auto;
  margin-right:-5px;
}

.tooltipped-ne span{
  right:auto;
  left:50%;
  margin-left:-15px;
}

.tooltipped-nw span{
  margin-right:-15px;
}

.tooltipped-s span,
.tooltipped-n span{
  -webkit-transform:translateX(50%);
          transform:translateX(50%);
}

.tooltipped-w span{
  right:100%;
  bottom:50%;
  margin-right:5px;
  -webkit-transform:translateY(50%);
          transform:translateY(50%);
}

.tooltipped-w:before{
  top:50%;
  bottom:50%;
  left:-5px;
  margin-top:-5px;
}

.tooltipped-e span{
  bottom:50%;
  left:100%;
  margin-left:5px;
  -webkit-transform:translateY(50%);
          transform:translateY(50%);
}

.tooltipped-e:before{
  top:50%;
  right:-5px;
  bottom:50%;
  margin-top:-5px;
}

.multiline span{
  display:table-cell;
  max-width:250px;
  width:-webkit-max-content;
  width:-moz-max-content;
  width:max-content;
  border-collapse:separate;
  white-space:pre-line;
  word-wrap:normal;
  word-break:break-word;
}

.multiline.tooltipped-s span,
.multiline.tooltipped-n span{
  right:auto;
  left:50%;
  -webkit-transform:translateX(-50%);
          transform:translateX(-50%);
}

.multiline.tooltipped-w span,
.multiline.tooltipped-e span{
  right:100%;
}

@media screen and (min-width: 0 \0){
  .multiline span{
    width:250px;
  }
}

.tooltipped-sticky:before,
.tooltipped-sticky span{
  display:inline-block;
}

.tooltipped-sticky.multiline span{
  display:table-cell;
}

.enter{
  opacity:0;
}

.enterActive{
  opacity:1;
}

.leave{
  opacity:0;
}

.leaveActive{
  opacity:0;
}

:global(.light) .tooltipped span{
    background:rgba(0, 0, 0, 0.8);
    color:#ffffff;
  }

:global(.light) .tooltipped:before{
    color:rgba(0, 0, 0, 0.8);
  }

:global(.light) .tooltipped-s:before,
  :global(.light) .tooltipped-se:before,
  :global(.light) .tooltipped-sw:before{
    border-bottom-color:rgba(0, 0, 0, 0.8);
  }

:global(.light) .tooltipped-n:before,
  :global(.light) .tooltipped-ne:before,
  :global(.light) .tooltipped-nw:before{
    border-top-color:rgba(0, 0, 0, 0.8);
  }

:global(.light) .tooltipped-w:before{
    border-left-color:rgba(0, 0, 0, 0.8);
  }

:global(.light) .tooltipped-e:before{
    border-right-color:rgba(0, 0, 0, 0.8);
  }

:global(.dark) .tooltipped span{
    background:rgba(0, 0, 0, 0.8);
    color:#cacaca;
  }

:global(.dark) .tooltipped:before{
    color:rgba(0, 0, 0, 0.8);
  }

:global(.dark) .tooltipped-s:before,
  :global(.dark) .tooltipped-se:before,
  :global(.dark) .tooltipped-sw:before{
    border-bottom-color:rgba(0, 0, 0, 0.8);
  }

:global(.dark) .tooltipped-n:before,
  :global(.dark) .tooltipped-ne:before,
  :global(.dark) .tooltipped-nw:before{
    border-top-color:rgba(0, 0, 0, 0.8);
  }

:global(.dark) .tooltipped-w:before{
    border-left-color:rgba(0, 0, 0, 0.8);
  }

:global(.dark) .tooltipped-e:before{
    border-right-color:rgba(0, 0, 0, 0.8);
  }
