:global {
  .CodeMirror-show-whitespace {
    .cm-space {
      position: relative;
      display: inline-block;
      /* Adjust line height of inline-block to match that of an inline element */
      line-height: normal;
      /* This ensures the box is aligned in case its too tall. This is a problem on OSX with the Monaco font */
      vertical-align: text-bottom;
    }

    .cm-space::before {
      position: absolute;
      pointer-events: none;
      content: "·";
      left: 0;
    }

    .CodeMirror-code > div:not(:last-child) .CodeMirror-line > span::after,
    .CodeMirror-line:not(:last-child) > span::after {
      pointer-events: none;
      content: "\00B6";
      position: absolute;
    }

    .cm-tab {
      display: inline-block;
      background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADAAAAAMCAYAAAAkuj5RAAAAAXNSR0IArs4c6QAAAGFJREFUSMft1LsRQFAQheHPowAKoACx3IgEKtaEHujDjORSgWTH/ZOdnZOcM/sgk/kFFWY0qV8foQwS4MKBCS3qR6ixBJvElOobYAtivseIE120FaowJPN75GMu8j/LfMwNjh4HUpwg4LUAAAAASUVORK5CYII=);
      background-position: right;
      background-repeat: no-repeat;
      text-decoration: inherit;
    }

    .CodeMirror pre {
      /* we override ex with ch if the browser supports it in order to cover as many browsers as possible */
      padding-right: 1ex;
      padding-right: 1ch;
    }
  }

  /*
   * Theme
   */

  .light {
    .cm-space::before,
    .CodeMirror-code > div:not(:last-child) .CodeMirror-line > span::after,
    .CodeMirror-line:not(:last-child) > span::after {
      color: rgba(0, 0, 0, 0.2);
    }
  }

  .dark {
    .cm-space::before,
    .CodeMirror-code > div:not(:last-child) .CodeMirror-line > span::after,
    .CodeMirror-line:not(:last-child) > span::after {
      color: rgba(255, 255, 255, 0.2);
    }

    .cm-tab {
      filter: invert(100%);
    }
  }
}
