.root{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
}

.content{
  -ms-flex:1 1 auto;
      flex:1 1 auto;
}

.navigation{
  -ms-flex:1 0 190px;
      flex:1 0 190px;
  overflow-x:hidden !important;
  max-width:190px;
}

.content,
.navigation{
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  -webkit-box-sizing:border-box;

          box-sizing:border-box;
  height:100%;
}

.spacer{
  margin:0 5px;
  border-right:1px solid;
}

.listItem{
  text-transform:none;
}

@media (max-width: 600px){
  .root{
    -ms-flex-direction:column;
        flex-direction:column;
  }

  .navigation{
    max-width:none;
    max-height:35vh;
  }

  .spacer{
    margin:0 0 5px;
    padding:0 0 5px;
    border-right:0;
    border-bottom:1px solid;
  }
}

.search{
  margin-bottom:5px;
}

:global(.light) .spacer{
    border-color:rgba(58, 58, 58, 0.1);
  }

:global(.dark) .spacer{
    border-color:rgba(156, 156, 156, 0.07);
  }
