.details{
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  position:relative;
  z-index:2;
  width:100%;
}

.detailsTable{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  width:100%;
  padding:10px;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  -ms-flex-align:center;
      align-items:center;
}

.tableText{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}

.testString,
.compareString{
  white-space:pre-wrap;
  -ms-flex:0 1 auto;
      flex:0 1 auto;
}

.testString,
.target,
.compareString{
  margin:0 5px;
}

.target,
.criteria,
.testString,
.compareString{
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  padding:3px 5px;
  max-height:100px;
  border-radius:3px;
}

.target,
.criteria{
  white-space:nowrap;
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}

.error{
  white-space:pre-wrap;
  word-wrap:break-word;
  word-break:break-all;
  overflow-wrap:break-word;

  padding:0 10px 10px;
}

:global(.light) .details{
    color:rgba(0, 0, 0, 0.65);
  }

:global(.light) .target{
    background:rgba(255, 255, 255, 0.5);
  }

:global(.light) .criteria{
    background:rgba(255, 255, 255, 0.5);
  }

:global(.light) .testString{
    background:rgba(255, 255, 255, 0.5);
  }

:global(.light) .compareString{
    background:rgba(255, 255, 255, 0.5);
  }

:global(.dark) .details{
    color:#7b7b7b;
  }

:global(.dark) .target{
    background:rgba(0, 0, 0, 0.5);
  }

:global(.dark) .criteria{
    background:rgba(0, 0, 0, 0.5);
  }

:global(.dark) .testString{
    background:rgba(0, 0, 0, 0.5);
  }

:global(.dark) .compareString{
    background:rgba(0, 0, 0, 0.5);
  }
