/* stylelint-disable */
:global {
  /* BASICS */
  .CodeMirror {
    /* Set height, width, borders, and global font properties here */
    background: transparent;
    letter-spacing: 1px;
    line-height: 1.6em;
    cursor: text;
  }
  .CodeMirror-scrollbar-filler,
  .CodeMirror-gutter-filler {
    background-color: transparent; /* The little square between H and V scrollbars */
  }
  /* GUTTER */
  .CodeMirror-gutters {
    margin-left: -5px;
    border-right: 1px solid;
    white-space: nowrap;
  }
  .CodeMirror-linenumber {
    margin-left: -5px;
    padding: 0 3px 0 5px;
    min-width: 20px;
    text-align: right;
    white-space: nowrap;
  }
  /* CURSOR */
  .CodeMirror-cursor {
    width: 0;
    border-right: none;
    border-left: 1px solid black;
    pointer-events: none; /* this is necessary for tooltips to not lose focus */
  }
  .cm-tab {
    display: inline-block;
    text-decoration: inherit;
  }
  /* STOP */
  /* The rest of this file contains styles related to the mechanics of
     the editor. You probably shouldn't touch them. */
  .CodeMirror {
    position: relative;
    overflow: hidden;
  }
  .CodeMirror-scroll {
    position: relative;
    overflow: scroll !important; /* Things will break if this is overridden */
    margin-right: -30px;
    /* 30px is the magic margin used to hide the element's real scrollbars */
    /* See overflow: hidden in .CodeMirror */
    margin-bottom: -30px;
    padding-bottom: 30px;
    height: 100%;
    outline: none; /* Prevent dragging from highlighting the element */
  }
  .CodeMirror-sizer {
    position: relative;
    border-right: 30px solid transparent;
  }
  /* The fake, visible scrollbars. Used to force redraw during scrolling
     before actual scrolling happens, thus preventing shaking and
     flickering artifacts. */
  .CodeMirror-vscrollbar,
  .CodeMirror-hscrollbar,
  .CodeMirror-scrollbar-filler,
  .CodeMirror-gutter-filler {
    position: absolute;
    z-index: 6;
    display: none;
  }
  .CodeMirror-vscrollbar {
    top: 0;
    right: 0;
    overflow-x: hidden;
    overflow-y: scroll;
  }
  .CodeMirror-hscrollbar {
    bottom: 0;
    left: 0;
    overflow-x: scroll;
    overflow-y: hidden;
  }
  .CodeMirror-scrollbar-filler {
    right: 0;
    bottom: 0;
  }
  .CodeMirror-gutter-filler {
    bottom: 0;
    left: 0;
  }
  .CodeMirror-gutters {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    min-height: 100%;
  }
  .CodeMirror-gutter {
    display: inline-block;
    margin-bottom: -30px;
    height: 100%;
    vertical-align: top;
    white-space: normal;
  }
  .CodeMirror-gutter-wrapper {
    position: absolute;
    z-index: 4;
    border: none !important;
    background: none !important;
  }
  .CodeMirror-gutter-background {
    position: absolute;
    top: 0;
    bottom: 0;
    z-index: 4;
  }
  .CodeMirror-gutter-elt {
    position: absolute;
    z-index: 4;
    cursor: default;
  }
  .CodeMirror-gutter-wrapper {
    user-select: none;
  }
  .CodeMirror-lines {
    min-height: 1px; /* prevents collapsing before first draw */
    cursor: text;
  }
  .CodeMirror pre {
    position: relative;
    z-index: 0;
    overflow: visible;
    margin: 0;
    border-width: 0;
    /* Reset some styles that the rest of the page might have set */
    border-radius: 0;
    background: transparent;
    color: inherit;
    white-space: pre;
    word-wrap: normal;
    font-size: inherit;
    font-family: inherit;
    line-height: inherit;
    -webkit-tap-highlight-color: transparent;
    font-variant-ligatures: none;
  }
  .CodeMirror-wrap pre {
    white-space: pre-wrap;
    word-wrap: break-word;
    word-break: normal;
  }
  .CodeMirror-linebackground {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0;
  }
  .CodeMirror-linewidget {
    position: relative;
    z-index: 2;
    overflow: auto;
  }
  .CodeMirror-code {
    outline: none;
  }
  /* Force content-box sizing for the elements where we expect it */
  .CodeMirror-scroll,
  .CodeMirror-sizer,
  .CodeMirror-gutter,
  .CodeMirror-gutters,
  .CodeMirror-linenumber {
    box-sizing: content-box;
  }
  .CodeMirror-measure {
    position: absolute;
    visibility: hidden;
    overflow: hidden;
    width: 100%;
    height: 0;
  }
  .CodeMirror-cursor {
    position: absolute;
    pointer-events: none;
  }
  .CodeMirror-measure pre {
    position: static;
  }
  div.CodeMirror-cursors {
    position: relative;
    z-index: 3;
    visibility: hidden;
  }
  div.CodeMirror-dragcursors {
    visibility: visible;
  }
  .CodeMirror-focused div.CodeMirror-cursors {
    visibility: visible;
  }
  /* Used to force a border model for a node */
  .cm-force-border {
    padding-right: 0.1px;
  }
  /* See issue #2901 */
  .cm-tab-wrap-hack:after {
    content: '';
  }

  .CodeMirror-selected {
    pointer-events: none;
  }

  /*
   * Theme
   */
  .light {
    .CodeMirror-cursor {
      border-color: $light_codeMirror_cursor_borderColor;
    }
    .CodeMirror-selected {
      background: $light_codeMirror_inactive_selectedBackground;
    }
    .CodeMirror-focused .CodeMirror-selected {
      background: $light_codeMirror_selectedBackground;
    }
    .CodeMirror-placeholder {
      color: $light_codeMirror_placeholder_color;
    }
    .CodeMirror-gutters {
      border-right-color: $light_codeMirror_gutter_borderColor;
      background: $light_codeMirror_gutter_background;
    }
    .CodeMirror-linenumber {
      color: $light_codeMirror_gutter_color;
    }
  }
  .dark {
    .CodeMirror-cursor {
      border-color: $dark_codeMirror_cursor_borderColor;
    }
    .CodeMirror-selected {
      background: $dark_codeMirror_inactive_selectedBackground;
    }
    .CodeMirror-focused .CodeMirror-selected {
      background: $dark_codeMirror_selectedBackground;
    }
    .CodeMirror-placeholder {
      color: $dark_codeMirror_placeholder_color;
    }
    .CodeMirror-gutters {
      border-right-color: $dark_codeMirror_gutter_borderColor;
      background: $dark_codeMirror_gutter_background;
    }
    .CodeMirror-linenumber {
      color: $dark_codeMirror_gutter_color;
    }
  }
}
