.contentArea {
  /* IE11 fix */
  flex-basis: calc(100% - 10px);
  flex: 1 0 calc(33.333% - 10px);
  border: 1px solid;
  border-radius: 3px;
  font-size: 0.85rem;
  transition: flex $testareaSpeed ease;
  will-change: flex;
}

.titleBtn {
  margin: 0 10px;
  padding: 10px 0 4px;
  transition: border-color $testareaSpeed ease;
  user-select: none;
}

.content > * {
  @mixin fullscreen 7px, 10px, 10px, 10px;
}

.shrinkArea {
  min-height: 37px;
  flex: 0 1 37px;

  .titleBtn {
    border-color: transparent;
  }
}

.resizable {
  margin-bottom: 10px;
}

/*
 * Theme
 */

:global(.light) {
  .contentArea {
    border-color: $light_rightSidebar_contentArea_borderColor;
    background: $light_rightSidebar_contentArea_background;
  }
}

:global(.dark) {
  .contentArea {
    border-color: $dark_rightSidebar_contentArea_borderColor;
    background: $dark_rightSidebar_contentArea_background;
  }
}
