.root {
  display: inline-block;
  text-transform: lowercase;
  letter-spacing: -1px;
  font-weight: 100;
  font-size: 120%;

  &:hover {
    text-decoration: none;
  }
}

.root:hover {
  cursor: pointer;
}

.suffix {
  margin-top: 3px;
  font-size: 50%;
}

.narrow {
  display: none;
}

.wide {
  display: inline;
}

@media (max-width: $smallLogoBreakpoint) {
  .narrow {
    display: inline;
  }
  .wide {
    display: none;
  }
}

.regular,
.expressions {
  .wide {
    margin-right: 3px;
  }
}

.noClick {
  pointer-events: none;
}

/*
 * Theme
 */

:global(.light) {
  .regular {
    color: $light_logo_regular_color;
  }
  .suffix {
    color: $light_logo_suffix_color;
  }
  .expressions {
    color: $light_logo_expressions_color;
  }
  .root:hover {
    .regular {
      color: $light_logo_regular_hoverColor;
    }
    .suffix {
      color: $light_logo_suffix_hoverColor;
    }
    .expressions {
      color: $light_logo_expressions_hoverColor;
    }
  }
}

:global(.dark) {
  .regular {
    color: $dark_logo_regular_color;
  }
  .suffix {
    color: $dark_logo_suffix_color;
  }
  .expressions {
    color: $dark_logo_expressions_color;
  }
  .root:hover {
    .regular {
      color: $dark_logo_regular_hoverColor;
    }
    .suffix {
      color: $dark_logo_suffix_hoverColor;
    }
    .expressions {
      color: $dark_logo_expressions_hoverColor;
    }
  }
}
