.root{
  margin-left:10px;
  text-transform:lowercase;
}

.link{
  color:inherit;
  text-decoration:none;
  font-size:0.8rem;
}

.linkIcon{
  margin-right:5px;
}

:global(.light) .root:not(:hover) .text{
    color:#ffffff;
  }

:global(.light) .iconButton{
    background:rgb(87, 135, 193);
  }

:global(.light) .iconButton:hover{
  background:rgb(106, 148, 200);
}

:global(.light) .iconButton:active{
  background:rgb(125, 162, 207);
}

:global(.dark) .root:not(:hover) .text{
    color:#c1c1c1;
  }

:global(.dark) .iconButton{
    background:rgb(52, 94, 141);
  }

:global(.dark) .iconButton:hover{
  background:rgb(59, 106, 160);
}

:global(.dark) .iconButton:active{
  background:rgb(66, 119, 179);
}
