.input {
  padding: 8px;

  :global {
    .CodeMirror pre {
      padding: 0 2px; /* Horizontal padding of content, needed for borders to not clip when hovering */
    }

    .CodeMirror-lines {
      padding: 2px 0; /* Horizontal padding of content, needed for borders to not clip when hovering */
    }
  }
}

.input {
  @mixin box;
  @mixin inputArea;

  outline: 0;
  font-size: 0.875rem;
  font-family: $codeFont;
  resize: none;
}

/*
 * Theme
 */

:global(.light) {
  .input {
    border-color: $light_textInput_borderColor;
    background: $light_textInput_background;

    &.editorFocused {
      border-color: $light_textInput_activeBorderColor;
      box-shadow: $light_textInput_activeBoxShadow;
    }
  }
}

:global(.dark) {
  .input {
    border-color: $dark_textInput_borderColor;
    background: $dark_textInput_background;

    &.editorFocused {
      border-color: $dark_textInput_activeBorderColor;
      box-shadow: $dark_textInput_activeBoxShadow;
    }
  }
}
