.wrapper{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
}

.entries{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
  -webkit-transition:-webkit-filter 0.2s ease-in-out;
  transition:-webkit-filter 0.2s ease-in-out;
  -o-transition:filter 0.2s ease-in-out;
  transition:filter 0.2s ease-in-out;
  transition:filter 0.2s ease-in-out, -webkit-filter 0.2s ease-in-out;

  display:-ms-flexbox;

  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  padding:10px;
}

.entries.isFetchingData{
  -webkit-filter:blur(2px);
          filter:blur(2px);
  pointer-events:none;
}

.error{
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack:center;
      justify-content:center;
}

.label{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}

.dataContainer{
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  -ms-flex:1 1 auto;

      flex:1 1 auto;
  padding:10px;
}

.searchInput{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
  margin-bottom:10px;
}

.paginator{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}

:global(.light) .libraryEntry:nth-child(odd){
    background:#f7f7f7;
  }

:global(.dark) .libraryEntry:nth-child(odd){
    background:#2d2d2d;
  }
