.sidebar{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  width:85vw;
  border-left:1px solid;
  -webkit-box-shadow:-3px 0 10px -5px rgba(0, 0, 0, 0.35);
          box-shadow:-3px 0 10px -5px rgba(0, 0, 0, 0.35);
  -webkit-transition:all 0.25s ease;
  -o-transition:all 0.25s ease;
  transition:all 0.25s ease;
  opacity:1;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  will-change:opacity, transform;
  z-index:2;
}

.menuHamburgerParent{
  top:0;
  right:0;
}

.collapsedSidebar{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  z-index:1000;
}

.sidebarBackdrop{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  width:100%;
  height:100%;
  opacity:1;
  -webkit-transition:opacity 0.25s ease-in-out;
  -o-transition:opacity 0.25s ease-in-out;
  transition:opacity 0.25s ease-in-out;
  will-change:opacity;
  z-index:1;
}

.enter .sidebar,
  .enter .sidebarBackdrop{
    opacity:0;
  }

.enter .sidebar{
    -webkit-transform:translateX(100vw);
            transform:translateX(100vw);
  }

.enterActive .sidebar,
  .enterActive .sidebarBackdrop{
    opacity:1;
    pointer-events:none;
  }

.enterActive .sidebar{
    -webkit-transform:translateX(85vw);
            transform:translateX(85vw);
  }

.leave .sidebar,
  .leave .sidebarBackdrop{
    opacity:1;
  }

.leave .sidebar{
    -webkit-transform:translateX(85vw);
            transform:translateX(85vw);
  }

.leaveActive .sidebar,
  .leaveActive .sidebarBackdrop{
    opacity:0;
    pointer-events:none;
  }

.leaveActive .sidebar{
    -webkit-transform:translateX(100vw);
            transform:translateX(100vw);
  }

:global(.light) .sidebar{
    border-color:#ffffff;
    background:#f7f7f7;
    -webkit-box-shadow:0 5px 30px rgba(93, 93, 93, 0.6);
            box-shadow:0 5px 30px rgba(93, 93, 93, 0.6);
    color:#333333;
  }

:global(.light) .menuHamburger{
    background:rgb(87, 135, 193);

    color:#ffffff;
  }

:global(.light) .menuHamburger:hover{
  background:rgb(106, 148, 200);
}

:global(.light) .menuHamburger:active{
  background:rgb(125, 162, 207);
}

:global(.light) .sidebarBackdrop{
    background:hsla(0, 0%, 40%, 0.6);
  }

:global(.dark) .sidebar{
    border-color:#212121;
    background:#1a1a1a;
    -webkit-box-shadow:0 5px 30px hsla(0, 0%, 20%, 0.6);
            box-shadow:0 5px 30px hsla(0, 0%, 20%, 0.6);
    color:#ababab;
  }

:global(.dark) .menuHamburger{
    background:rgb(52, 94, 141);

    color:#c1c1c1;
  }

:global(.dark) .menuHamburger:hover{
  background:rgb(59, 106, 160);
}

:global(.dark) .menuHamburger:active{
  background:rgb(66, 119, 179);
}

:global(.dark) .sidebarBackdrop{
    background:hsla(0, 0%, 40%, 0.6);
  }
