:global(.light) {
  .inQuote {
    color: #737d94;
  }
  .alternator,
  .meta,
  .quantifier {
    background: #89b6ff;
    color: #263142;
  }
  .escaped_string {
    background: #aaaaaa;
    color: #282828;
  }
  .charclass,
  .inCharClass {
    background: #ffc080;
    color: #753e07;

    &.meta,
    &.quantifier,
    &.escaped_string {
      background: #e0a060;
    }
  }
  .inline_modifiers {
    background: #a35fdc;
    color: #ffffff;
  }
  .verb {
    background: #ce5dc3;
    color: #ffffff;
  }
  .group-0 {
    background: #b3f172;
    color: #48543b;
  }
  .group-1 {
    background: #b4c76e;
    color: #414c17;
  }
  .group-2 {
    background: #d6ae4f;
    color: #4e3c11;
  }
  .group-3 {
    background: #3d9644;
    color: #ffffff;
  }
  .group-4 {
    background: #3a5094;
    color: #ffffff;
  }
  .group-5 {
    background: #335833;
    color: #ffffff;
  }
  .group-6 {
    background: #dada68;
    color: #5a5a19;
  }
  .group-7 {
    background: #249aa2;
    color: #ffffff;
  }
  .group-8 {
    background: #808000;
    color: #ffffff;
  }
  .group-9 {
    background: #955ece;
    color: #ffffff;
  }
  .selected_bracket {
    background: #00ffff;
    color: #000000;
  }
  .subpattern_reference {
    background: #8262af;
    color: #ffffff;
  }
  .comment {
    background: none;
    color: #888888;
    font-style: italic;
  }
  .backreference {
    background: #5c9e8e;
    color: #ffffff;
  }
  /* Error rule last to override everything */
  .error {
    background: #ed5c65 !important;
    color: #ffffff !important;
  }
  .mouseoverRelated,
  .mouseoverRelatedLeft,
  .mouseoverRelatedRight {
    border-color: rgba(0, 0, 0, 0.22);
  }
  .mouseover,
  .mouseoverLeft,
  .mouseoverRight {
    border-color: rgba(0, 0, 0, 0.33);
  }
  .cursorover {
    background: #09eae0 !important;
    color: #225a58 !important;
  }
}

:global(.dark) {
  .inQuote {
    color: #868686;
  }
  .alternator,
  .meta,
  .quantifier {
    background: #264577;
    color: #e1edff;
  }
  .escaped_string {
    background: #a9a9a9;
    color: #2f2f2f;
  }
  .charclass,
  .inCharClass {
    background: #ca9157;
    color: #33200b;

    &.meta,
    &.quantifier,
    &.escaped_string {
      background: #866443;
    }
  }
  .inline_modifiers {
    background: #63229a;
    color: #e5e5e5;
  }
  .verb {
    background: #841479;
    color: #e5e5e5;
  }
  .group-0 {
    background: #3e6713;
    color: #d8e0ce;
  }
  .group-1 {
    background: #a1c712;
    color: #3b480a;
  }
  .group-2 {
    background: #bd8c18;
    color: #423006;
  }
  .group-3 {
    background: #117719;
    color: #ddf9df;
  }
  .group-4 {
    background: #384c8a;
    color: #e2e9ff;
  }
  .group-5 {
    background: #6cad6c;
    color: #133513;
  }
  .group-6 {
    background: #d6d62f;
    color: #484809;
  }
  .group-7 {
    background: #0f7177;
    color: #f0feff;
  }
  .group-8 {
    background: #808000;
    color: #f9f9e1;
  }
  .group-9 {
    background: #6e1dc1;
    color: #f5ecff;
  }
  .selected_bracket {
    background: #00ffff;
    color: #000000;
  }
  .subpattern_reference {
    background: #8d28b3;
    color: #e5e5e5;
  }
  .comment {
    background: none;
    color: #868686;
    font-style: italic;
  }
  .backreference {
    background: #10715a;
    color: #e5e5e5;
  }
  /* Error rule last to override everything */
  .error {
    background: #8a2d2d !important;
    color: #ffffff !important;
  }
  .mouseoverRelated,
  .mouseoverRelatedLeft,
  .mouseoverRelatedRight {
    border-color: rgba(255, 255, 255, 0.22);
  }
  .mouseover,
  .mouseoverLeft,
  .mouseoverRight {
    border-color: rgba(255, 255, 255, 0.33);
  }
  .cursorover {
    background: #09eae0 !important;
    color: #225a58 !important;
  }
}

/*
 * General mouseover styles
 */

.mouseoverRelated {
  margin-top: -1px;
  margin-bottom: -1px;
  border-top: 1px solid;
  border-bottom: 1px solid;
}

.mouseoverRelatedLeft {
  margin-left: -1px;
  border-left: 1px solid;
}

.mouseoverRelatedRight {
  margin-right: -1px;
  border-right: 1px solid;
}

.mouseover {
  margin-top: -2px;
  margin-bottom: -2px;
  border-top: 2px solid;
  border-bottom: 2px solid;
}

.mouseoverLeft {
  margin-left: -2px;
  border-left: 2px solid;
}

.mouseoverRight {
  margin-right: -2px;
  border-right: 2px solid;
}

.mouseover,
.mouseoverLeft,
.mouseoverRight,
.mouseoverRelated,
.mouseoverRelatedLeft,
.mouseoverRelatedRight {
  position: relative;
}
