.root {
  flex: 1 1 100%;
  min-width: 140px;
  /*
    The padding here is 1px more than OptionsContainer and DelimiterSelector
    I am not entirely sure why, but it seems to fix the alignment issue of 1px in FF and Chrome.
  */
  padding: 5px 0;

  :global {
    .CodeMirror pre {
      padding: 0 2px; /* Horizontal padding of content, needed for borders to not clip when hovering */
    }

    .CodeMirror-lines {
      padding: 2px 0; /* Horizontal padding of content, needed for borders to not clip when hovering */
    }
  }
}
