.okButton,
.cancelButton,
.dangerButton,
.executeButton {
  box-sizing: content-box;
  padding: 6px 20px;
  outline: 0;
  border: 1px solid;
  border-radius: 3px;
  box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.19);
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0 -1px 1px rgba(0, 0, 0, 0.4), 0 0 15px rgba(0, 0, 0, 0.2);
  white-space: nowrap;
  font-weight: bold;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background $defaultSpeed ease;
  user-select: none;

  &:active {
    box-shadow: inset 0 2px 3px 0 rgba(0, 0, 0, 0.12), inset 0 -1px 2px 0 rgba(0, 0, 0, 0.06), 0 1px 2px 0 rgba(0, 0, 0, 0.11);
  }
}

.disabled {
  opacity: 0.3;
  pointer-events: none;
}

/*
 * Theme
 */

:global(.light) {
  .okButton,
  .cancelButton,
  .dangerButton,
  .executeButton {
    color: $light_button_color;
  }
  .okButton {
    @mixin button $light_button_ok_background;
  }
  .cancelButton {
    @mixin button $light_button_cancel_background;
  }
  .dangerButton {
    @mixin button $light_button_danger_background;
  }
  .executeButton {
    @mixin button $light_button_execute_background;
  }
}

:global(.dark) {
  .okButton,
  .cancelButton,
  .dangerButton,
  .executeButton {
    color: $dark_button_color;
  }
  .okButton {
    @mixin button $dark_button_ok_background;
  }
  .cancelButton {
    @mixin button $dark_button_cancel_background;
  }
  .dangerButton {
    @mixin button $dark_button_danger_background;
  }
  .executeButton {
    @mixin button $dark_button_execute_background;
  }
}
