.root{
  max-width:1000px;
  width:100%;
  height:100%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  overflow:hidden;
}

.container{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  margin-bottom:10px;
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  overflow:hidden;
}

.language{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
  padding-right:20px;
  min-width:200px;
}

.codeContainer{
  -ms-flex:2 1 auto;
      flex:2 1 auto;
}

.code{
  white-space: pre-wrap;
  word-wrap: break-word;
  word-break: break-all;
  overflow-wrap: break-word;
  overflow: auto;
  -webkit-overflow-scrolling: touch;

  position:relative;
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
  max-height:100%;
}

.code code{
    overflow:visible;
    margin:0;
    padding:0;
    line-height:1.3;
  }

.copyButton{
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 5px;
  opacity: 0.4;

  font-size:1rem;
}

.copyButton:hover {
    opacity: 0.6;
}

.copyButton:active {
    opacity: 0.8;
}

.referenceUrl{
  white-space: pre-wrap;
  word-wrap: break-word;
  word-break: break-all;
  overflow-wrap: break-word;
}

.infoNotice{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
  text-align:center;
  line-height:1.3em;
}

@media (max-width: 900px){
  .container{
    padding:0;
  }

  .container{
    -ms-flex-direction:column;
        flex-direction:column;
  }

  .language{
    -ms-flex:1 1 auto;
