$railHeight: 2px;
$handleSize: 14px;

.slider {
  margin: 10px 20px;
  position: relative;
  height: 40px;
}

.rail {
  position: absolute;
  left: 0;
  right: 0;
  top: -20px;
  bottom: 0;
  height: 40px;
  border-radius: 3px;
  cursor: pointer;

  .actualRail {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    height: $railHeight;
  }
}

.handle {
  position: absolute;
  margin-left: -8px;
  margin-top: -6px;
  z-index: 2;
  width: $handleSize;
  height: $handleSize;
  cursor: pointer;
  border: 0;
  border-radius: 50%;

  &::after {
    position: absolute;
    display: block;
    content: '';
    left: -10px;
    top: -10px;
    bottom: -10px;
    right: -10px;
  }
}

.track {
  position: absolute;
  height: $railHeight;
  z-index: 1;
  border-radius: 3px;
  cursor: pointer;
}

.tickLine {
  position: absolute;
  margin-top: calc($railHeight + 10px);
  width: 1px;
  height: 6px;
  margin-left: -1px;
}

.tickText {
  position: absolute;
  margin-top: calc($railHeight + 18px);
  font-size: 0.7rem;
  text-align: center;
}

/*
 * Theme
 */

:global(.light) {
  .tickLine,
  .actualRail {
    background: rgba(0, 0, 0, 0.2);
  }

  .handle {
    background: $light_theme_color;
  }

  .track {
    background: $light_theme_color;
  }
}

:global(.dark) {
  .tickLine,
  .actualRail {
    background: rgba(255, 255, 255, 0.2);
  }

  .handle {
    background: $dark_theme_color;
  }

  .track {
    background: $dark_theme_color;
  }
}
