.root{
  padding:0 10px 10px;
  border:1px solid;
  border-radius:3px;
}

@media (max-width: 500px){
  .toolbar{
    height:auto;
  }
}

.tools{
  -ms-flex:1 1 auto;
      flex:1 1 auto;
}

.markdownLink{
  margin-top:10px;
}

.markdownLink:hover{
    color:inherit;
  }

.editorWrapper{
  position:relative;
}

.previewWindow,
.editor{
  font-size:0.8rem;
  line-height:1.45;
}

.editor{
  height:200px;
  resize:vertical;
}

.previewWindow{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  border-top:1px solid transparent;
  border-bottom:1px solid;
}

:global(.light) .root{
    border-color:#ffffff #f3f3f3 #ededed #f3f3f3;
  }

:global(.light) .root,
  :global(.light) .previewWindow{
    background:#fdfdfd;
  }

:global(.light) .previewWindow{
    border-bottom-color:#e0e0e0;
  }

:global(.light) .tab{
    color:#333333;
  }

:global(.light) .tab.active{
    border-color:#e0e0e0;
    background:#fdfdfd;
  }

:global(.light) .toolbar{
    border-bottom-color:#e0e0e0;
    background:#efefef;
  }

:global(.dark) .root{
    border-color:#1f1f1f #212121 #212121 #212121;
  }

:global(.dark) .root,
  :global(.dark) .previewWindow{
    background:#1a1a1a;
  }

:global(.dark) .previewWindow{
    border-bottom-color:#212121;
  }

:global(.dark) .tab{
    color:#6d6d6d;
  }

:global(.dark) .tab.active{
    border-color:#212121;
    background:#1a1a1a;
  }

:global(.dark) .toolbar{
    border-bottom-color:#212121;
    background:#171717;
  }
