.portal {
  pointer-events: none;
}

.tooltipped {
  position: relative;
  z-index: $tooltipZindex;
  transition: opacity $fastSpeed ease-in;
}

.tooltipped span {
  position: absolute;
  padding: 6px 8px;
  border-radius: 3px;
  text-align: center;
  white-space: pre;
  word-wrap: break-word;
  font-size: 0.75rem;
  font-family: $appFont;
}

.tooltipped:before {
  position: absolute;
  z-index: $tooltipZindex;
  width: 0;
  height: 0;
  border: 5px solid transparent;
  content: '';
  pointer-events: none;
}

.tooltipped:before,
.tooltipped span {
  display: inline-block;
  text-decoration: none;
}

.tooltipped-s span,
.tooltipped-se span,
.tooltipped-sw span {
  top: 100%;
  right: 50%;
  margin-top: 5px;
}

.tooltipped-s:before,
.tooltipped-se:before,
.tooltipped-sw:before {
  top: auto;
  right: 50%;
  bottom: -5px;
  margin-right: -5px;
}

.tooltipped-se span {
  right: auto;
  left: 50%;
  margin-left: -15px;
}

.tooltipped-sw span {
  margin-right: -15px;
}

.tooltipped-n span,
.tooltipped-ne span,
.tooltipped-nw span {
  right: 50%;
  bottom: 100%;
  margin-bottom: 5px;
}

.tooltipped-n:before,
.tooltipped-ne:before,
.tooltipped-nw:before {
  top: -5px;
  right: 50%;
  bottom: auto;
  margin-right: -5px;
}

.tooltipped-ne span {
  right: auto;
  left: 50%;
  margin-left: -15px;
}

.tooltipped-nw span {
  margin-right: -15px;
}

.tooltipped-s span,
.tooltipped-n span {
  transform: translateX(50%);
}

.tooltipped-w span {
  right: 100%;
  bottom: 50%;
  margin-right: 5px;
  transform: translateY(50%);
}

.tooltipped-w:before {
  top: 50%;
  bottom: 50%;
  left: -5px;
  margin-top: -5px;
}

.tooltipped-e span {
  bottom: 50%;
  left: 100%;
  margin-left: 5px;
  transform: translateY(50%);
}

.tooltipped-e:before {
  top: 50%;
  right: -5px;
  bottom: 50%;
  margin-top: -5px;
}

.multiline span {
  display: table-cell;
  max-width: 250px;
  width: max-content;
  border-collapse: separate;
  white-space: pre-line;
  word-wrap: normal;
  word-break: break-word;
}

.multiline.tooltipped-s span,
.multiline.tooltipped-n span {
  right: auto;
  left: 50%;
  transform: translateX(-50%);
}

.multiline.tooltipped-w span,
.multiline.tooltipped-e span {
  right: 100%;
}

@media screen and (min-width: 0 \0) {
  .multiline span {
    width: 250px;
  }
}

.tooltipped-sticky:before,
.tooltipped-sticky span {
  display: inline-block;
}

.tooltipped-sticky.multiline span {
  display: table-cell;
}

/*
 * Transition
 */

.enter {
  opacity: 0;
}

.enterActive {
  opacity: 1;
}

.leave {
  opacity: 0;
}

.leaveActive {
  opacity: 0;
}

/*
 * Theme
 */

:global(.light) {
  .tooltipped span {
    background: $light_tooltipped_background;
    color: $light_tooltipped_color;
  }
  .tooltipped:before {
    color: $light_tooltipped_background;
  }
  .tooltipped-s:before,
  .tooltipped-se:before,
  .tooltipped-sw:before {
    border-bottom-color: $light_tooltipped_background;
  }
  .tooltipped-n:before,
  .tooltipped-ne:before,
  .tooltipped-nw:before {
    border-top-color: $light_tooltipped_background;
  }
  .tooltipped-w:before {
    border-left-color: $light_tooltipped_background;
  }
  .tooltipped-e:before {
    border-right-color: $light_tooltipped_background;
  }
}

:global(.dark) {
  .tooltipped span {
    background: $dark_tooltipped_background;
    color: $dark_tooltipped_color;
  }
  .tooltipped:before {
    color: $dark_tooltipped_background;
  }
  .tooltipped-s:before,
  .tooltipped-se:before,
  .tooltipped-sw:before {
    border-bottom-color: $dark_tooltipped_background;
  }
  .tooltipped-n:before,
  .tooltipped-ne:before,
  .tooltipped-nw:before {
    border-top-color: $dark_tooltipped_background;
  }
  .tooltipped-w:before {
    border-left-color: $dark_tooltipped_background;
  }
  .tooltipped-e:before {
    border-right-color: $dark_tooltipped_background;
  }
}
