.sidebar {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 85vw;
  border-left: 1px solid;
  box-shadow: -3px 0 10px -5px rgba(0, 0, 0, 0.35);
  transition: all $sidebarSpeed ease;
  opacity: 1;
  user-select: none;
  will-change: opacity, transform;
  z-index: 2;
}

.menuHamburgerParent {
  top: 0;
  right: 0;
}

.collapsedSidebar {
  @mixin fullscreen;

  z-index: 1000;
}

.sidebarBackdrop {
  @mixin fullscreen;

  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity $sidebarSpeed ease-in-out;
  will-change: opacity;
  z-index: 1;
}

/*
 * Animations
 */

.enter {
  .sidebar,
  .sidebarBackdrop {
    opacity: 0;
  }

  .sidebar {
    transform: translateX(100vw);
  }
}

.enterActive {
  .sidebar,
  .sidebarBackdrop {
    opacity: 1;
    pointer-events: none;
  }

  .sidebar {
    transform: translateX(85vw);
  }
}

.leave {
  .sidebar,
  .sidebarBackdrop {
    opacity: 1;
  }

  .sidebar {
    transform: translateX(85vw);
  }
}

.leaveActive {
  .sidebar,
  .sidebarBackdrop {
    opacity: 0;
    pointer-events: none;
  }

  .sidebar {
    transform: translateX(100vw);
  }
}

/*
 * Theme
 */

:global(.light) {
  .sidebar {
    border-color: $light_sidebar_borderColor;
    background: $light_sidebar_background;
    box-shadow: 0 5px 30px $light_modal_boxShadow_color;
    color: $light_globalApp_color;
  }

  .menuHamburger {
    @mixin iconBtnTheme $light_sidebar_menuHamburger_background, lightness;

    color: $light_sidebar_menuHamburger_color;
  }

  .sidebarBackdrop {
    background: $light_modal_backdrop_background;
  }
}

:global(.dark) {
  .sidebar {
    border-color: $dark_sidebar_borderColor;
    background: $dark_sidebar_background;
    box-shadow: 0 5px 30px $dark_modal_boxShadow_color;
    color: $dark_globalApp_color;
  }

  .menuHamburger {
    @mixin iconBtnTheme $dark_sidebar_menuHamburger_background, lightness;

    color: $dark_sidebar_menuHamburger_color;
  }

  .sidebarBackdrop {
    background: $dark_modal_backdrop_background;
  }
}
