.root {
  width: 30px;
  text-align: center;
  user-select: none;
}

.votebutton {
  @mixin genericButton;
}

.disabled {
  pointer-events: none;
}

.score {
  font-weight: bold;
}

.hasVoted {
  opacity: 1;
}

/*
 * Theme
 */

:global(.light) {
  .upvote:active,
  .upvote:hover,
  .upvote.hasVoted {
    color: $light_voteButton_upvote_color;
  }
  .downvote:active,
  .downvote:hover,
  .downvote.hasVoted {
    color: $light_voteButton_downvote_color;
  }
}

:global(.dark) {
  .upvote:active,
  .upvote:hover,
  .upvote.hasVoted {
    color: $dark_voteButton_upvote_color;
  }
  .downvote:active,
  .downvote:hover,
  .downvote.hasVoted {
    color: $dark_voteButton_downvote_color;
  }
}
