.root {
  font-weight: bold;
  cursor: pointer;
  user-select: none;
  padding: 6px 0 6px 8px;

  .delimiterSelector {
    padding-right: 5px;
  }
  &.unClickableDelimiter {
    pointer-events: none;

    .delimiterSelector {
      display: none;
    }
  }
}

.centered {
  display: flex;
  align-items: baseline;
  padding: 4px;
}

/*
 * Theme
 */

:global(.light) {
  .root {
    color: $light_delimiterSelector_color;

    &:hover .delimiterSelector {
      color: $light_delimiterSelector_hoverColor;
    }
    &:active .delimiterSelector {
      color: $light_delimiterSelector_activeColor;
    }
  }
}

:global(.dark) {
  .root {
    color: $dark_delimiterSelector_color;

    &:hover .delimiterSelector {
      color: $dark_delimiterSelector_hoverColor;
    }
    &:active .delimiterSelector {
      color: $dark_delimiterSelector_activeColor;
    }
  }
}
