.root {
  display: flex;
  flex-direction: row;
}

.content {
  flex: 1 1 auto;
}

.navigation {
  flex: 1 0 190px;
  overflow-x: hidden !important;
  max-width: 190px;
}

.content,
.navigation {
  @mixin overflowAuto;

  box-sizing: border-box;
  height: 100%;
}

.spacer {
  margin: 0 5px;
  border-right: 1px solid;
}

.listItem {
  text-transform: none;
}

/*
 * On small screens we can't show the two navigators next to eachother.
 * This assumes we are using the overlay version of the sidebar!
 */

@media (max-width: 600px) {
  .root {
    flex-direction: column;
  }

  .navigation {
    max-width: none;
    max-height: 35vh;
  }

  .spacer {
    margin: 0 0 5px;
    padding: 0 0 5px;
    border-right: 0;
    border-bottom: 1px solid;
  }
}

.search {
  margin-bottom: 5px;
}

/*
 * Theme
 */

:global(.light) {
  .spacer {
    border-color: $light_label_borderColor;
  }
}

:global(.dark) {
  .spacer {
    border-color: $dark_label_borderColor;
  }
}
