:global .CodeMirror-show-whitespace .cm-space{
      position:relative;
      display:inline-block;
      line-height:normal;
      vertical-align:text-bottom;
    }
    :global .CodeMirror-show-whitespace .cm-space::before{
      position:absolute;
      pointer-events:none;
      content:"·";
      left:0;
    }
    :global .CodeMirror-show-whitespace .CodeMirror-code > div:not(:last-child) .CodeMirror-line > span::after,
    :global .CodeMirror-show-whitespace .CodeMirror-line:not(:last-child) > span::after{
      pointer-events:none;
      content:"\00B6";
      position:absolute;
    }
    :global .CodeMirror-show-whitespace .cm-tab{
      display:inline-block;
      background:url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADAAAAAMCAYAAAAkuj5RAAAAAXNSR0IArs4c6QAAAGFJREFUSMft1LsRQFAQheHPowAKoACx3IgEKtaEHujDjORSgWTH/ZOdnZOcM/sgk/kFFWY0qV8foQwS4MKBCS3qR6ixBJvElOobYAtivseIE120FaowJPN75GMu8j/LfMwNjh4HUpwg4LUAAAAASUVORK5CYII=);
      background-position:right;
      background-repeat:no-repeat;
      text-decoration:inherit;
    }
    :global .CodeMirror-show-whitespace .CodeMirror pre{
      padding-right:1ex;
      padding-right:1ch;
    }
  :global .light .cm-space::before,
    :global .light .CodeMirror-code > div:not(:last-child) .CodeMirror-line > span::after,
    :global .light .CodeMirror-line:not(:last-child) > span::after{
      color:rgba(0, 0, 0, 0.2);
    }
  :global .dark .cm-space::before,
    :global .dark .CodeMirror-code > div:not(:last-child) .CodeMirror-line > span::after,
    :global .dark .CodeMirror-line:not(:last-child) > span::after{
      color:rgba(255, 255, 255, 0.2);
    }
  :global .dark .cm-tab{
      -webkit-filter:invert(100%);
              filter:invert(100%);
    }
