.flagItem {
  width: 155px;

  div {
    padding-top: 3px;
    white-space: pre-wrap;
    font-size: 0.7rem;
  }
}

.flagIcon {
  margin-left: 150px;
}

/*
 * Theme
 */

:global(.light) {
  .flagItem {
    strong {
      color: $light_flagsMenu_flag_color;
    }
    div {
      color: $light_flagsMenu_description_color;
    }
  }
}

:global(.dark) {
  .flagItem {
    strong {
      color: $dark_flagsMenu_flag_color;
    }
    div {
      color: $dark_flagsMenu_description_color;
    }
  }
}
