.quizContent {
  flex: 0 0 auto;
  margin: 0 0 10px;
}

.loginInfo {
  @mixin overflowAuto;

  flex: 1 1 auto;
}

.shortestEntry,
.regexArea {
  flex: 0 0 auto;
  margin: 10px 0;
}

.substArea {
  flex: 0 0 auto;
  margin: 0 0 10px;
}

.resultArea {
  flex: 0 0 auto;
  margin: 10px 0;
}

.buttonArea {
  display: flex;
  flex: 0 0 auto;

  .button {
    flex: 0 0 auto;
    margin-left: auto;
  }

  .spinnerContainer {
    margin-right: 10px;
    flex: 1 1 auto;
    align-self: center;
    text-align: right;

    .spinner {
      display: inline-block;
    }
  }

  .score {
    flex: 1 1 auto;
  }
}

.markdownContainer,
.error {
  word-break: break-word;
  white-space: pre-wrap;
  overflow: hidden;
}

.nextLink {
  margin-top: 10px;
  display: inline-block;
}

.fetchWrapper {
  @mixin fullscreen;
  @mixin fetchDataContainer;
}

.introSpace {
  margin: 10px 0;
}

.creditList {
  list-style: circle;
  list-style: disc;
  padding: 10px 30px;
}

/*
 * Theme
 */

:global(.light) {
  .error {
    color: $light_notice_error_color;
  }
}

:global(.dark) {
  .error {
    color: $dark_notice_error_color;
  }
}
