:global html,
  :global body{
    font-family:'Open Sans', 'Lucida Sans Unicode', 'Lucida Grande', sansSerif;
    overflow:hidden;
    margin:0;
    padding:0;
    width:100%;
    height:100%;
    -webkit-tap-highlight-color:transparent;
    -webkit-text-size-adjust:100%;
       -moz-text-size-adjust:100%;
        -ms-text-size-adjust:100%;
            text-size-adjust:100%;
    line-height:16px;
    font-size:16px;
  }
  :global #regex-app{
    width:100%;
    height:100%;
  }
  :global sub{
    vertical-align:sub;
    font-weight:normal;
    font-size:0.5rem;
  }
  :global ::-moz-selection{
    background:rgba(77, 145, 226, 0.3);
  }
  :global ::selection{
    background:rgba(77, 145, 226, 0.3);
  }
  :global ::-webkit-input-placeholder{
    opacity:1;
  }
  :global ::-ms-input-placeholder{
    opacity:1;
  }
  :global ::placeholder{
    opacity:1;
  }
  :global strong{
    font-weight:bold;
  }
  :global em{
    font-style:italic;
  }
  :global a{
    color:inherit;
    text-decoration:none;
  }
  :global .light ::-webkit-input-placeholder{
    color:#cccccc;
  }
  :global .light ::-ms-input-placeholder{
    color:#cccccc;
  }
  :global .light ::placeholder{
    color:#cccccc;
  }
  :global .dark ::-webkit-input-placeholder{
    color:#676767;
  }
  :global .dark ::-ms-input-placeholder{
    color:#676767;
  }
  :global .dark ::placeholder{
    color:#676767;
  }
