.parent {
  display: flex;
  flex-direction: column;
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
}

.header {
  flex: 0 0 auto;
}

.contentWrapper {
  flex: 1 1 auto;
  overflow: hidden;
  height: 100%;
  position: relative;
}

.content {
  position: absolute !important;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  @mixin overflowAuto;
}
