.menuPortal {
  position: absolute;
  z-index: $menuZIndex;
}

.menu {
  opacity: 1;
  transition: opacity $menuSpeed ease-out, transform $menuSpeed $jumpyEasing;
  transform: translateY(0) scale(1);
  transform-origin: center top;
  will-change: transform, opacity;
}

/*
 * Animations
 */

.enter {
  opacity: 0;
  transform: translateY(-10px) scale(0.95);
}

.enterActive {
  opacity: 1;
  transform: translateY(0) scale(1);
  pointer-events: none;
}

.leave {
  opacity: 1;
  transform: translateY(0) scale(1);
}

.leaveActive {
  opacity: 0;
  transform: translateY(10px) scale(0.95);
  pointer-events: none;
}
