.root {
  width: 100%;
  padding: 0 10px 10px 10px;
  border: 1px solid;
  border-radius: 3px;
  box-sizing: border-box;
}

.toolbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 -10px 10px -10px;
  padding: 5px 10px 0 10px;
  border-bottom: 1px solid;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.tab {
  display: inline-block;
  margin-bottom: -1px;
  padding: 0 12px;
  border: 1px solid transparent;
  border-bottom: 0;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  font-size: 0.8rem;
  line-height: 35px;
  cursor: pointer;

  &:first-child {
    margin-right: 5px;
  }
}

.active {
  user-select: none;
}

/*
 * Theme
 */

:global(.light) {
  .root {
    border-color: $light_markdownEditor_borderColor;
    background: $light_markdownEditor_background;
  }
  .tab {
    color: $light_markdownEditor_tab_color;
  }
  .tab.active {
    border-color: $light_markdownEditor_previewWindow_borderColor;
    background: $light_markdownEditor_background;
  }
  .toolbar {
    border-bottom-color: $light_markdownEditor_previewWindow_borderColor;
    background: $light_markdownEditor_toolbar_background;
  }
}

:global(.dark) {
  .root {
    border-color: $dark_markdownEditor_borderColor;
    background: $dark_markdownEditor_background;
  }
  .tab {
    color: $dark_markdownEditor_tab_color;
  }
  .tab.active {
    border-color: $dark_markdownEditor_previewWindow_borderColor;
    background: $dark_markdownEditor_background;
  }
  .toolbar {
    border-bottom-color: $dark_markdownEditor_previewWindow_borderColor;
    background: $dark_markdownEditor_toolbar_background;
  }
}
