.saveButton{
  margin-right:10px;
}

.root{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
  overflow:auto;
  -webkit-overflow-scrolling:touch;

  padding:10px;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
}

.root select{
    margin:0 0 5px 5px;
    min-width:100px;
  }

.spaceUnder{
  margin-bottom:5px;
}

.saveIndicator{
  position:absolute;
  top:10px;
  right:10px;
  color:green;
  background:inherit;
  font-size:0.7rem;
  text-transform:uppercase;
  pointer-events:none;
  -webkit-transition:all 300ms ease-out;
  -o-transition:all 300ms ease-out;
  transition:all 300ms ease-out;
}

.saveLeave{
  opacity:1;
  top:10px;
}

.saveLeaveActive{
  opacity:0;
  top:0;
}

:global(.light) .content{
    background:#f7f7f7;
  }

:global(.light) .saveIndicator{
    color:#1e6933;
  }

:global(.dark) .content{
    background:#1a1a1a;
  }

:global(.dark) .saveIndicator{
    color:#3b9458;
  }
