.root {
  margin-left: 10px;
  text-transform: lowercase;
}

.link {
  color: inherit;
  text-decoration: none;
  font-size: 0.8rem;
}

.linkIcon {
  margin-right: 5px;
}

/*
 * Theme
 */

:global(.light) {
  .root:not(:hover) .text {
    color: $light_header_color;
  }
  .iconButton {
    @mixin iconBtnTheme $light_headerLink_button_background, lightness;
  }
}

:global(.dark) {
  .root:not(:hover) .text {
    color: $dark_header_color;
  }
  .iconButton {
    @mixin iconBtnTheme $dark_headerLink_button_background, lightness;
  }
}
