.matchHeader {
  padding-bottom: 5px;
  text-align: left;
  white-space: nowrap;
}

.contentWrapper {
  margin-bottom: 5px;
  padding-bottom: 5px;
  border-bottom: 1px solid;
}

.matchTable {
  table-layout: auto;
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 5px;
}

@media (max-width: $matchInfoSmallScreenBreakpoint) {
  .matchTable {
    table-layout: fixed;
  }
}

/*
 * Theme
 */

:global(.light) {
  .matchHeader {
    color: $light_matchGroups_header_color;
  }

  .contentWrapper {
    border-color: $light_matchGroups_borderColor;
  }
}

:global(.dark) {
  .matchHeader {
    color: $dark_matchGroups_header_color;
  }

  .contentWrapper {
    border-color: $dark_matchGroups_borderColor;
  }
}
