$offlineTextHeight: 35px;
$sponsorHeight: 75px;

.sidebar {
  position: absolute;
  top: $headerHeight;
  bottom: 0;
  left: 0;
  box-sizing: border-box;
  width: $totalLeftSidebarWidth;
  border-right: 1px solid;
  user-select: none;
}

.sidebarChild {
  @mixin overflowAuto;

  flex: 1 1 auto;
  margin: 10px 10px 10px 0;
}

.sidebarContents {
  position: relative;
  margin-left: $innerLeftSidebarWidth;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.offlineText {
  flex: 0 0 auto;
  height: $offlineTextHeight;
  line-height: $offlineTextHeight;
  text-align: center;
  font-size: 0.8rem;
}

.sponsor {
  font-size: 0.8rem;
  border-top: 1px solid;
  margin: 10px;
  outline: 0;
  flex: 0 0 auto;
}

/*
 * Theme
 */

:global(.light) {
  .sidebar {
    border-color: $light_sidebar_borderColor;
    background: $light_sidebar_background;
  }

  .offlineText {
    background: $light_regexSidebar_deleteButton_color;
    color: $light_regexSidebar_deleteButton_hoverColor;
  }

  .sponsor {
    color: $light_label_color;
    border-color: $light_label_borderColor;
  }
}

:global(.dark) {
  .sidebar {
    border-color: $dark_sidebar_borderColor;
    background: $dark_sidebar_background;
  }

  .offlineText {
    background: $dark_regexSidebar_deleteButton_color;
    color: $dark_regexSidebar_deleteButton_hoverColor;
  }

  .sponsor {
    color: $dark_label_color;
    border-color: $dark_label_borderColor;
  }
}
