.page{
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  padding:5px;
  opacity:0.4;
}
  .page:hover{
  opacity:0.6;
}
  .page:active{
  opacity:0.8;
}

.active{
  font-weight:bold;
  opacity:1;
  pointer-events:none;
}
