.root{
  display:-ms-flexbox;
  display:flex;
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  -ms-flex-direction:column;
      flex-direction:column;
  overflow:hidden;
  height:0;
  -webkit-transition:flex 0.25s cubic-bezier(0.23, 1, 0.32, 1);
  -o-transition:flex 0.25s cubic-bezier(0.23, 1, 0.32, 1);
  transition:flex 0.25s cubic-bezier(0.23, 1, 0.32, 1);
  transition:flex 0.25s cubic-bezier(0.23, 1, 0.32, 1), -ms-flex 0.25s cubic-bezier(0.23, 1, 0.32, 1);
}

  .root :global(.CodeMirror){
    height:100%;
  }

.input{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;

  z-index:2;
  padding:5px;
}

.inputArea{
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  padding:5px;
  width:100%;
  outline:0;
  border:1px solid;
  border-radius:3px;
  -webkit-box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.1);
          box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.1);
  font-family:inherit;
  -webkit-transition:all 0.2s ease;
  -o-transition:all 0.2s ease;
  transition:all 0.2s ease;
  -webkit-transition-property:border-color, -webkit-box-shadow;
  transition-property:border-color, -webkit-box-shadow;
  -o-transition-property:border-color, box-shadow;
  transition-property:border-color, box-shadow;
  transition-property:border-color, box-shadow, -webkit-box-shadow;

  position:relative;
  -ms-flex:1 0 auto;
      flex:1 0 auto;
  font-size:0.875rem;
  font-family:Monaco, Consolas, 'Andale Mono', 'Lucida Console', 'PT Mono', 'Courier New', monospace;
}

@media (max-height: 500px){
  .substitutionVisible{
    -ms-flex:0 1 auto;
        flex:0 1 auto;
    overflow:hidden;
  }
}

.switchAreaLabel{
  cursor:pointer;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
  padding:5px;
  opacity:0.4;
}

.switchAreaLabel:hover{
  opacity:0.6;
}

.switchAreaLabel:active{
  opacity:0.8;
}

.caret{
  margin-left:5px;
}

:global(.light) .inputArea{
    border-color:#bdbdbd;
    background:#ffffff;
  }

:global(.light) .inputArea.editorFocused{
      border-color:#457abb;
      -webkit-box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.075), 0 0 5px rgba(69, 122, 187, 0.5);
              box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.075), 0 0 5px rgba(69, 122, 187, 0.5);
    }

:global(.light) .switchAreaLabel{
    color:#000000;
  }

:global(.dark) .inputArea{
    border-color:#292828;
    background:#272727;
  }

:global(.dark) .inputArea.editorFocused{
      border-color:#333333;
      -webkit-box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.075), 0 0 5px rgba(0, 0, 0, 0.6);
              box-shadow:inset 0 1px 2px rgba(0, 0, 0, 0.075), 0 0 5px rgba(0, 0, 0, 0.6);
    }

:global(.dark) .switchAreaLabel{
    color:#989898;
  }
