/*
 * Use a separate container for padding within a scroller.
 * See: https://bugzilla.mozilla.org/show_bug.cgi?id=748518
 */
.modalBody {
  position: relative;
  flex: 1 1 auto;
  overflow-y: auto;
  padding: 10px;
  height: 100%;
}

/*
 * Theme
 */

:global(.light) {
  .modalBody {
    background: $light_modalBody_background;
  }
}

:global(.dark) {
  .modalBody {
    background: $dark_modalBody_background;
  }
}
