.modalFooter {
  display: flex;
  flex: 0 0 $modalButtonAreaHeight;
  align-items: center;
  justify-content: flex-end;
  padding: 0 15px;
  height: $modalButtonAreaHeight;
  border-top: 1px solid;
  text-align: right;
}

/*
 * Theme
 */

:global(.light) {
  .modalFooter {
    border-color: $light_modalFooter_borderColor;
    background: $light_modalFooter_background;
  }
}

:global(.dark) {
  .modalFooter {
    border-color: $dark_modalFooter_borderColor;
    background: $dark_modalFooter_background;
  }
}
