.tag {
  position: relative;
  display: inline-block;
  margin: 0 5px 2px 0;
  padding: 0 10px;
  border-radius: 50px;
  transition: background $defaultSpeed ease;
  user-select: none;
  box-sizing: border-box;

  &.deletable {
    padding-right: 30px;
  }

  &.clickable {
    /* Clickable = those in the sidebar */
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    /*
     The extra added 20px are those form the padding/margin around the content
     that pushes the content in with a 10px space around the edges.

     -1px seems to be necessary to avoid clipping, don't know why.
     */
    max-width: calc($leftSidebarWidth - 20px - 1px);
  }
}

.deleteButton {
  position: absolute;
  top: 0;
  right: 5px;
  padding: 0 5px;
  opacity: 0.5;
  cursor: pointer;

  &:hover {
    opacity: 0.9;
  }
}

.mediumSize {
  height: 25px;
  font-size: 0.9rem;
  line-height: 25px;
}

.smallSize {
  height: 20px;
  font-size: 0.65rem;
  line-height: 20px;
}

/*
 * Theme
 */

:global(.light) {
  .tag {
    background: $light_tag_background;
    color: $light_tag_color;

    &.isSelected,
    &.clickable:hover {
      background: $light_tag_hoverBackground;
    }
    &.clickable:active {
      background: $light_tag_activeBackground;
    }
  }
}

:global(.dark) {
  .tag {
    background: $dark_tag_background;
    color: $dark_tag_color;

    &.isSelected,
    &.clickable:hover {
      background: $dark_tag_hoverBackground;
    }
    &.clickable:active {
      background: $dark_tag_activeBackground;
    }
  }
}
