.actionList{
  margin-bottom:15px;
}

.saveBtn{
  font-weight:bold;
}

.paragraph{
  margin-bottom:10px;
  font-size:0.8rem;
}

:global(.light) .saveBtn:not(:active){
    color:#417751;
  }

:global(.light) .favoriteButton:not(:active){
    color:#a03b3b;
  }

:global(.light) .deleteButton:not(:active){
    color:#a03b3b;
  }

:global(.light) .deleteButton:not(:active):hover{
      background:#a03b3b;
      color:#fff;
    }

:global(.dark) .saveBtn:not(:active){
    color:#3b9458;
  }

:global(.dark) .favoriteButton:not(:active){
    color:#a03b3b;
  }

:global(.dark) .deleteButton:not(:active){
    color:#a03b3b;
  }

:global(.dark) .deleteButton:not(:active):hover{
      background:#a03b3b;
      color:#d0d0d0;
    }
