.root{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-align:center;
      align-items:center;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  padding:0 10px;
  height:50px;
  border-bottom:1px solid;
  -webkit-box-shadow:0 -3px 10px 3px rgba(0, 0, 0, 0.15);
          box-shadow:0 -3px 10px 3px rgba(0, 0, 0, 0.15);
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

  .root.leftSidebarHamburger{
    padding-left:48px;
  }

  .root.rightSidebarHamburger{
    padding-right:48px;
  }

.links{
  display:-ms-flexbox;
  display:flex;
  -ms-flex:2 0 0px;
      flex:2 0 0;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-pack:end;
      justify-content:flex-end;
}

@media (max-width: 300px){
  .links *{
    display:none;
  }
}

@media (max-width: 350px) and (min-width: 300px){
  .logo{
    display:none;
  }
}

:global(.light) .root{
    border-color:#d5dfeb;
    background:#457abb;
    color:#ffffff;
  }

:global(.light) .iconButton{
    background:rgb(87, 135, 193);
  }

:global(.light) .iconButton:hover{
  background:rgb(106, 148, 200);
}

:global(.light) .iconButton:active{
  background:rgb(125, 162, 207);
}

:global(.light) .twitterLink{
    color:#94c5ff;
  }

:global(.light) .donateLink{
    color:#ffd17b;
  }

:global(.light) .contactLink{
    color:#6dde93;
  }

:global(.light) .feedbackLink{
    color:#ff7b7b;
  }

:global(.light) .wiki{
    color:#dadea8;
  }

:global(.dark) .root{
    border-color:#345b88;
    background:#2d5079;
    color:#c1c1c1;
  }

:global(.dark) .iconButton{
    background:rgb(52, 94, 141);
  }

:global(.dark) .iconButton:hover{
  background:rgb(59, 106, 160);
}

:global(.dark) .iconButton:active{
  background:rgb(66, 119, 179);
}

:global(.dark) .twitterLink{
    color:#7c9cc3;
  }

:global(.dark) .donateLink{
    color:#eab54e;
  }

:global(.dark) .contactLink{
    color:#59b578;
  }

:global(.dark) .feedbackLink{
    color:#ce6262;
  }

:global(.dark) .wiki{
    color:#7ba568;
  }
