.toggleItem{
  display:-ms-flexbox;
  display:flex;
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  overflow:hidden;
}

.checkMark{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
  padding-left:5px;
  text-align:right;
  line-height:1.25;
}

.isActive{
  font-weight:bold;
}

.children{
  white-space:nowrap;
  overflow:hidden;
  -o-text-overflow:ellipsis;
     text-overflow:ellipsis;

  -ms-flex:1 1 auto;

      flex:1 1 auto;
}

:global(.light) .toggleItem:not(:active) .checkMark{
    color:#1e6933;
  }

:global(.dark) .toggleItem:not(:active) .checkMark{
    color:#3b9458;
  }
