.error,
.warn,
.info {
  font-size: 0.725rem;
  line-height: 1.5;
}

/*
 * Theme
 */

:global(.light) {
  .info {
    color: $light_notice_info_color;
  }
  .warn {
    color: $light_notice_warning_color;
  }
  .error {
    color: $light_notice_error_color;
  }
}

:global(.dark) {
  .info {
    color: $dark_notice_info_color;
  }
  .warn {
    color: $dark_notice_warning_color;
  }
  .error {
    color: $dark_notice_error_color;
  }
}
