.actionList {
  margin-bottom: 15px;
}

.saveBtn {
  font-weight: bold;
}

.paragraph {
  margin-bottom: 10px;
  font-size: 0.8rem;
}

/*
* Theme
*/

:global(.light) {
  .saveBtn:not(:active) {
    color: $light_regexSidebar_saveButton_color;
  }

  .favoriteButton:not(:active) {
    color: $light_regexSidebar_favoriteButton_color;
  }

  .deleteButton:not(:active) {
    color: $light_regexSidebar_deleteButton_color;

    &:hover {
      background: $light_regexSidebar_deleteButton_hoverBackground;
      color: $light_regexSidebar_deleteButton_hoverColor;
    }
  }
}

:global(.dark) {
  .saveBtn:not(:active) {
    color: $dark_regexSidebar_saveButton_color;
  }

  .favoriteButton:not(:active) {
    color: $dark_regexSidebar_favoriteButton_color;
  }

  .deleteButton:not(:active) {
    color: $dark_regexSidebar_deleteButton_color;

    &:hover {
      background: $dark_regexSidebar_deleteButton_hoverBackground;
      color: $dark_regexSidebar_deleteButton_hoverColor;
    }
  }
}
