.root{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
}

.url{
  -ms-flex:1 0 auto;
      flex:1 0 auto;
}

.flavor{
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  text-align:right;
}

.title{
  font-weight:bold;
}

:global(.light) .flavor{
    color:#aaaaaa;
  }

:global(.light) .root a:not(:active) .title{
    color:#486890;
  }

:global(.dark) .flavor{
    color:#828282;
  }

:global(.dark) .root a:not(:active) .title{
    color:#577fad;
  }
