.parent{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  position:relative;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  overflow:hidden;
}

.header{
  -ms-flex:0 0 auto;
      flex:0 0 auto;
}

.contentWrapper{
  -ms-flex:1 1 auto;
      flex:1 1 auto;
  overflow:hidden;
  height:100%;
  position:relative;
}

.content{
  position:absolute !important;
  top:0;
  left:0;
  right:0;
  bottom:0;

  overflow:auto;

  -webkit-overflow-scrolling:touch;
}
