.slider{
  margin:10px 20px;
  position:relative;
  height:40px;
}
.rail{
  position:absolute;
  left:0;
  right:0;
  top:-20px;
  bottom:0;
  height:40px;
  border-radius:3px;
  cursor:pointer;
}
.rail .actualRail{
    position:absolute;
    top:50%;
    left:0;
    right:0;
    height:2px;
  }
.handle{
  position:absolute;
  margin-left:-8px;
  margin-top:-6px;
  z-index:2;
  width:14px;
  height:14px;
  cursor:pointer;
  border:0;
  border-radius:50%;
}
.handle::after{
    position:absolute;
    display:block;
    content:'';
    left:-10px;
    top:-10px;
    bottom:-10px;
    right:-10px;
  }
.track{
  position:absolute;
  height:2px;
  z-index:1;
  border-radius:3px;
  cursor:pointer;
}
.tickLine{
  position:absolute;
  margin-top:12px;
  width:1px;
  height:6px;
  margin-left:-1px;
}
.tickText{
  position:absolute;
  margin-top:20px;
  font-size:0.7rem;
  text-align:center;
}
:global(.light) .tickLine,
  :global(.light) .actualRail{
    background:rgba(0, 0, 0, 0.2);
  }
:global(.light) .handle{
    background:#457abb;
  }
:global(.light) .track{
    background:#457abb;
  }
:global(.dark) .tickLine,
  :global(.dark) .actualRail{
    background:rgba(255, 255, 255, 0.2);
  }
:global(.dark) .handle{
    background:#2d5079;
  }
:global(.dark) .track{
    background:#2d5079;
  }
