.lengthCounter {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 3px;
  border-radius: 3px;
  letter-spacing: 1px;
  font-size: .6rem;
  pointer-events: none;

  &.singleLine {
    bottom: 50%;
    transform: translateY(50%);
  }
}

/*
 * Theme
 */

:global(.light) {
  .lengthCounter {
    background: $light_lengthCounter_background;
    color: $light_lengthCounter_color;
  }
}

:global(.dark) {
  .lengthCounter {
    background: $dark_lengthCounter_background;
    color: $dark_lengthCounter_color;
  }
}
