.regex {
  font-family: $codeFont;
  margin-top: 10px;
  border-radius: 3px;
  padding: 4px 8px;
}

/*
 * Theme
 */

:global(.light) {
  .regex {
    background: $light_markdownContainer_code_background;
  }

  .tooltip {
    background: $light_tooltipped_background;
    color: $light_tooltipped_color;
  }
}

:global(.dark) {
  .regex {
    background: $dark_markdownContainer_code_background;
  }

  .tooltip {
    background: $dark_tooltipped_background;
    color: $dark_tooltipped_color;
  }
}
