.menu {
  @mixin overflowAuto;

  box-sizing: border-box;
  padding: 5px 0;
  min-width: $dropdownWidth;
  max-height: 100vh;
  border: 1px solid;
  border-radius: 3px;
  box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.2);
  white-space: nowrap;
  font-size: 0.8rem;
}

/*
 * Theme
 */

:global(.light) {
  .menu {
    border-color: $light_menuContainer_border;
    background: $light_menuContainer_background;
    color: $light_menuContainer_color;
  }
}

:global(.dark) {
  .menu {
    border-color: $dark_menuContainer_border;
    background: $dark_menuContainer_background;
    color: $dark_menuContainer_color;
  }
}
