.root {
  flex: 1 0 auto;
  outline: 0;
  cursor: pointer;
  user-select: none;
  padding: 6px 8px 6px 0;

  .options {
    margin: 0 3px;
  }
  .flagBtn,
  .options {
    display: inline-block;
    outline: 0;
    resize: none;
  }
}

.centered {
  display: flex;
  align-items: baseline;
  padding: 4px;
}

.delimiter {
  font-weight: bold;
}

/*
 * Theme
 */

:global(.light) {
  .delimiter {
    color: $light_optionsContainer_delimiter_color;
  }
  .root {
    color: $light_optionsContainer_color;

    .flagBtn {
      color: $light_optionsContainer_flagButton_color;
    }
    &:hover .flagBtn {
      color: $light_optionsContainer_flagButton_hoverColor;
    }
    &:active .flagBtn {
      color: $light_optionsContainer_flagButton_activeColor;
    }
  }
}

:global(.dark) {
  .delimiter {
    color: $dark_optionsContainer_delimiter_color;
  }
  .root {
    color: $dark_optionsContainer_color;

    .flagBtn {
      color: $dark_optionsContainer_flagButton_color;
    }
    &:hover .flagBtn {
      color: $dark_optionsContainer_flagButton_hoverColor;
    }
    &:active .flagBtn {
      color: $dark_optionsContainer_flagButton_activeColor;
    }
  }
}
