.root{
  position:absolute;
  bottom:0;
  left:0;
  right:0;
  height:55px;
  padding:0 10px;
  border:1px solid;
  border-bottom-right-radius:3px;
  border-bottom-left-radius:3px;
  text-align:right;
  -webkit-box-shadow:0 -3px 10px -5px rgba(0, 0, 0, 0.15);
          box-shadow:0 -3px 10px -5px rgba(0, 0, 0, 0.15);
  z-index:10;
}

.progress{
  position:absolute;
  top:-1px;
  left:0;
  height:3px;
}

.buttons{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-align:center;
      align-items:center;
  height:100%;
}

.buttons .left{
    -ms-flex:0 0 auto;
        flex:0 0 auto;
  }

.buttons .right{
    -ms-flex:1 1 auto;
        flex:1 1 auto;
    text-align:right;
  }

.addTest{
  margin-left:10px;
}

:global(.light) .root{
    border-color:#bdbdbd #d2d2d2 #d2d2d2;
    background:#f7f7f7;
  }

:global(.light) .progress{
    background:#457abb;
  }

:global(.dark) .root{
    border-color:#292828 #232323 #232323;
    background:#1a1a1a;
  }

:global(.dark) .progress{
    background:#2d5079;
  }
